<template>
    <el-row style="overflow:visible;padding:40px 0">
        <el-col :span="18" :offset="3">
            <el-form ref="form" label-width="120px">
                <el-form-item label="中文名">
                    <el-input v-model="Quota.quota_text" name="exp_name" placeholder="如：评论率"></el-input>
                </el-form-item>
                <el-form-item label="英文名">
                    <el-input name="exp_name" placeholder="如：PV" v-model="Quota.quota_en_text"></el-input>
                </el-form-item>
                <el-form-item label="key:">
                    <el-input
                        name="exp_name"
                        placeholder="如：comment_rate"
                        v-model="Quota.quota_name"
                    ></el-input>
                </el-form-item>
                <el-form-item label="描述">
                    <el-input
                        type="textarea"
                        name="exp_desc"
                        rows="5"
                        v-model="Quota.quota_comment"
                    ></el-input>
                </el-form-item>
                <el-form-item label="适用实验范围">
                    <el-radio-group v-model="Quota.quota_range">
                        <el-radio value="0" label="0">全局</el-radio>
                        <el-radio
                            value="1"
                            label="1"
                            v-show="defaultQuotaRange == 'none' || defaultQuotaRange == 'traffic'"
                        >流量测</el-radio>
                        <el-radio
                            value="2"
                            label="2"
                            v-show="defaultQuotaRange == 'none' || defaultQuotaRange == 'customer'"
                        >广告主测</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="产品线">
                    <el-select v-model="Quota.product" multiple placeholder="请选择" style="width:400px">
                        <el-option label="超级粉丝通" value="783" name="type"></el-option>
                        <el-option label="全局" value="1000" name="type"></el-option>
                        <el-option label="粉丝头条投非粉" value="11" name="type"></el-option>
                        <el-option label="粉丝头条" value="102" name="type"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="格式化类型">
                    <el-checkbox-group v-model="Quota.formate['percent']">
                        <el-checkbox label="百分比" value="percent"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="精确到小数点">
                    <el-select name="fmt_precision" v-model="Quota.fmt_precision">
                        <el-option :label="'保留整数'" :value="0"></el-option>
                        <el-option :label="'保留'+i+'位'" :value="i" :key="i" v-for="i in 6"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="计算公式">
                    <el-row :gutter="10">
                        <el-col :span="20">
                            <el-input disabled :value="Quota.expression.join(' ')"></el-input>
                        </el-col>
                        <el-col :span="3">
                            <el-button @click="delLast">回删</el-button>
                        </el-col>
                    </el-row>
                </el-form-item>
                <el-card class="box-card exp-effect" style="width:100%;">
                    <div slot="header" class="clearfix">
                        <span>原始指标</span>
                        <el-button
                            style="float: right; padding: 3px 0"
                            type="text"
                            @click="showIf= !showIf"
                            v-if="showIf"
                        >收起</el-button>
                        <el-button
                            style="float: right; padding: 3px 0"
                            type="text"
                            @click="showIf= !showIf"
                            v-else
                        >展开</el-button>
                    </div>
                    <el-collapse-transition>
                        <div v-show="showIf">
                            <template v-for="(item, index) in originQuotaList">
                                <el-button
                                    @click="inputExpression(item.quota.quota_name)"
                                    style="margin:5px"
                                    :key="index"
                                >{{item.quota.quota_name}}({{item.quota.quota_text}})</el-button>
                            </template>
                        </div>
                    </el-collapse-transition>
                </el-card>

                <el-card class="box-card">
                    <template>
                        <el-form-item label="运算符" label-width="60px">
                            <el-button @click="inputExpression('(')">(</el-button>
                            <el-button @click="inputExpression(')')">)</el-button>
                            <el-button @click="inputExpression('+')">+</el-button>
                            <el-button @click="inputExpression('-')">-</el-button>
                            <el-button @click="inputExpression('*')">*</el-button>
                            <el-button @click="inputExpression('/')">/</el-button>
                        </el-form-item>
                        <el-form-item label="常量" label-width="60px">
                            <el-row>
                                <el-col :span="18">
                                    <el-input v-model="constVal"></el-input>
                                </el-col>
                                <el-col :span="4" style="margin-left:10px">
                                    <el-button @click="addConstVal">输入</el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </template>
                </el-card>
                <el-form-item label="指标类型" v-show="userType == 10">
                    <el-radio-group v-model="Quota.quota_type">
                        <el-radio label="0">一级指标</el-radio>
                        <el-radio label="1">二级指标</el-radio>
                    </el-radio-group>
                </el-form-item>
                <div style="display:flex;justify-content:center">
                    <el-button @click="toCreateQuota" type="primary" style="width:200px">添加</el-button>
                </div>
            </el-form>
        </el-col>
    </el-row>
</template>
<style scoped>
.btn-group > .btn {
    margin-bottom: 8px;
    margin-right: 8px;
}
</style>

<script>
import lodash from "lodash";
export default {
    props: {
        quotaRange: {
            type: String,
            default: "none"
        }
    },
    watch: {
        quotaRange: {
            handler: function(newVal, oldVal) {
                this.defaultQuotaRange = newVal;
            }
        }
    },
    data() {
        return {
            constVal: 0,
            Quota: {
                quota_text: "",
                quota_en_text: "",
                quota_name: "",
                quota_comment: "",
                quota_range: "0",
                expression: [],
                formate: {
                    percent: false
                },
                fmt_precision: 4,
                creator: "nobody",
                quota_type: "1",
                product: [],
            },
            originQuotaList: [],
            defaultQuotaRange: "none",
            userType: 0,
            showIf: true
        };
    },
    mounted() {
        this.getOriginQuotas();
        this.userType = localStorage.getItem("userType");
    },
    methods: {
        inputExpression: function(val) {
            this.Quota.expression.push(val);
        },
        addConstVal: function() {
            this.Quota.expression.push(this.constVal);
        },
        delLast: function() {
            this.Quota.expression.pop();
        },
        async getOriginQuotas() {
            let res = await this.$api.quota.getListByGo("origin");
            this.originQuotaList = res.data;
        },
        checkExpression: function() {
            let list = [];
            this.Quota.expression.forEach(function(element, index) {
                if (
                    element == "+" ||
                    element == "-" ||
                    element == "*" ||
                    element == "/" ||
                    element == "(" ||
                    element == ")"
                ) {
                    list.push(element);
                } else {
                    list.push(5);
                }
            });
            try {
                eval(list.join(" "));
                return true;
            } catch (exception) {
                this.$message.error("计算公式有误，" + exception);
                return false;
            }
        },
        async toCreateQuota() {
            var requestData = this.Quota;
            if (this.Quota.quota_type == 1 && !this.checkExpression()) {
                return false;
            }
            let postData = this.$lodash.pick(requestData, [
                "quota_text",
                "quota_en_text",
                "quota_name",
                "quota_range",
                "fmt_precision",
                "quota_type",
                "quota_comment"
            ]);
            postData.expression = requestData.expression.join(",");
            postData.quota_type = parseInt(postData.quota_type);
            if (requestData.formate.percent) {
                postData.formate = "percent";
            }
            postData.fmt_precision = parseInt(postData.fmt_precision);
            postData.product = requestData.product.join(",");
            postData.quota_range = parseInt(postData.quota_range);

            let res = await this.$api.quota.createByGo(postData);
            if (res.errcode == 0) {
                this.$message({
                    message: "创建成功",
                    type: "success"
                });
                this.$tool.reload();
            } else {
                this.$message(res.errmsg);
            }
        }
    }
};
</script>
