    </div>
    <div v-else>暂无</div>
</template>
<script>
export default {
    props: ["strategySetId", "expLayerId"],
    data() {
        return {
            applyList: []
        };
    },
    mounted() {
        if (this.strategySetId) {
            this.getApplyList();
            return;
        }

        if (this.expLayerId) {
            this.getApplyListByLayer();
            return;
        }
    },
    methods: {
        async getApplyList() {
            let res = await this.$api.exp.getVolumeFullApplyList(
                this.strategySetId
            );
            this.applyList = res.data;
        },
        async getApplyListByLayer() {
            let res = await this.$api.exp.getVolume20ApplyListByLayer(
                this.expLayerId
            );
            this.applyList = res.data;
        }
    }
};
</script>
