<template>
    <div>
        <el-row>
            <el-col v-bind:span="colWidth" style="margin-bottom:10px">
                <el-input
                    placeholder="输入策略、描述或创建人，自动检索"
                    v-model="keyword"
                    @input="filterBykey"
                    @focus="filterGetFocus"
                >
                    <el-button
                        slot="append"
                        @click="filterLostFocus"
                        icon="el-icon-close"
                        v-if="!searchShow"
                    ></el-button>
                </el-input>
            </el-col>
            <el-col :span="17" v-if="searchShow" :offset="1" size="small">
                <el-form :inline="true">
                    <el-form-item label="产品线" style="margin-bottom:10px">
                        <el-select v-model="filter.product_code" @change="changeList">
                            <el-option
                                v-for="(item, index) in productList"
                                :label="item.product_chinese_name"
                                :value="item.product_code"
                                :key="index"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="状态" style="margin-bottom:10px">
                        <el-select v-model="filter.strategy_set_status" @change="changeList">
                            <el-option
                                v-for="(item, index) in strategySetStatusList"
                                :label="item.name"
                                :value="item.val"
                                :key="index"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="类型" style="margin-bottom:10px">
                        <el-select v-model="filter.strategy_set_type" @change="changeList">
                            <el-option
                                v-for="(item, index) in strategySetTypeList"
                                :label="item.name"
                                :value="item.code"
                                :key="index"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :span="2" style="float:right">
                <el-button id="export" type="warning" icon="el-icon-download" @click="doit">导出</el-button>
            </el-col>
        </el-row>
        <StrategyListTable
            :strategySetList="datas"
            strategySetTabId="allStrategy"
            from="allList"
        ></StrategyListTable>
        <div v-if="pageShow" style="text-align: center;margin-top:20px">
            <el-pagination
                @current-change="swicthPage"
                :current-page.sync="pageIndex"
                layout="total, prev, pager, next"
                :total="total"
                :page-size="20"
                align="center"
                :hide-on-single-page="true"
            ></el-pagination>
        </div>
    </div>
</template>

<script>
import StrategyListTable from "./StrategyListTable.vue";
import debounce from "@/components/Widget/debounce";

export default {
    name: "StrategyAllList",
    data() {
        return {
            strategySetTabId: "allList",
            keyword: "",
            datas: [],
            productList: [],
            filter: {
                product_code: 783,
                strategy_set_status: "all",
                strategy_set_type: -1
            },
            pageIndex: 1,
            pageShow: true,
            searchShow: true,
            colWidth: 4,
            total: 10,
            strategySetStatusList: [
                {
                    val: "all",
                    name: "全部"
                },
                {
                    val: "online",
                    name: "生效中"
                },
                {
                    val: "audit",
                    name: "待审核"
                },
                {
                    val: "offline",
                    name: "未生效"
                }
            ],
            strategySetTypeList: [
                {
                    code: -1,
                    name: "全部"
                },
                {
                    code: 0,
                    name: "流量测"
                },
                {
                    code: 1,
                    name: "广告主测"
                },
                {
                    code: 2,
                    name: "流量测+广告主测"
                }
            ]
        };
    },
    components: {
        StrategyListTable,
        debounce
    },

    mounted() {
        this.getList();
        this.getProductList();
    },
    methods: {
        changeList() {
            this.pageIndex = 1;
            this.getList();
        },
        swicthPage() {
            this.getList();
        },
        async getList() {
            this.pageShow = true;
            let param = {
                page: this.pageIndex,
                strategy_set_type: this.filter.strategy_set_type,
                // strategy_set_type:0,
                product_code: this.filter.product_code,
                // product_code:783,
                strategy_set_status: this.filter.strategy_set_status
                // strategy_set_status:'all'
            };
            // console.log(param);
            let res = await this.$api.strategy.getAllListByGo(param);

            if (res.errcode != 0) {
                this.$set(this, "datas", []);
                return;
            }
            this.$set(this, "datas", res.data.list);
            this.total = res.data.total;
        },
        doit() {
            this.Export2Excel.export_table_to_excel(
               'allStrategy',
                "所有实验策略"
            );
        },
        async getProductList() {
            let res = await this.$api.strategy.getProductList();
            this.productList = this.$lodash.uniqBy(res.data, "product_code");
        },
        async filterBykey() {
            this.pageShow = false;
            let res = await this.$api.strategy.dataFilter(this.keyword);
            this.$set(this, "datas", res.data);                        
        },
        filterGetFocus() {
            this.colWidth = 12;
            this.searchShow = false;
        },
        filterLostFocus() {
            this.keyword = "";
            this.getList();
            this.colWidth = 4;
            this.searchShow = true;
        }
    }
};
</script>