<template>
    <div>
        <el-table :data="newListData" height="500" style="width: 100%">
            <el-table-column label="ID(法拉第)" prop="quota.quota_id" width="140">
                <template slot-scope="scope">
                    <a
                        href="javascript:;"
                        @click="showQuotaDetail(scope.row)"
                    >{{scope.row.quota.quota_id}}</a>
                </template>
            </el-table-column>
            <el-table-column label="中文名" prop="quota.quota_text"></el-table-column>
            <el-table-column label="英文名" prop="quota.quota_en_text"></el-table-column>
            <el-table-column label="产品线" width="300">
                <template slot-scope="scope">
                    <span>{{scope.row.support_product_list.join('、')}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="quota.third_party_id" label="HubbleID"></el-table-column>
            <el-table-column prop="quota.quota_weight" label="权重"></el-table-column>
            <el-table-column prop="address" label="操作">
                <template slot-scope="scope">
                    <a href="javascript:;" @click="editQuota(scope.row)">修改</a>
                    <a href="javascript:;" @click="delQuota(scope.row)">删除</a>
                    <a href="javascript:;" @click="linkHubbleQuota(scope.row)">关联</a>
                </template>
            </el-table-column>
        </el-table>

        <el-drawer title="指标详情" :visible.sync="quotaDetail" direction="rtl">
            <span>
                <ul v-if="Object.keys(clickQuota).length>0">
                    <li class="drawer-item">
                        <b>指标ID：</b>
                        {{clickQuota.quota.quota_id}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>中文名:</b>
                        {{clickQuota.quota.quota_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>英文名：</b>
                        {{clickQuota.quota.quota_en_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>key：</b>
                        {{clickQuota.quota.quota_name}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>类型：</b>
                        {{clickQuota.quota_type_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>描述：</b>
                        {{clickQuota.quota.quota_comment}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>适用实验范围：</b>
                        {{clickQuota.quota_range_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>计算公式：</b>
                        {{clickQuota.expression_text}}
                    </li>
                    <el-divider></el-divider>
                    <li class="drawer-item">
                        <b>创建时间：</b>
                        {{clickQuota.quota.create_time}}
                    </li>
                    <el-divider></el-divider>
                </ul>
            </span>
        </el-drawer>

        <el-drawer title="修改指标" :visible.sync="editDrawer" direction="rtl" size="40%">
            <div style="padding:20px">
                <template v-if="Object.keys(clickQuota).length>0">
                    <el-form ref="form">
                        <el-form-item label="中文名:">
                            <el-input v-model="clickQuota.quota.quota_text"></el-input>
                        </el-form-item>
                        <el-form-item label="英文名:">
                            <el-input v-model="clickQuota.quota.quota_en_text"></el-input>
                        </el-form-item>
                        <el-form-item label="描述:">
                            <el-input v-model="clickQuota.quota.quota_comment" type="textarea"></el-input>
                        </el-form-item>
                        <el-form-item label="指标排序权重(0-100):">
                            <el-input v-model="clickQuota.quota.quota_weight"></el-input>
                        </el-form-item>
                        <el-form-item label="所属产品线">
                            <el-select
                                v-model="product"
                                multiple
                                placeholder="请选择"
                                style="width:400px"
                            >
                                <el-option label="超级粉丝通" value="783" name="type"></el-option>
                                <el-option label="全局" value="1000" name="type"></el-option>
                                <el-option label="粉丝头条投非粉" value="11" name="type"></el-option>
                                <el-option label="粉丝头条" value="102" name="type"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-form>
                    <el-button type="danger" style="width:140px;" @click="updateQuota">更新</el-button>
                </template>
            </div>
        </el-drawer>

        <el-drawer title="关联hubble指标" :visible.sync="linkDrawer" direction="rtl" size="50%">
            <el-form style="padding:0 20px">
                <el-form-item label="请输入关键字搜索Hubble指标:">
                    <el-input v-model="keyword" placeholder="可搜索id、中文名、theme、描述"></el-input>
                </el-form-item>
            </el-form>
            <el-row style="height:400px;padding:0 20px;overflow:scroll;">
                <template v-if="Object.keys(clickQuota).length>0">
                    <el-form>
                        <el-form-item
                            style="height: 500px;overflow: scroll;display:flex;flex-direction:column;"
                        >
                            <el-radio-group v-model="hubbleId">
                                <el-radio label="0">无</el-radio>
                                <template v-for="item in showHubbleQuotas">
                                    <el-radio :label="item.id">
                                        {{item.indicator_name}}
                                        <span
                                            v-if="hubbleId==item.id"
                                        >【{{item.indicator_meaning}}】</span>
                                    </el-radio>
                                </template>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                </template>
            </el-row>
            <el-row style="padding:0 20px;">
                <el-button type="danger" @click="confirmLinkHubbleQuota">确定关联</el-button>
            </el-row>
        </el-drawer>
    </div>
</template>

<script>
export default {
    props: [
        "listData",
        "hubbleQuotas",
        "theModalId",
        "EditId",
        "thehubbleId",
        "linkId"
    ],
    data() {
        return {
            clickQuota: {},
            clickThirdQuota: {},
            keyword: "",
            hubbleId: 0,
            product: [],
            newListData: [],
            quotaDetail: false,
            editDrawer: false,
            hubbleDrawer: false,
            linkDrawer: false
        };
    },
    watch: {
        listData: {
            handler(newval, old) {
                this.newListData = newval;
            },
            immediate: true,
            deep: true
        }
    },
    computed: {
        showHubbleQuotas: function() {
            var result = this.hubbleQuotas.filter(function(data) {
                var reg = eval("/(?=" + this.keyword + ")/");

                let retData =
                    reg.test(data.id) ||
                    reg.test(data.indicator_name) ||
                    reg.test(data.indicator_meaning) ||
                    reg.test(data.theme);

                return retData;
            }, this);

            return result;
        }
    },
    methods: {
        showQuotaDetail: function(quota) {
            this.clickQuota = quota;
            this.quotaDetail = true;
        },
        showThirdPartyDetail(thirdData) {
            this.clickThirdQuota = thirdData;
            this.hubbleDrawer = true;
        },
        editQuota: function(quota) {
            this.product = quota.quota.product.split(",");
            this.clickQuota = quota;
            this.editDrawer = true;
        },
        linkHubbleQuota: function(quota) {
            this.clickQuota = quota;
            this.linkDrawer = true;
            this.hubbleId = this.clickQuota.third_party_id;
        },
        confirmLinkHubbleQuota() {
            this.updateQuotaThirdId(
                this.clickQuota.quota.quota_id,
                this.hubbleId
            );
        },
        async updateQuotaThirdId(quotaId, thirdId) {
            let res = await this.$api.quota.updateByGo({
                quota_id: parseInt(quotaId),
                third_party_id: parseInt(thirdId)
            });

            if (res.errcode != 0) {
                this.$message.error(res.errmsg || "更新失败");
                return;
            }
            this.$message({
                message: "更新成功",
                type: "success"
            });

            this.$emit("updateData");
        },
        async delQuota(data) {
            this.$confirm("确认删除该指标？", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(async () => {
                    let res = await this.$api.quota.deleteByGo(
                        data.quota.quota_id
                    );
                    if (res.errcode != 0) {
                        this.$message.error(res.errmsg || "删除失败");
                        return;
                    }
                    this.$message({
                        type: "success",
                        message: "删除成功!"
                    });
                    this.$emit("updateData");
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "删除失败"
                    });
                });
        },
        async updateQuota() {
            let quotaParam = this.clickQuota.quota;
            quotaParam.support_product = this.clickQuota.support_product;
            let postData = this.$lodash.pick(quotaParam, [
                "quota_id",
                "quota_text",
                "quota_en_text",
                "quota_comment",
                "quota_weight"
            ]);
            postData.quota_id = parseInt(postData.quota_id);
            postData.quota_weight = parseInt(postData.quota_weight);
            postData.product = this.product.join(",");
            console.log(postData);
            //return
            let res = await this.$api.quota.updateByGo(postData);

            if (res.errcode != 0) {
                this.$message.error(res.errmsg || "更新失败");
                return;
            }
            this.$message({
                message: "更新成功",
                type: "success"
            });
            this.$emit("updateData");
        }
    }
};
</script>

<style lang="less" scoped>
.drawer-item {
    list-style-type: none;
}
.el-radio-group {
    display: flex;
    flex-direction: column;
}
</style>
