<template>
    <el-row>
        <el-col :span="20" :offset="2" v-show="layer.show_add_user_tip">
            <el-alert type="warning" :closable="false">
                <p style="font-size:14px">
                    想拥有该实验的放量权限，请联系实验创建者
                    <strong class="red-font">{{layer.creator}}</strong>为你添加协作者身份
                </p>
            </el-alert>
        </el-col>

        <el-col align="center">
            <OptBtn :layerId="$route.query.exp_layer_id" :layerInfo="layer" @toPushAll="toPushAll" />
        </el-col>

        <el-col
            :span="16"
            :offset="4"
            style="margin-top:20px;"
            v-if="Object.keys(grayIpInfo).length > 0"
        >
            <el-card class="box-card" style="background:rgb(250, 236, 216)">
                <p>
                    <strong>单台机器灰度中</strong>
                </p>
                <p>
                    <strong>灰度机器IP：</strong>
                    {{grayIpInfo.ip}}
                </p>
                <p :class="grayIpInfo.running_status_cls">
                    <strong>运行状态：</strong>
                    {{grayIpInfo.running_status_text}}
                </p>
                <template v-if="grayIpInfo.running_status == -1">
                    <p :class="grayIpInfo.running_status_cls">
                        <strong>该机器上fisher进程检查失败，检查脚本如下:</strong>
                    </p>
                    <code>#!/bin/bash\nsudo netstat -anpt | grep \":9999\" | wc -l</code>
                    <p>检查结果为：{{grayIpInfo.health_state.log || 'null'}}</p>
                </template>
            </el-card>
        </el-col>

        <el-col :span="20" :offset="2" style="margin-top:20px">
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <strong style="color:#d04925;font-size:23px">{{layer.exp_layer_name}}</strong>
                    (ID：{{layer.exp_layer_id}})
                    <span
                        style="display:inline-block;float:right"
                    >
                        <el-button type="text" @click="showUpdateRecord">变更记录</el-button>
                        <el-divider direction="vertical"></el-divider>
                        <el-button type="text" @click="toExpStat" style="font-size:18px">实验检验</el-button>
                        <el-divider direction="vertical"></el-divider>
                        <router-link
                            v-if="layer.is_same_type == 1"
                            :to="{path : '/lab/create_exp_step' , query : {
                          from_exp_layer_id : layer.exp_layer_id,
                      }}"
                        >克隆</router-link>
                    </span>
                </div>
                <el-form label-width="120px">
                    <el-form-item label="状态：" style="margin-bottom: 2px">
                        <span style="font-size:20px;color:red">{{formateInfo.status_text}}</span>
                    </el-form-item>
                    <el-form-item
                        label="产品线："
                        style="margin-bottom: 2px"
                    >{{formateInfo.product_chinese_name}} | {{formateInfo.exp_zone_chinese_name}}</el-form-item>
                    <el-form-item label="作用策略：" style="margin-bottom: 2px">
                        <router-link
                            :to="{ path: '/strategy/detail', query: { strategy_set_id: layer.strategy_set_id}}"
                            style="font-size:20px"
                        >
                            <strong>{{strategySet.strategy_set_name}}</strong>
                        </router-link>
                    </el-form-item>
                    <el-form-item label="创建人：" style="margin-bottom: 2px">
                        {{layer.creator}}
                        <span
                            style="color: grey"
                        >（创建于：{{layer.create_time}}， 更新于：{{layer.update_time}}）</span>
                    </el-form-item>
                    <el-form-item
                        label="审核人："
                        style="margin-bottom: 2px"
                    >{{layer.assign_auditor != '' ? layer.assign_auditor : '无'}}</el-form-item>
                    <el-form-item label="协作人：" style="margin-bottom: 2px">
                        {{layer.access_user != '' ? layer.access_user : '暂无'}}
                        <el-button type="warning" size="small" @click="toAddUser">
                            <i class="el-icon-plus"></i>添加
                        </el-button>
                    </el-form-item>

                    <el-form-item label="属性：" style="margin-bottom: 2px">
                        <el-tag type="primary">{{layer.exp_layer_type_name}}</el-tag>
                        <el-tag type="success">{{layer.is_same_type_name}}</el-tag>
                        <el-tag type="info" v-if="formateInfo.is_divide_budget">预算分配实验</el-tag>
                    </el-form-item>
                    <el-form-item
                        label="实验对象类型："
                        style="margin-bottom: 2px"
                        v-if="layer.is_same_type == 0"
                    >{{formateInfo.alloc_type_name}}</el-form-item>
                </el-form>

                <el-collapse>
                    <el-collapse-item name="1">
                        <template slot="title">
                            <strong style="font-size:16px;">实验描述</strong>
                            <i class="header-icon el-icon-info"></i>
                        </template>
                        <div v-html="layer.exp_layer_comment.replace(/\n/g,'<br>')"></div>
                    </el-collapse-item>
                </el-collapse>
            </el-card>

            <el-card class="box-card" style="margin-top:20px;" v-if="layer.is_same_type != 0">
                <div slot="header" class="clearfix">
                    <strong style="font-size:21px">流量团圈定</strong>
                </div>
                <p>
                    <strong>流量区域：</strong>
                    {{layer.flow_type== 1 ? '灰度流量' : '全部流量'}}
                </p>
                <p>
                    <strong>流量分配类型：</strong>
                    {{formateInfo.alloc_type_name}}
                </p>

                <p v-if="layer.exp_layer_type==0">
                    <strong>流量分配条件:</strong>
                    <template v-if="layer.alloc_condition_enable == 0">禁用</template>
                </p>
                <p v-if="layer.exp_layer_type==1">
                    <strong>实验对象:</strong>
                </p>
                <el-card class="box-card" v-if="layer.alloc_condition_enable == 1">
                    <template v-for="(val, index) in allocConfDisplay">
                        <p :key="index">
                            <strong>{{val.name}}：</strong>
                            {{val.data}}
                        </p>
                    </template>
                </el-card>
            </el-card>

            <el-card class="box-card" style="margin-top:10px">
                <div slot="header" class="clearfix">
                    <strong style="color:#d04925;font-size:23px">实验配置：</strong>
                </div>

                <el-table :data="expList" border style="width: 100%" height="600px" ref="tab">
                    <el-table-column label="实验id" width="180" fixed="left">
                        <template
                            slot-scope="scope"
                        >layer-{{layer.exp_layer_id}}#exp-{{scope.row.exp_id}}</template>
                    </el-table-column>
                    <el-table-column prop="exp_title" label="标题" width="220" fixed="left"></el-table-column>
                    <el-table-column prop="traffic_weight" label="流量权重" width="100"></el-table-column>
                    <el-table-column
                        prop="traffic_weight"
                        :label="layer.is_same_type == 1 ? '用户白名单' : '实验对象'"
                        width="150"
                    >
                        <template slot-scope="scope">
                            <p class="p-break">{{scope.row.white_list_uid}}</p>
                        </template>
                    </el-table-column>
                    <template v-for="key in layer.expListStrategyKey">
                        <el-table-column :label="'策略：' + key" width="400">
                            <template slot-scope="scope">
                                <p
                                    class="p-break"
                                    :class="scope.row.exp_strategy_content[key]==base[key]?'':diffFont"
                                >{{ scope.row.exp_strategy_content[key] || ''}}</p>
                            </template>
                        </el-table-column>
                    </template>
                </el-table>
            </el-card>
            <el-card class="box-card" style="margin-top:10px">
                <div slot="header" class="clearfix">
                    <span>放量邮件申请记录</span>
                </div>
                <VolumeApplyList
                    :expLayerId="$route.query.exp_layer_id"
                    :strategySetId="layer.strategy_set_id"
                    ref="VolumeApplyList"
                />
            </el-card>
        </el-col>

        <el-dialog title="填写申请全量信息" :visible.sync="showApplyFullForm">
            <p class="red-font">重要提醒：效果实验放量超20%需要leader邮件组一致审核通过！！！请确认你已经发送了邮件</p>
            <el-form label-position="top" label-width="150px">
                <el-form-item label="选择实验效果满意的子实验:">
                    <el-radio-group v-model="pushAllExpId">
                        <template v-for="exp in expList">
                            <el-radio
                                :label="exp.exp_id"
                                v-if="exp.exp_type_code == 1 && exp.exp_title != '基准nodiff实验'"
                            >{{exp.exp_title}}【layer-{{layer.exp_layer_id}}#exp-{{exp.exp_id}}</el-radio>
                        </template>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="邮件发送时间:">
                    <el-date-picker
                        v-model="mailInfo.time"
                        type="datetime"
                        placeholder="选择日期"
                        format="yyyy-MM-dd HH:mm"
                        value-format="yyyy-MM-dd HH:mm"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="邮件内容:">
                    <el-input type="textarea" v-model="mailInfo.content" :rows="6"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="showApplyFullForm = false">取 消</el-button>
                <el-button type="primary" @click="applyPushAll">确 定</el-button>
            </div>
        </el-dialog>

        <el-drawer
            title="实验变更记录"
            :visible.sync="showRecord"
            size="80%"
            :custom-class="'drawerscroll'"
        >
            <el-row style="overflow:scroll" :style="{'height' : drawerHeight}">
                <ExpUpdateRecord
                    :layerId="layer.exp_layer_id"
                    ref="record"
                    :strategySetName="layer.strategy_set_name"
                    :strategys="layer.expListStrategyKey"
                />
            </el-row>
        </el-drawer>

        <el-col align="center" style="margin-top:20px">
            <OptBtn :layerId="$route.query.exp_layer_id" :layerInfo="layer" @toPushAll="toPushAll" />
        </el-col>
    </el-row>
</template>

<script>
import AddConcernQuota from "./AddConcernQuota.vue";
import SingelDomainLayer from "@/Faraday/Domain/SingelDomainLayer.vue";
import FullVolumeConfirmList from "./FullVolumeConfirmList.vue";
import VolumeApplyList from "./VolumeApplyList.vue";
import VolumeFullApplyList from "../Strategy/VolumeFullApplyList.vue";
import OptBtn from "./OptBtn.vue";
import ExpUpdateRecord from "./ExpUpdateRecord.vue";

export default {
    data() {
        return {
            layer: {
                exp_layer_comment: ""
            },
            selectQuotaId: [],
            expLayerId: null,
            grayIpInfo: {},
            pushAllExpId: "",
            mailInfo: {
                time: "",
                content: ""
            },
            mailSubject: "",
            base: {},
            diffFont: "diff-font",
            showApplyFullForm: false,
            showRecord: false,
            strategySet: {},
            expList: [],
            formateInfo: {},
            allocConfDisplay: [],
            drawerHeight: "600px"
        };
    },
    components: {
        SingelDomainLayer,
        FullVolumeConfirmList,
        VolumeApplyList,
        VolumeFullApplyList,
        OptBtn,
        ExpUpdateRecord
    },
    mounted() {
        if (this.$route.query.exp_layer_id > 0) {
            this.getExpLayerInfo(this.$route.query.exp_layer_id);
            //this.getExpGrayIpInfo(this.$route.query.exp_layer_id);
        } else {
            this.$router.push({ path: "/lab/mine" });
        }
        if (this.$route.query.action == "open_record") {
            this.showUpdateRecord();
        }
        this.drawerHeight = document.documentElement.clientHeight - 150 + "px";
    },
    watch: {
        expList: function() {
            this.$nextTick(() => {
                this.$refs.tab.doLayout();
            });
        }
    },
    methods: {
        async getExpGrayIpInfo(expLayerId) {
            let res = await this.$api.exp.getGrayIp(expLayerId);
            this.grayIpInfo = res.data;
        },
        async getExpLayerInfo(expLayerId) {
            let res = await this.$api.exp.getExpDetailByGo(expLayerId);
            var data = res.data;

            this.layer = data.origin_exp_layer;
            this.strategySet = data.strategy_set;
            this.expList = data.exp_list;
            this.formateInfo = data.format;
            setTimeout(() => {
                this.$refs["VolumeApplyList"].getApplyList();
            }, 100);

            if (
                this.layer.alloc_condition_enable == 1 &&
                this.layer.exp_layer_type == 0
            ) {
                this.allocConfDisplay = this.$tool.getAllocConfDisplayData(
                    this.layer.alloc_condition_value
                );
            }
            if (
                this.layer.alloc_condition_enable == 1 &&
                this.layer.exp_layer_type == 1
            ) {
                this.allocConfDisplay = this.$tool.formateCustExpObject(
                    this.layer
                );
            }

            this.layer.expListStrategyKey = Object.keys(
                this.expList[0].exp_strategy_content
            );
            this.$store.commit("setHeadBreadCrumb", this.layer.exp_layer_name);

            this.base = this.expList[0].exp_strategy_content;

            this.layer.exp_layer_type_name = "流量侧实验";
            if (this.layer.access_user.indexOf(",") == 0) {
                this.layer.access_user = this.layer.access_user.substr(1);
            }
            if (this.layer.access_user != "") {
                this.layer.access_user = this.layer.access_user.replace(
                    /\,/g,
                    "、"
                );
            }
            if (this.layer.assign_auditor != "") {
                this.layer.assign_auditor = this.layer.assign_auditor.replace(
                    /\,/g,
                    "、"
                );
            }
            if (this.layer.exp_layer_type == 1) {
                this.layer.exp_layer_type_name = "广告主侧实验";
            }
            if (this.layer.is_same_type > 0) {
                this.layer.is_same_type_name = "同质性实验";
            } else {
                this.layer.is_same_type_name = "非同质性实验";
            }
        },
        domainClick: function(domain) {
            this.$tool.reload();
        },
        async doApplyPusllAll() {
            let res = await this.$api.exp.applyPushAllByGo(
                this.pushAllExpId,
                this.mailInfo
            );

            let showMsg = "";
            if (res.errcode == 0) {
                showMsg = "申请完成，等待审核";
                this.showApplyFullForm = false;
                this.$alert("申请成功", {
                    confirmButtonText: "确定",
                    callback: () => {
                        this.$tool.reload();
                    }
                });
            } else {
                showMsg = res.errmsg || "申请失败";
                this.showApplyFullForm = true;
                this.$alert(res.errmsg || "申请失败", {
                    confirmButtonText: "确定",
                    callback: () => {
                        this.$tool.reload();
                    }
                });
            }
        },
        toPushAll() {
            this.showApplyFullForm = true;
        },
        applyPushAll() {
            if (this.pushAllExpId == "") {
                this.$message("请选择子实验");
                return;
            }
            if (this.mailInfo.time == "") {
                this.$message("请选择全量申请邮件发送时间");
                return;
            }

            if (this.mailInfo.content == "") {
                this.$message("请选择全量申请邮件发送内容");
                return;
            }

            this.doApplyPusllAll();
        },
        showUpdateRecord() {
            this.showRecord = true;
            //this.$refs.record.getUpdateRecord();
        },
        toExpStat() {
            if (this.layer.is_same_type > 0) {
                this.$router.push({
                    path: "/monitor/exp_data_stat",
                    query: {
                        exp_layer_id: this.layer.exp_layer_id
                    }
                });
            } else {
                this.$router.push({
                    path: "/monitor/cust_exp_data_stat",
                    query: {
                        exp_layer_id: this.layer.exp_layer_id
                    }
                });
            }
        },
        toAddUser() {
            this.$prompt("请输入用户名", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                inputValidator: data => {
                    if (data == null || data == "") {
                        return "请输入用户名";
                    }
                }
            })
                .then(({ value }) => {
                    this.addUser(value);
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消输入"
                    });
                });
        },
        async addUser(user) {
            let res = await this.$api.exp.addUser(
                this.layer.exp_layer_id,
                user
            );
            if (res.errcode < 0) {
                this.$message(res.errmsg);
                return;
            }
            this.$message({
                message: "添加成功",
                duration: 500,
                onClose: () => {
                    this.$tool.reload();
                }
            });
        }
    }
};
</script>

<style scoped lang="less">
.diff-font {
    color: #ed1250;
}
</style>

