<template>
    <div>
        <el-table :data="dataList" height="1000" :id="strategySetTabId">
            <el-table-column
                width="60"
                prop="origin_strategy_set.strategy_set_id"
                label="编号"
            ></el-table-column>
            <el-table-column
                label="策略名"
                width="260"
            >
                <template slot-scope="scope">
                    <router-link
                        :to="{ path: '/strategy/audit_detail', query: { strategy_set_id: scope.row.origin_strategy_set.strategy_set_id}}"
                        v-if="from == 'audit'"
                    >{{scope.row.origin_strategy_set.strategy_set_name}}</router-link>
                    <router-link
                        :to="{ path: '/strategy/detail', query: { strategy_set_id: scope.row.origin_strategy_set.strategy_set_id}}"
                        v-else
                    >{{scope.row.origin_strategy_set.strategy_set_name}}</router-link>
                </template>
            </el-table-column>
            <el-table-column
                label="状态"
                width="100"
            >
                <template slot-scope="scope">
                    <el-tag
                        :type="scope.row.status_cls"
                    >{{scope.row.strategy_set_status_text}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column
                prop="strategy_set_type_name"
                label="作用域"
                width="140"
            ></el-table-column>
            <el-table-column
                label="描述"
                width="300"
            >
                <template slot-scope="scope">
                    <el-popover
                        placement="top-start"
                        width="400"
                        trigger="hover"
                        :content="scope.row.origin_strategy_set.strategy_set_comment"
                    >
                        <a href="javascript:;" slot="reference" v-if="scope.row.origin_strategy_set.strategy_set_comment.length<=20">
                            <span>{{scope.row.origin_strategy_set.strategy_set_comment}}</span>
                        </a>
                        <a href="javascript:;" slot="reference" v-else><span>{{scope.row.origin_strategy_set.strategy_set_comment.substr(0,20)+'...'}}</span></a>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="产品线" width="200">
                <template
                    slot-scope="scope"
                >{{ scope.row.product_chinese_name }} | {{ scope.row.exp_zone_chinese_name }}</template>
            </el-table-column>
            <el-table-column
                prop="origin_strategy_set.creator"
                label="创建者"
                width="120"
            ></el-table-column>
            <el-table-column
                label="实验状态"
            >
                <template slot-scope="scope">
                    <span v-if="scope.row.online_exp_layer_id >0 " class="text-center">
                        <span
                            @click="onlineExpButton(scope.row.online_exp_layer_id)"
                            style="color: green"
                        >实验中</span>
                    </span>
                    <span
                        v-else-if="scope.row.origin_strategy_set.strategy_set_status == 0 || scope.row.origin_strategy_set.to_audit_status != 0"
                        class="text-center"
                    >
                        <a href="javascript:;">
                            <label class="label-sm" style="padding: .2em .6em .3em;">--</label>
                        </a>
                    </span>
                    <span v-else class="text-center">
                        <a
                            href="javascript:;"
                            v-if="scope.row.origin_strategy_set.strategy_set_status == '0'"
                        >无</a>
                        <a
                            href="javascript:;"
                            v-else-if="scope.row.origin_strategy_set.strategy_set_type == 0"
                            @click="toCreateExp(scope.row.origin_strategy_set.strategy_set_id)"
                        >
                            <span>创建实验</span>
                        </a>
                        <a
                            href="javascript:;"
                            v-else-if="scope.row.origin_strategy_set.strategy_set_type == 1"
                            @click="toCreateCustomerExp(scope.row.origin_strategy_set.strategy_set_id)"
                        >
                            <span>创建实验</span>
                        </a>
                        <a
                            href="javascript:;"
                            v-else-if="scope.row.origin_strategy_set.strategy_set_type == 2"
                            class="dropdown"
                        >
                            <span
                                :id="'dropdownMenu' + scope.row.origin_strategy_set.strategy_set_id"
                                data-toggle="dropdown"
                                aria-haspopup="true"
                                aria-expanded="false"
                                class="dropdown-toggle"
                            >
                                <span>创建实验</span>
                            </span>
                            <ul
                                class="dropdown-menu"
                                :aria-labelledby="'dropdownMenu' + scope.row.origin_strategy_set.strategy_set_id"
                            >
                                <li>
                                    <a
                                        href="javascript:;"
                                        @click="toCreateExp(scope.row.origin_strategy_set.strategy_set_id)"
                                    >流量侧实验</a>
                                </li>
                                <li>
                                    <a
                                        href="javascript:;"
                                        @click="toCreateCustomerExp(scope.row.origin_strategy_set.strategy_set_id)"
                                    >商业侧实验</a>
                                </li>
                            </ul>
                        </a>
                    </span>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    props: ["strategySetList", "strategySetTabId", "from"],
    data() {
        return {
            dataList: []
        };
    },
    watch: {
        strategySetList: function(newval, oldval) {
            this.dataList = newval;
        }
    },
    methods: {
        toCreateCustomerExp: function(strategySetId) {
            this.$router.push({
                path: "/lab/create_exp_step",
                query: { strategy_set_id: strategySetId, exp_layer_type : 1}
            });
        },
        toCreateExp: function(strategySetId) {
            this.$router.push({
                path: "/lab/create_exp_step",
                query: { strategy_set_id: strategySetId, exp_layer_type : 0}
            });
        },
        onlineExpButton: function(layerId) {
            this.$router.push({
                path: "/lab/detail",
                query: { exp_layer_id: layerId }
            });
        }
    }
};
</script>
