<template>
    <div>
        <el-form ref="form" :label-position="'right'" label-width="150px">
            <el-form-item label="产品线">
                <el-radio-group v-model="expZoneCode">
                    <template v-for="(item, code) in supportExpZones">
                        <el-radio-button :label="code">{{item}}</el-radio-button>
                    </template>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="策略/参数">
                <el-col :span="20">
                    <el-select
                        placeholder="请选择策略"
                        style="display:block;"
                        v-model="strategySetId"
                        filterable
                    >
                        <template v-for="item in strategySetList">
                            <el-option
                                :label="item.strategy_set_name"
                                :value="item.strategy_set_id"
                                :key="item.strategy_set_id"
                            ></el-option>
                        </template>
                    </el-select>
                </el-col>
            </el-form-item>
        </el-form>

        <el-row v-if="showDomain">
            <el-form ref="form" :label-position="'right'" label-width="150px">
                <el-form-item>
                    <el-col :span="20">
                        <SingelDomainLayer :strategySetId="strategySetId" :expLayerId="tabLayerId" @domainClick="domainClick"></SingelDomainLayer>
                    </el-col>
                </el-form-item>
                <el-form-item>
                    <el-col :span="20">
                        <el-tabs  type="border-card" v-model="tabLayerId">
                            <template v-for="(item, idx) in domainLayer">
                                <el-tab-pane
                                    :label="'实验域' + (idx + 1) "
                                    :name="item.exp_layer.exp_layer_id + ''"
                                    v-if="item.exp_layer.creator != 'system'"
                                >
                                    <el-progress
                                        :stroke-width="15"
                                        :percentage="item.progress"
                                        style="margin-bottom:10px;"
                                        :text-inside="true"
                                    ></el-progress>
                                    <p>
                                        <strong>实验名：</strong>
                                        <router-link
                                            :to="{path :'/lab/detail', query : {
                                        exp_layer_id : item.exp_layer.exp_layer_id
                                    }}"
                                        >{{ item.exp_layer.exp_layer_name }}【{{ item.exp_layer.exp_layer_id }}】</router-link>
                                    </p>
                                    <p>
                                        <strong>流量分配条件：</strong>
                                        <span
                                            v-if="item.exp_layer.alloc_condition_enable == 1"
                                        >无</span>
                                    </p>
                                    <el-card
                                        class="box-card"
                                        v-if="item.exp_layer.alloc_condition_enable == 1"
                                    >
                                        <p
                                            v-for="(conf ,k) in item.exp_layer.alloc_condition"
                                            class="p-break"
                                        >{{conf.name}} : {{conf.data}}</p>
                                    </el-card>
                                    <p>
                                        <strong>创建人：</strong>
                                        {{ item.exp_layer.creator }}
                                    </p>
                                    <p>
                                        <strong>创建时间：</strong>
                                        {{ item.exp_layer.create_time }}
                                    </p>
                                    <p>
                                        <strong>更新时间：</strong>
                                        {{ item.exp_layer.update_time }}
                                    </p>
                                    <el-button
                                        size="medium"
                                        type="primary"
                                        @click="toExpDataStat(item.exp_layer)"
                                        style="margin-left:10px;margin-top:10px;"
                                    >实验检验</el-button>
                                </el-tab-pane>
                            </template>
                        </el-tabs>
                    </el-col>
                </el-form-item>
            </el-form>
        </el-row>

        <el-row v-if="!showDomain">
            <el-form ref="form" :label-position="'right'" label-width="150px">
                <el-form-item>
                    <el-col :span="20">
                        <template v-for="(item, idx) in expLayerList">
                            <el-card class="box-card">
                                <p>
                                    <strong>实验名：</strong>
                                    <router-link
                                        :to="{path :'/lab/detail', query : {
                                        exp_layer_id : item.exp_layer_id
                                    }}"
                                    >{{ item.exp_layer_name }}【{{ item.exp_layer_id }}】</router-link>
                                </p>
                                <p>
                                    <strong>流量分配条件：</strong>
                                    <span v-if="item.alloc_condition_enable == 0">无</span>
                                </p>
                                <el-card class="box-card" v-if="item.alloc_condition_enable == 1">
                                    <p
                                        v-for="(conf ,k) in item.alloc_condition"
                                        class="p-break"
                                    >{{conf.name}} : {{conf.data}}</p>
                                </el-card>
                                <p>
                                    <strong>创建人：</strong>
                                    {{ item.creator }}
                                </p>
                                <p>
                                    <strong>创建时间：</strong>
                                    {{ item.create_time }}
                                </p>
                                <p>
                                    <strong>更新时间：</strong>
                                    {{ item.update_time }}
                                </p>
                                <el-button
                                    size="medium"
                                    type="primary"
                                    @click="toExpDataStat(item)"
                                    style="margin-left:10px;margin-top:10px;"
                                >实验检验</el-button>
                            </el-card>
                        </template>
                    </el-col>
                </el-form-item>
            </el-form>
        </el-row>
    </div>
</template>
<script>
import SingelDomainLayer from "@/Faraday/Domain/SingelDomainLayer.vue";

export default {
    data() {
        return {
            expZoneCode: 0,
            strategySetId: 0,

            domainLayer: [],
            layerList: {},
            supportExpZones: {},
            allStratgyList: {},
            allLayerList: {},
            tabLayerId : "0",
        };
    },
    components: {
        SingelDomainLayer
    },
    mounted() {
        this.getLayerList();
    },
    watch: {
        expZoneCode() {
            if (this.strategySetList.length > 0) {
                this.strategySetId = this.strategySetList[0].strategy_set_id;
            }
        },
        strategySetId: function(val, oldval) {
            if (this.showDomain) {
                this.getDomainLayer();
            }
        }
    },
    computed: {
        showDomain: function() {
            return (
                this.expZoneCode == "78301" &&
                this.expLayerList[0].exp_layer_type == 0
            );
        },
        strategySetList: function() {
            if (this.allStratgyList[this.expZoneCode] != undefined) {
                return this.$lodash.uniqBy(
                    this.allStratgyList[this.expZoneCode],
                    "strategy_set_id"
                );
            } else {
                return [];
            }
        },
        expLayerList: function() {
            if (this.allLayerList[this.strategySetId] != undefined) {
                return this.allLayerList[this.strategySetId];
            } else {
                return [];
            }
        }
    },
    methods: {
        async getLayerList() {
            let res = await this.$api.exp.getLayerListByGo("online");
            let expZones = {};
            let strategySetList = {};
            let groupLayers = {};
            res.data.forEach(item => {
                if (item.origin_exp_layer.product_code == 1000) {
                    expZones[item.origin_exp_layer.exp_zone_code] =
                        item.product_chinese_name +
                        "-" +
                        item.exp_zone_chinese_name;
                } else {
                    expZones[item.origin_exp_layer.exp_zone_code] =
                        item.product_chinese_name;
                }

                if (
                    strategySetList[item.origin_exp_layer.exp_zone_code] ==
                    undefined
                ) {
                    strategySetList[item.origin_exp_layer.exp_zone_code] = [];
                }
                strategySetList[item.origin_exp_layer.exp_zone_code].push({
                    strategy_set_id: item.origin_exp_layer.strategy_set_id,
                    strategy_set_name: item.strategy_set_name
                });

                if (
                    groupLayers[item.origin_exp_layer.strategy_set_id] ==
                    undefined
                ) {
                    groupLayers[item.origin_exp_layer.strategy_set_id] = [];
                }

                if (
                    item.origin_exp_layer.alloc_condition_enable == 1 &&
                    item.origin_exp_layer.exp_layer_type == 0
                ) {
                    item.origin_exp_layer.alloc_condition = this.$tool.foramatAllocCondition(
                        JSON.parse(item.origin_exp_layer.alloc_condition_value)
                    );
                } else if (
                    item.origin_exp_layer.alloc_condition_enable == 1 &&
                    item.origin_exp_layer.exp_layer_type == 1
                ) {
                    item.origin_exp_layer.alloc_condition = this.$tool.formateCustExpObject(
                        item.origin_exp_layer
                    );
                } else {
                    item.origin_exp_layer.alloc_condition = [];
                }

                groupLayers[item.origin_exp_layer.strategy_set_id].push(
                    item.origin_exp_layer
                );
            });

            this.$set(this, "allStratgyList", strategySetList);
            this.$set(this, "supportExpZones", expZones);
            this.$set(this, "allLayerList", groupLayers);
            if (this.supportExpZones[78301] != undefined) {
                this.expZoneCode = 78301;
            } else {
                this.expZoneCode = Object.keys(this.supportExpZones)[0];
            }
            this.$set(this, "layerList", res.data);
        },
        async getDomainLayer() {
            if (parseInt(this.strategySetId) == 0) {
                return false;
            }

            this.domainLayer = [];
            let res = await this.$api.strategy.getStrategyDomainList(this.strategySetId);

            let domains = []
            res.data.forEach(item => {
                if (
                    item.exp_layer.alloc_condition_enable == 1 &&
                    item.exp_layer.exp_layer_type == 0
                ) {
                    item.exp_layer.alloc_condition = this.$tool.foramatAllocCondition(
                        JSON.parse(item.exp_layer.alloc_condition_value)
                    );
                } else if (
                    item.exp_layer.alloc_condition_enable == 1 &&
                    item.exp_layer.exp_layer_type == 1
                ) {
                    item.exp_layer.alloc_condition = this.$tool.formateCustExpObject(
                        item.origin_exp_layer
                    );
                } else {
                    item.exp_layer.alloc_condition = [];
                }
                domains.push(item)
            })
            this.domainLayer = domains;
            this.tabLayerId = this.domainLayer[0].exp_layer.exp_layer_id + ""
        },
        toExpDataStat(layerInfo) {
            if (layerInfo.is_same_type > 0) {
                this.$router.push({
                    path: "/monitor/exp_data_stat",
                    query: {
                        exp_layer_id: layerInfo.exp_layer_id
                    }
                });
            } else {
                this.$router.push({
                    path: "/monitor/cust_exp_data_stat",
                    query: {
                        exp_layer_id: layerInfo.exp_layer_id
                    }
                });
            }
        },
        domainClick(item) {
            this.tabLayerId = item.exp_layer_id + ""
        }
    }
};
</script>

