<template>
    <div>
        <template v-for="(item, index) in domainLayer">
            <div
                :style="{ 'width': item.progress_bar_wd,  'height' : (item.exp_layer.exp_layer_id == expLayerId) ? '60px' : '60px', 'opacity' : (item.exp_layer.exp_layer_id == expLayerId || item.domain.domain_type == 0 || expLayerId == undefined) ? '1' : '0.3'}"
                class="domain-block"
                :class="item.bg_class ? item.bg_class : 'bg-info'"
                v-show="item.domain.traffic_weight>0"
                :key="index"
            >
                <template v-if="item.domain.domain_type == 1">
                    <p
                        class="text-center h5"
                        @mouseover="showDomain(item, $event)"
                        @mouseleave="hideDomain(item, $event)"
                        @click="domainClick(item, $event)"
                        style="padding-top:15px;"
                    >{{item.domain.traffic_weight}}/1000</p>
                </template>
                <template v-if="item.domain.domain_type == 0">
                    <p
                        class="text-center h5"
                        style="padding-top:15px;"
                        @mouseover="showDomain(item, $event)"
                        @mouseleave="hideDomain(item, $event)"
                    >剩余({{item.domain.traffic_weight}}/1000)</p>
                </template>
            </div>
        </template>
    </div>
</template>

<script>
export default {
    props: ["domainLayer", "expLayerId"],
    methods: {
        domainClick: function(data, event) {
            $(event.currentTarget).tooltip("hide");
            if (
                this.$route.path == "/lab/create" ||
                data.exp_layer.exp_layer_id == this.expLayerId
            ) {
                return;
            }
            this.$emit("domainClick", data);
        },
        hideDomain: function(data, event) {
            $(event.currentTarget).tooltip("destroy");
        },
        showDomain: function(data, event) {
            if (data.domain_type == 0) {
                $(event.currentTarget)
                    .tooltip({
                        placement: "top",
                        html: true,
                        title: "剩余流量:" + data.domain.traffic_weight,
                        container: "body"
                    })
                    .tooltip("toggle");
                return;
            }

            let tipContent =
                "实验人:" +
                data.exp_layer.creator +
                "<br>点击进入查看实验详情";

            if (this.$route.path == "/monitor/exp_effect") {
                tipContent =
                    "实验人:" +
                    data.exp_layer.creator +
                    "<br>点击查看该实验检验";
            } else if (this.$route.path == "/lab/create") {
                tipContent = "实验人:" + data.exp_layer.creator;
            }

            $(event.currentTarget)
                .tooltip({
                    placement: "top",
                    html: true,
                    title: tipContent,
                    container: "body"
                })
                .tooltip("toggle");
        }
    }
};
</script>
