<template>
    <el-row :gutter="10">
        <el-col :span="12">
            <h4>修改前</h4>
            <template v-for="(item, idx) in oldStrategyList">
                <el-card class="box-card" style="margin-top:10px;height:300px;overflow:scroll">
                    <div>
                        <p>键值/key：{{ item.strategy_name }}</p>
                        <p>类型：{{ item.type }}</p>
                        <p v-if="item.type == 'string'">最大长度：{{item.max_length }}</p>
                        <div v-else-if="item.type == 'int'">
                            <p>最小值：{{item.min }}</p>
                            <p>最大值：{{ item.max }}</p>
                        </div>
                        <p class="break-words-defaultValue">默认值：{{ item.strategy_defult_value }}</p>
                        <p>描述：{{ item.strategy_value_comment }}</p>
                    </div>
                </el-card>
            </template>
        </el-col>
        <el-col :span="12">
            <h4>修改后</h4>
            <template v-for="(item, idx) in oldStrategyList">
                <el-card class="box-card new" style="margin-top:10px;height:300px;overflow:scroll">
                    <div v-if="updateData[idx].is_del == undefined">
                        <p>
                            键值/key：
                            <span
                                :class="{'update' : item.strategy_name != updateData[idx]['strategy_name']}"
                            >{{ updateData[idx]['strategy_name'] }}</span>
                        </p>
                        <p>类型：{{ updateData[idx]['type'] }}</p>
                        <p v-if="item.type === 'string'">
                            最大长度：
                            <span
                                :class="{'update' : item.max_length != updateData[idx].max_length}"
                            >{{ updateData[idx]['max_length'] }}</span>
                        </p>
                        <div v-else-if="item.type === 'int'">
                            <p>
                                最小值：
                                <span
                                    :class="{'update' : item.min != updateData[idx].min}"
                                >{{ updateData[idx]['min'] }}</span>
                            </p>
                            <p>
                                最大值：
                                <span
                                    :class="{'update' : item.max != updateData[idx].max}"
                                >{{ updateData[idx]['max'] }}</span>
                            </p>
                        </div>
                        <p>
                            默认值：
                            <span
                                :class="{'break-words-defaultValue' : true, 'update' : item.strategy_defult_value != updateData[idx]['strategy_defult_value']}"
                            >{{ updateData[idx]['strategy_defult_value'] }}</span>
                        </p>
                        <p>
                            描述：
                            <span
                                :class="{'update' : item.strategy_value_comment != updateData[idx].strategy_value_comment}"
                            >{{ updateData[idx]['strategy_value_comment'] }}</span>
                        </p>
                    </div>
                    <div v-if="updateData[idx].is_del">
                        <p>
                            <span class="update">此策略已删除</span>
                        </p>
                    </div>
                </el-card>
            </template>

            <template v-for="(item, idx) in addData">
                <el-card class="box-card new" style="margin-top:10px;height:300px;overflow:scroll">
                    <div class="update">
                        <p style="font-size:18px">新增策略</p>
                        <p>键值/key：{{ item.strategy_name }}</p>
                        <p>类型：{{ item.type }}</p>
                        <p v-if="item.type == 'string'">最大长度：{{item.max_length }}</p>
                        <div v-else-if="item.type == 'int'">
                            <p>最小值：{{item.min }}</p>
                            <p>最大值：{{ item.max }}</p>
                        </div>
                        <p class="break-words-defaultValue">默认值：{{ item.strategy_defult_value }}</p>
                        <p>描述：{{ item.strategy_value_comment }}</p>
                    </div>
                </el-card>
            </template>
        </el-col>
    </el-row>
</template>

<script>
export default {
    props: ["strategyList", "oldStrategyList"],
    computed: {
        updateData: function() {
            let updateStrategys = {};
            this.strategyList.forEach(item => {
                if (item.strategy_id != undefined && item.strategy_id > 0) {
                    updateStrategys[item.strategy_id] = item;
                }
            });
            return updateStrategys;
        },
        addData: function() {
            let addStrategys = {};
            this.strategyList.forEach(item => {
                if (item.strategy_id == undefined || item.strategy_id == 0) {
                    addStrategys[item.strategy_id] = item;
                }
            });
            return addStrategys;
        }
    }
};
</script>

<style scoped>
.break-words-defaultValue {
    word-break: break-all;
}

.new input,
.new p span,
.new textarea {
    color: green;
}

.update {
    color: red !important;
}
</style>

