<template>
    <div>
        <el-form :label-position="'right'" label-width="180px">
            <el-form-item label="选择审核人：">
                <el-select v-model="selectUsers" placeholder="请选择" multiple filterable collapse-tags @change="setSelectUser">
                    <el-option
                        v-for="user in userList"
                        :key="user.user_name"
                        :label="user.user_name"
                        :value="user.user_name"
                    ></el-option>
                </el-select>
                <span>{{selectUsers.join('、')}}</span>
            </el-form-item>
        </el-form>
       
    </div>
</template>
<script>
export default {
    props: ["expZoneCode", "value"],
    data() {
        return {
            userList: [],
            selectUsers: [],
        };
    },
    mounted() {
        this.getAuditorList();
        if(this.value != undefined) {
            this.selectUsers = this.value
        }
    },
    watch: {
        expZoneCode: function() {
            this.getAuditorList();
        },
        value : function(val) {
            this.selectUsers = val;
        }
    },
    methods: {
        async getAuditorList() {
            if (this.expZoneCode > 0) {
                let admin = await this.$api.user.queryUserByGo(
                    "",
                    4,
                    this.expZoneCode
                );
                this.userList = admin.data;
                let root = await this.$api.user.queryUserByGo(
                    "",
                    10,
                    this.expZoneCode
                );
                this.userList = this.userList.concat(root.data);
            }
        },
        setSelectUser() {
            this.$emit('input', this.selectUsers)
        }
    }
};
</script>
