<template>
    <div>
        <el-steps :active="stepIndex" simple  finish-status="success" process-status="process">
            <el-step title="选择实验类型" icon="el-icon-s-check"></el-step>
            <el-step title="选择策略" icon="el-icon-set-up"></el-step>
            <el-step title="实验基本信息" icon="el-icon-edit"></el-step>
            <el-step title="实验分桶信息" icon="el-icon-copy-document"></el-step>
        </el-steps>

        <el-col
            :offset="3"
            :span="18"
            style="padding:20px 0 30px 0;"
            v-if="checkMsgList.length > 0"
        >
            <p style="font-size:18px;">本页面错误信息如下</p>
            <template v-for="(item, index) in checkMsgList">
                <p v-html="(index + 1 ) +'、'+ item" class="red-font"></p>
            </template>
        </el-col>

        <el-row style="margin-top:20px" v-if="stepIndex == 0">
            <SelectExpType @nextStep="nextStep" />
        </el-row>

        <el-row style="margin-top:40px" v-if="stepIndex == 1">
            <el-col :span="18" :offset="3">
                <SelectStrategySet ref="strategyset" />
            </el-col>
        </el-row>

        <el-row style="margin-top:40px" v-if="stepIndex == 2">
            <el-col :span="18" :offset="3">
                <ExpBaseInfo v-if="layerType == 0" ref="expbaseinfo" />
                <CustExpBaseInfo v-if="layerType == 1 && sameType == 1" ref="expbaseinfo" />
                <CustExp2BaseInfo v-if="layerType == 1 && sameType == 0" ref="expbaseinfo" />
            </el-col>
        </el-row>

        <el-row style="margin-top:40px" v-if="stepIndex == 3">
            <el-col :span="18" :offset="3">
                <SetExp ref="setexp" v-if="sameType == 1" />
                <SetExpTwo ref="setexp" v-if="sameType == 0" />
            </el-col>
        </el-row>

        <el-row style="padding-top:100px">
            <el-col :span="1" :offset="3">
                <el-button type="primary" @click="prevStep" v-if="showPrevStep">
                    <i class="el-icon-back"></i>上一步
                </el-button>
            </el-col>

            <el-col :span="2" :offset="8">
                <el-button type="primary" v-if="showPreview" @click="showPreviewDrawer">预览</el-button>
            </el-col>
            <el-col :span="1" :offset="16">
                <el-button @click="nextStep" v-if="showNextStep" type="primary">
                    下一步
                    <i class="el-icon-right"></i>
                </el-button>
            </el-col>
        </el-row>
        <el-dialog
            title="页面预览"
            :visible.sync="previewDrawer"
            size="80%"
            :custom-class="'drawerscroll'"
        >
            <ExpPreview :previewData="postData" ref="preview" />
            <el-row style="margin:30px 20px">
                <AddAuditor :expZoneCode="expZoneCode" v-model="assignAuditor" />
            </el-row>
            <el-row style="margin:30px 0 50px">
                <el-col :span="2" :offset="11">
                    <el-button type="primary" @click="toSubmit" :disabled="disableSubmit">创建</el-button>
                </el-col>
            </el-row>
        </el-dialog>
    </div>
</template>
<style>
.drawerscroll {
    overflow: scroll;
}
</style>

<script>
import lodash from "lodash";
import { mapState } from "vuex";
import SelectExpType from "./SelectExpType";
import SelectStrategySet from "./SelectStrategySet";
import ExpBaseInfo from "./ExpBaseInfo";
import CustExpBaseInfo from "./CustExpBaseInfo";
import CustExp2BaseInfo from "./CustExp2BaseInfo";
import AddAuditor from "./AddAuditor";

import SetExp from "./SetExp";
import SetExpTwo from "./SetExp2";

import ExpPreview from "./ExpPreview";
export default {
    data() {
        return {
            stepIndex: 0,
            showNextStep: false,
            showPrevStep: false,
            showPreview: false,

            disableSubmit: false,
            previewDrawer: false,
            checkMsgList: [],
            postData: {},
            assignAuditor: []
        };
    },
    components: {
        SelectExpType,
        SelectStrategySet,
        ExpBaseInfo,
        CustExpBaseInfo,
        CustExp2BaseInfo,
        SetExp,
        SetExpTwo,
        ExpPreview,
        AddAuditor
    },
    beforeDestroy() {
        this.$store.commit("setStrategySetId", 0);
    },
    computed: {
        ...mapState({
            layerType: state => state.create_exp.exp_layer_type,
            sameType: state => state.create_exp.is_same_type,
            strategySetId: state => state.create_exp.strategy_set_id,
            expZoneCode: state => state.create_exp.exp_zone_code
        }),
        refName: function() {
            if (this.stepIndex == 1) {
                return "strategyset";
            }
            if (this.stepIndex == 2) {
                return "expbaseinfo";
            }
            if (this.stepIndex == 3) {
                return "setexp";
            }
        }
    },
    watch: {
        stepIndex: function() {
            if (this.stepIndex == 0) {
                this.showNextStep = this.showPrevStep = false;
                this.showPreview = false;
            }
            if (this.stepIndex == 1) {
                this.showPrevStep = true;
                this.showNextStep = true;
                this.showPreview = false;
            }

            if (this.stepIndex == 2) {
                this.showPrevStep = this.showNextStep = true;
                this.showPreview = false;
            }

            if (this.stepIndex == 3) {
                this.showPreview = true;
                this.showNextStep = false;
                this.showPrevStep = true;
            }
        },
        strategySetId: function() {
            this.checkStrategySet();
            this.$store.dispatch("clearExpInfo");
        }
    },
    mounted() {
        if (this.$route.query.from_exp_layer_id > 0) {
            this.cloneExpLayer(this.$route.query.from_exp_layer_id);
        }
    },
    methods: {
        async cloneExpLayer(layerId) {
            let res = await this.$api.exp.getExpDetailByGo(layerId);

            let list = res.data.exp_list;
            list.forEach((item, idx) => {
                list[idx].tab_name = idx + "";
                list[idx].exp_id =0;
            });

            let layerInfo = res.data.origin_exp_layer;
            this.$store.commit("clearData");
            this.$store.commit("setExpZoneCode", layerInfo.exp_zone_code);
            this.$store.commit("setExpLayerType", layerInfo.exp_layer_type);
            this.$store.commit("setSameType", layerInfo.is_same_type);
            this.$store.commit("setStrategySetId", layerInfo.strategy_set_id);
            this.$set(this, "stepIndex", 1);

            setTimeout(() => {
                this.checkStrategySet();
                setTimeout(() => {
                    if (!this.showNextStep) {
                        return;
                    }

                    if (layerInfo.alloc_condition_enable == 1) {
                        let conf = this.$tool.unZipAlloCondtion(
                            layerInfo.alloc_condition_value
                        );
                        if (Object.keys(conf).length > 0) {
                            this.$store.commit("setFlowAlloConf", conf);
                        }
                    }
                    this.$store.commit("setExpBaseInfo", {
                        alloc_type_code: layerInfo.alloc_type_code,
                        exp_layer_comment: layerInfo.exp_layer_comment,
                        exp_layer_name: "",
                        alloc_condition_enable:
                            layerInfo.alloc_condition_enable,
                        flow_type: 0,
                        traffic_weight: 0
                    });
                    this.$store.commit("setExpList", list);
                    this.$set(this, "stepIndex", 2);
                }, 1000);
            }, 1000);

            //this.$set(this, "stepIndex", 2);
        },
        nextStep() {
            if (this.stepIndex == 2) {
                let res = this.$refs[this.refName].checkInputData();
                this.checkMsgList = res;
                if (res.length > 0) {
                    this.$message(res.join("、"))
                    window.scrollTo(0, 0);
                    return;
                }
            }

            this.stepIndex = this.stepIndex + 1;
            window.scrollTo(0, 0);
        },
        async checkStrategySet() {
            this.showNextStep = false;
            if (this.$refs[this.refName] == undefined) {
                return;
            }
            let res = await this.$refs[this.refName].checkInputData();
            this.checkMsgList = res;
            if (res.length < 1) {
                this.showNextStep = true;
            }
        },
        prevStep() {
            this.stepIndex = this.stepIndex - 1 < 0 ? 0 : this.stepIndex - 1;
            this.checkMsgList = [];
            window.scrollTo(0, 0);
        },
        getLayerPostParam: function() {
            let postData = this.$store.state.create_exp.exp_base_info;
            postData.strategy_set_id = this.$store.state.create_exp.strategy_set_id;
            postData.is_same_type = this.$store.state.create_exp.is_same_type;
            postData.alloc_condition_value = {};
            postData.alloc_condition_code = [];

            if (postData.alloc_condition_enable == 1) {
                let conf = this.$tool.zipAllocCondition(
                    this.$store.state.create_exp.flow_alloc_conf
                );
                postData.alloc_condition_value = conf;
                postData.alloc_condition_code = Object.keys(conf);
            }

            postData.data = this.$store.state.create_exp.exp_list;
            postData.exp_layer_type = this.$store.state.create_exp.exp_layer_type;
            return postData;
        },
        showPreviewDrawer() {
            let res = this.$refs[this.refName].checkInputData();
            this.checkMsgList = res;

            if (res.length > 0) {
                this.$message(res.join("、"))
                window.scroll(0, 0);
                return;
            }
            this.previewDrawer = true;
            if (this.layerType == 0) {
                this.$set(this, "postData", this.getLayerPostParam());
            } else {
                this.$set(this, "postData", this.getCustLayerPostParam());
            }

            setTimeout(() => {
                this.$refs["preview"].reload();
            }, 100);

            return;
        },
        getCustLayerPostParam: function() {
            let postData = this.$store.state.create_exp.exp_base_info;
            postData.strategy_set_id = this.$store.state.create_exp.strategy_set_id;
            postData.is_same_type = this.$store.state.create_exp.is_same_type;
            postData.data = this.$store.state.create_exp.exp_list;
            postData.exp_layer_type = this.$store.state.create_exp.exp_layer_type;

            if (postData.is_same_type == 0) {
                postData.alloc_condition_enable = 0
                postData.alloc_condition_value = {}
            } else if (postData.is_same_type == 1) {
                postData.alloc_condition_enable = 1
                postData.alloc_condition_value = {}
                postData.alloc_condition_value[postData.exp_object_type] = postData.exp_object
            }
            return postData;
        },
        async toSubmit() {
            if (this.assignAuditor.length < 1) {
                this.$message("请指定审核人");
                return;
            }

            this.postData.assign_auditor = this.assignAuditor;
            this.postData.flow_type = 0;

            try {
                this.disableSubmit = true;

                let res = {};
                if (this.layerType == 0) {
                    res = await this.$api.exp.create(this.postData);
                } else {
                    res = await this.$api.exp.createCustomerExp(this.postData);
                }

                if (res.errcode == 0) {
                    this.$alert(res.errmsg || "创建成功", "提示", {
                        confirmButtonText: "确定",
                        callback: action => {
                            this.$router.push({ path: "/lab/list" });
                        }
                    });
                    this.$store.dispatch("clearExpInfo");
                } else {
                    this.disableSubmit = false;
                    this.$message({
                        type: "error",
                        message: res.errmsg
                    });
                }
            } catch (err) {
                this.$message({
                    type: "error",
                    message: err.message || "请刷新页面重试"
                });
                this.disableSubmit = false;
            }
        }
    }
};
</script>


