<template>
    <el-col>
        <el-form :label-position="'top'" label-width="150px" v-model="layerInfo">
            <el-form-item label="实验名称：">
                <el-input v-model="layerInfo.exp_layer_name" placeholder="请输入实验名称（不超过20个字）" @change="commitLayerInfo"></el-input>
            </el-form-item>

            <el-form-item label="实验描述：">
                <el-input
                    type="textarea"
                    v-model="layerInfo.exp_layer_comment"
                    rows="6"
                    placeholder="【请填写实验的预期目标，实验的设计方案，实验的放量计划，实验关心的核心指标等】"
                    @change="commitLayerInfo"
                ></el-input>
            </el-form-item>

            <el-form-item label="流量A/B分桶类型：">
                <el-radio v-model="layerInfo.alloc_type_code" :label="2" @change="commitLayerInfo">用户哈希分桶</el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="3" @change="commitLayerInfo">用户按天哈希分桶</el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="1" @change="commitLayerInfo">流量哈希分桶</el-radio>
            </el-form-item>

            <el-form-item label="特殊支持：">
                <el-switch v-model="layerInfo.is_divide_budget" active-text="预算分桶实验" @change="changeBudgetExp"></el-switch>
            </el-form-item>

            <el-form-item label="实验对象类型：">
                <el-radio v-model="layerInfo.exp_object_type" label="CUSTID" @change="commitLayerInfo" :disabled="layerInfo.is_divide_budget">广告主</el-radio>
                <el-radio v-model="layerInfo.exp_object_type" label="ADID" @change="commitLayerInfo" >广告计划</el-radio>
                <el-radio v-model="layerInfo.exp_object_type" label="MID" @change="commitLayerInfo" :disabled="layerInfo.is_divide_budget">博文</el-radio>
                <el-radio v-model="layerInfo.exp_object_type" label="TAG" @change="commitLayerInfo" :disabled="layerInfo.is_divide_budget">广告行业</el-radio>
            </el-form-item>

            <el-form-item label="实验对象：">
                <el-input
                    type="textarea"
                    v-model="layerInfo.exp_object"
                    rows="6"
                    placeholder="实验对象"
                    @change="commitLayerInfo"
                ></el-input>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>

export default {
    data() {
        return {
            layerInfo: {
                exp_layer_type : 1,
                alloc_type_code: 2,
                exp_layer_comment:
                    "【实验目的】\n\n【实验方案】\n\n【实验风险】",
                exp_layer_name: "",
                flow_type: 0,
                is_divide_budget : false,
                exp_object_type : "ADID",
                exp_object : "",
            },
            budgetExp: false
        };
    },
    mounted() {
        if (this.$store.state.create_exp.strategy_set_id < 1) {
            this.$emit("preStep");
            return;
        }
        if (
            Object.keys(this.$store.state.create_exp.exp_base_info).length > 0
        ) {
            console.log(this.$store.state.create_exp.exp_base_info);
            this.layerInfo = this.$store.state.create_exp.exp_base_info;
        }
    },
    methods: {
        changeBudgetExp() {
            if(this.layerInfo.is_divide_budget && this.layerInfo.exp_object_type != "ADID") {
                this.layerInfo.exp_object_type = "ADID"
                this.layerInfo.exp_object = "";
            }
            this.commitLayerInfo()
        },
        commitLayerInfo() {
            this.$store.commit("setExpBaseInfo", this.layerInfo);
            return;
        },
        checkInputData() {
            if (this.layerInfo.exp_layer_name == "") {
                return ["请输入实验名称"];
            }

            if (this.layerInfo.exp_layer_name.length > 20) {
                return ["实验名称长度在 1 到 20个字符"];
            }

            if (this.layerInfo.exp_layer_comment == "") {
                return ["请输入实验描述"];
            }

            if (this.layerInfo.exp_object == "") {
                return ["请输入实验对象"];
            }
            return [];
        }
    }
};
</script>
