<template>
    <el-row>
        <el-col :offset="2" :span="16">
            <el-form :label-position="'right'" label-width="180px" :model="strategySetInfo">
                <el-form-item label="所属产品线(实验区)：">
                    <p>{{strategySetInfo.product_chinese_name}} | {{strategySetInfo.exp_zone_chinese_name}}</p>
                </el-form-item>
                <el-form-item label="策略：">
                    <p>{{strategySetInfo.strategy_set_name}}</p>
                </el-form-item>
                <el-form-item label="策略类型：">
                    <p>{{strategySetInfo.strategy_set_type_name}}</p>
                </el-form-item>
                <el-form-item label="策略功能：">
                    <p>{{strategySetInfo.func =='exp' ? '效果实验' : '功能控制'}}</p>
                </el-form-item>
                <el-form-item label="策略描述：">
                    <el-input
                        type="textarea"
                        v-model="strategySetInfo.strategy_set_comment"
                        rows="6"
                        placeholder="请输入策略描述信息"
                    ></el-input>
                </el-form-item>
            </el-form>
            <AddAuditor
                :expZoneCode="strategySetInfo.exp_zone_code"
                v-model="strategySetInfo.assign_auditor"
            />
        </el-col>

        <el-col :offset="4" :span="16" style="margin-top:30px;">
            <SetStrategy :propStrategyList="strategyList" />
        </el-col>

        <el-col :offset="11" :span="2" style="margin-top:30px">
            <el-button type="primary" @click="showCompareDialog = true">对比策略修改</el-button>
        </el-col>

        <el-dialog title="对比" :visible.sync="showCompareDialog" width="70%" top="4vh">
            <StrategyCompare
                :oldStrategySetInfo="oldStrategySetInfo"
                :strategySetInfo="strategySetInfo"
                :oldStrategyList="oldStrategyList"
                :strategyList="strategyList"
            ></StrategyCompare>

            <div slot="footer">
                <el-button
                    type="primary"
                    @click="toEditStrategySet"
                    :disabled="isDisableSubmit"
                >确认修改</el-button>
            </div>
        </el-dialog>
    </el-row>
</template>

<script>
import StrategyCompare from "./StrategyCompare";
import SetStrategy from "./SetStrategy";
import lodash from "lodash";
import AddAuditor from "../Exp/AddAuditor";

export default {
    data() {
        return {
            strategyList: [],
            strategySetInfo: {
                strategy_set_type: 0,
                assign_auditor: [],
            },
            oldStrategySetInfo: {},
            oldStrategyList: [],
            isDisableSubmit: false,
            showCompareDialog: false
        };
    },
    mounted() {
        if (this.$route.query.strategy_set_id > 0) {
            this.getStrategyInfo(this.$route.query.strategy_set_id);
        } else {
            this.$router.push({ path: "/strategy/index" });
        }
    },
    components: {
        AddAuditor,
        StrategyCompare,
        SetStrategy
    },
    methods: {
        async getStrategyInfo(strategySetId) {
            let res = await this.$api.strategy.getStrategyByGo(strategySetId);

            if (res.errcode != 0) {
                this.$message(res.errmsg);
                this.$router.push({
                    path: "/strategy/detail",
                    query: {
                        strategy_set_id: strategySetId
                    }
                });
                return;
            }

            if (res.data.is_used_exp != false) {
                this.$message("该策略集上有运行的在线实验，暂时不能编辑");
                this.$router.push({
                    path: "/strategy/detail",
                    query: {
                        strategy_set_id: strategySetId
                    }
                });
                return;
            }

            res.data.strategy_set.product_chinese_name = res.data.product_chinese_name
            res.data.strategy_set.exp_zone_chinese_name = res.data.exp_zone_chinese_name
            res.data.strategy_set.strategy_set_type_name = res.data.strategy_set_type_name
            res.data.strategy_set.last_update_by = res.data.last_update_by
            res.data.strategy_set.is_used_exp = res.data.is_used_exp
            this.strategySetInfo = res.data.strategy_set;
            this.strategySetInfo.assign_auditor = [];

            let strategys = []
            res.data.strategy_list.forEach( item => {
                strategys.push(this.$lodash.merge(item, JSON.parse(item.verification)))
            })
            this.strategyList = strategys

            let tmpOldStrategys = lodash.cloneDeep(this.strategyList);
            this.oldStrategyList = lodash.keyBy(tmpOldStrategys, 'strategy_id');
            //

        },
        collectStrategyInfo: function() {
            var requestData = {
                strategy_set_comment: this.strategySetInfo.strategy_set_comment,
                assign_auditor: this.strategySetInfo.assign_auditor
            };
            this.strategyList = lodash.remove(this.strategyList, function(
                item
            ) {
                return item.is_del == undefined || item.strategy_id > 0;
            });
            requestData.strategy_list = this.strategyList.map( item => {
                if (item.strategy_id != undefined) {
                    item.strategy_id = item.strategy_id + ''
                }
                if (item.product_code != undefined) {
                    item.product_code = item.product_code + ''
                }
                if (item.strategy_set_id != undefined) {
                    item.strategy_set_id = item.strategy_set_id + ''
                }
                if (item.exp_zone_code != undefined) {
                    item.exp_zone_code = item.exp_zone_code + ''
                }
                if (item.max_length != undefined) {
                    item.max_length = item.max_length + ''
                }

                if (item.max != undefined) {
                    item.max = item.max + ''
                }
                
                if (item.min != undefined) {
                    item.min = item.min + ''
                }
                
                return item;
            });
            return requestData;
        },
        getStrategyCount: function() {
            let count = this.strategyList.length;
            this.strategyList.forEach(function(element, index) {
                if (element.is_del != undefined && element.is_del == true) {
                    count--;
                }
            }, count);
            return count;
        },
        async toEditStrategySet() {

            if (this.getStrategyCount() < 1) {
                this.$message("请至少填写一个策略");
                return;
            }
            var requestData = this.collectStrategyInfo();

            requestData.strategy_set_id = parseInt(this.$route.query.strategy_set_id);

            if (this.isDisableSubmit) {
                return false;
            }

            if (requestData.assign_auditor.length < 1) {
                this.$message("请指定审核人");
                return;
            }

            this.isDisableSubmit = true;

            let res = await this.$api.strategy.updateByGo(requestData);

            if (res.errcode == 0) {
                this.$message("修改成功");
                this.$router.push({
                    path: "/strategy/detail",
                    query: {
                        strategy_set_id: this.$route.query.strategy_set_id
                    }
                });
            } else {
                this.isDisableSubmit = false;
                this.$message(res.errmsg);
            }
        }
    }
};
</script>
