<template>
    <div>
        <el-form :label-position="'left'" label-width="80px" v-model="conf">
            <el-form-item label="年龄">
                <el-slider range show-stops :max="100" :min="1" v-model="conf.AGE"></el-slider>
            </el-form-item>

            <el-form-item label="性别">
                <el-radio v-model="conf.GENDER" :label="401">男</el-radio>
                <el-radio v-model="conf.GENDER" :label="402">女</el-radio>
                <el-radio v-model="conf.GENDER" :label="400">保密</el-radio>
                <el-radio v-model="conf.GENDER" :label="0">不限</el-radio>
            </el-form-item>

            <el-form-item label="设备">
                <el-checkbox-group v-model="conf.PLATFORM">
                    <el-checkbox :label="'90110201'" :key="90110201">IOS平台</el-checkbox>
                    <el-checkbox :label="'90110202'" :key="90110202">Android平台</el-checkbox>
                    <el-checkbox :label="'90110100'" :key="90110100">PC端</el-checkbox>
                    <el-checkbox :label="'90110200'" :key="90110200">移动端_其它</el-checkbox>
                    <el-checkbox :label="'90000000'" :key="90000000">未知</el-checkbox>
                </el-checkbox-group>
            </el-form-item>

            <el-form-item label="场景流">
                <el-select
                    v-model="conf.SERVICE"
                    multiple
                    collapse-tags
                    placeholder="请选择场景"
                    style="width:40%"
                >
                    <el-option
                        v-for="(item, index) in msgFlowConfig"
                        :key="index"
                        :label="item"
                        :value="index"
                    ></el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="地域">
                <el-select
                    v-model="conf.LOCATION"
                    multiple
                    collapse-tags
                    placeholder="请选择城市"
                    style="width:40%"
                >
                    <el-option-group key="hotCity" label="热门城市">
                        <el-option
                            v-for="(area, areaKey) in locationsConfig.hotCity"
                            :key="areaKey"
                            :label="area.location_name"
                            :value="areaKey"
                        ></el-option>
                    </el-option-group>
                    <el-option-group key="city" label="其他城市">
                        <el-option
                            v-for="(area, areaKey) in locationsConfig.locationObj"
                            :key="areaKey"
                            :label="area.location_name"
                            :value="areaKey"
                        ></el-option>
                    </el-option-group>
                </el-select>
            </el-form-item>

            <el-form-item label="App版本">
                <el-input v-model="conf.VERSION" placeholder="格式ABC，比如863" style="width:20%"></el-input>
                <span style="margin-left:10px">(及以上版本)</span>
            </el-form-item>

            <el-form-item label="服务器">
                <el-radio v-model="severName" :label="''">无</el-radio>
                <el-radio
                    v-model="severName"
                    v-for="(item, idx) in severNameList"
                    :label="item"
                    :key="idx"
                >{{item}}</el-radio>
            </el-form-item>
            <el-form-item v-if="severName != ''">
                <el-select
                    v-model="conf.IP"
                    multiple
                    collapse-tags
                    placeholder="请选择IP"
                    style="width:40%"
                >
                    <el-option-group
                        :key="index"
                        :label="index"
                        v-for="(itemList, index) in allIpList[severName]"
                    >
                        <el-option
                            v-for="(item, idx) in itemList"
                            :key="idx"
                            :label="item"
                            :value="item"
                        ></el-option>
                    </el-option-group>
                </el-select>
                <el-popover placement="top" width="300" v-model="showIpText">
                    <p>
                        <el-input
                            type="textarea"
                            v-model="inputIps"
                            rows="8"
                            placeholder="请输入IP，一行一个"
                        ></el-input>
                    </p>
                    <div style="text-align: right; margin: 0">
                        <el-button size="mini" type="text" @click="showIpText = false">取消</el-button>
                        <el-button type="primary" size="mini" @click="confirmInputIp">确定</el-button>
                    </div>
                    <el-button
                        type="primary"
                        @click="showIpText = !showIpText"
                        slot="reference"
                    >批量输入IP</el-button>
                </el-popover>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import sceneServiceConfig from "@/config/SceneService";
import lodash from "lodash";
import locationConfig from "@/config/locationv1";

export default {
    props: ["isSfst", "inputConf"],
    data() {

        let inputConf = this.$store.state.create_exp.flow_alloc_conf
        let inputSeverName = this.$store.state.create_exp.server_name
        return {
            msgFlowConfig: sceneServiceConfig,
            locationsConfig: locationConfig,

            conf: inputConf,
            severName: inputSeverName,

            severNameList: [],
            allIpList: {},
            showIpText: false,
            inputIps: "",
            inputIpMsg: ""
        };
    },
    mounted() {
        this.loadAllIp();
    },
    watch : {
        conf : {
            handler : function() {
                this.$store.commit("setFlowAlloConf", this.conf);
            },
            deep : true
        },
        severName : function() {
            if(this.severName == '') {
                this.conf.IP = []
            } else {
                this.$store.commit("setServerName", this.severName);
            }
        },
    },
    methods: {
        async loadAllIp() {
            let res = await this.$api.exp.getIpList();
            this.allIpList = res.data;

            this.severNameList = Object.keys(this.allIpList);
        },
        confirmInputIp() {
            let list = this.inputIps.trim().split("\n");
            let avalibleIps = this.$lodash.values(
                this.allIpList[this.severName]
            );

            let ipList = this.$lodash.union(...avalibleIps);
            let diffIps = this.$lodash.difference(list, ipList);

            let okIps = this.$lodash.pullAll(list, diffIps);

            if (okIps.length < 1) {
                this.$message({
                    message: "请输入ip，或者ip不在所选机器之内",
                    type: "warning"
                });
                return
            }
            this.conf.IP = okIps;

            if (diffIps.length > 0) {
                this.inputIpMsg = `已经选中${
                    okIps.length
                }个IP,有另外的${diffIps.join("、")}没有匹配上，请检查`;
                this.$message({
                    message: this.inputIpMsg,
                    type: "warning"
                });
                this.inputIps = this.$lodash.join(diffIps, "\n");
            } else {
                this.inputIps = "";
                this.showIpText = false;
            }
        }
    }
};
</script>



