<template>
    <el-row>
        <StrategyListTable
            :strategySetList="datas"
            :strategySetTabId="strategySetTabId"
            from="audit"
        />
    </el-row>
</template>

<script>
import StrategyListTable from "./StrategyListTable.vue";

export default {
    data() {
        return {
            datas: [],
            strategySetTabId: "auditList"
        };
    },
    mounted() {
        this.getAuditList();
    },
    components: {
        StrategyListTable
    },
    methods: {
        async getAuditList() {
            let res = await this.$api.strategy.getAuditListByGo();

            if (res.errcode != 0) {
                this.$set(this, "datas", []);
                return;
            }

            this.$set(this, "datas", res.data);
        }
    }
};
</script>
