<template>
    <div>
        <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="原始指标" name="origin">
                <div>
                    <QuotaListTable
                        theModalId="oo"
                        EditId="oo1"
                        thehubbleId="oo2"
                        linkId="oo3"
                        :listData="listData"
                        @updateData="init"
                        :hubbleQuotas="hubbleQuotas"
                    ></QuotaListTable>
                </div>
            </el-tab-pane>
            <el-tab-pane label="系统定义指标" name="system_define">
                <div>
                    <QuotaListTable
                        theModalId="yy"
                        EditId="yy1"
                        thehubbleId="yy2"
                        linkId="yy3"
                        :listData="listData"
                        @updateData="init"
                        :hubbleQuotas="hubbleQuotas"
                    ></QuotaListTable>
                </div>
            </el-tab-pane>
            <el-tab-pane label="Hubble指标" name="hubble">
                <div>
                    <HubbleQuotaTable :listData="hubbleQuotas"></HubbleQuotaTable>
                </div>
            </el-tab-pane>
            <el-tab-pane label="指标字典" name="quotaBook">
                <div>
                    <ExpEffectBook />
                </div>
            </el-tab-pane>
            <el-tab-pane label="*新增指标*" name="add_quota">
                <AddQuotaCtr  quotaRange="none"></AddQuotaCtr>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import QuotaListTable from "./QuotaListTable.vue";
import HubbleQuotaTable from "./HubbleQuotaTable.vue";
import AddQuotaCtr from "./AddQuotaCtr";
import ExpEffectBook from "../InspectGo/ExpEffectBook.vue";
export default {
    data() {
        return {
            listData: [],
            hubbleQuotas: [],
            tableType: "origin",
            activeName: "origin"
        };
    },
    components: {
        QuotaListTable,
        HubbleQuotaTable,
        AddQuotaCtr,
        ExpEffectBook
    },
    mounted() {
        this.tableType = this.$route.query.quota_type || "origin";
        this.init();
        this.getHubbleQuotas();
    },
    methods: {
        handleClick(tab, event) { 
            this.tableType = tab.name;
            if (this.tableType != "add_quota") {
                this.init();
            }
        },
        async init() {
            let res = [];

            if (this.tableType == "hubble") {
                return;
            }

            res = await this.$api.quota.getListByGo(this.tableType);

            this.listData = res.data;
        },
        async getHubbleQuotas() {
            let res = await this.$api.quota.getHubbleList();
            this.hubbleQuotas = res.data;
        }
    }
};
</script>
