<template>
    <el-row>
        <el-row v-show="strategySetInfo.show_add_user_tip">
            <el-col :span="20" :offset="2">
                <el-alert type="warning" :closable="false">
                    <p style="font-size:14px">
                        想拥有该策略的编辑权限，请联系策略创建者
                        <strong
                            class="red-font"
                        >{{strategySetInfo.strategy_set.creator}}</strong>为你添加协作者身份
                    </p>
                </el-alert>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="16" :offset="4" style="margin-top:20px;">
                <FullVolumeConfirmList
                    :strategySetId="$route.query.strategy_set_id"
                    ref="FullVolumeConfirmList"
                />
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="20" :offset="2">
                <!-- <el-card class="box-card"> -->
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span style="color:#d04925;font-size:23px">
                            策略名称：
                            <strong>{{strategySetInfo.strategy_set.strategy_set_name}}</strong>
                            ({{strategySetInfo.strategy_set_type_name}})
                        </span>
                        <span style="display:inline-block;float:right">
                            <el-button type="text" @click="showUpdateRecord">变更记录</el-button>
                        </span>
                    </div>

                    <el-timeline>
                        <el-timeline-item
                            key="1"
                            :timestamp="strategySetInfo.strategy_set.create_time"
                        >
                            <el-tag size="medium">{{strategySetInfo.strategy_set.creator}}</el-tag>创建
                        </el-timeline-item>
                        <el-timeline-item
                            key="2"
                            :timestamp="strategySetInfo.strategy_set.update_time"
                        >
                            <el-tag size="medium">{{strategySetInfo.last_update_by}}</el-tag>更新
                        </el-timeline-item>
                    </el-timeline>
                    <!-- <p>
                        <strong>创建信息：</strong>
                        {{strategySetInfo.strategy_set.creator}}</el-tag>创建于
                        <el-tag size="medium">{{strategySetInfo.strategy_set.create_time}}</el-tag>
                    </p>
                    <p>
                        <strong>更新信息：</strong>
                        <el-tag size="medium">{{strategySetInfo.last_update_by}}</el-tag>更新于
                        <el-tag size="medium">{{strategySetInfo.strategy_set.update_time}}</el-tag>
                    </p>-->
                    <el-divider></el-divider>
                    <el-form label-width="120px">
                        <el-form-item
                            label="产品线："
                            style="margin-bottom: 2px"
                        >{{strategySetInfo.product_chinese_name}} | {{strategySetInfo.exp_zone_chinese_name}}</el-form-item>
                        <el-form-item
                            label="审核人："
                            style="margin-bottom: 2px"
                        >{{strategySetInfo.strategy_set.assign_auditor != '' ? strategySetInfo.strategy_set.assign_auditor.replace(',', '、') : '超级管理员'}}</el-form-item>
                        <el-form-item label="协作者：" style="margin-bottom: 2px">
                            {{strategySetInfo.strategy_set.access_user != '' ? strategySetInfo.strategy_set.access_user.replace(',', '、') : '暂无'}}
                            <el-button type="warning" size="small" @click="toAddUser">
                                <i class="el-icon-plus"></i>添加
                            </el-button>
                        </el-form-item>
                        <el-form-item label="策略类型：" style="margin-bottom: 2px">
                            <span
                                style="font-size:20px"
                            >{{strategySetInfo.strategy_set.func == 'exp' ? '效果实验' : '功能控制'}}</span>
                        </el-form-item>
                    </el-form>
                    <el-collapse>
                        <el-collapse-item name="1">
                            <template slot="title">
                                <strong style="font-size:16px;">描述</strong>
                                <i class="header-icon el-icon-info"></i>
                            </template>
                            <div
                                v-html="strategySetInfo.strategy_set.strategy_set_comment.replace(/\n/g,'<br>')"
                            ></div>
                        </el-collapse-item>
                    </el-collapse>
                </el-card>

                <el-card class="box-card" style="margin-top:10px;">
                    <div slot="header" class="clearfix">
                        <h4>
                            <strong>策略构成</strong>
                        </h4>
                    </div>
                    <el-row :gutter="50">
                        <el-col
                            :span="12"
                            v-for="(item) in strategySetInfo.strategy_list"
                            :key="item.id"
                        >
                            <el-card
                                class="box-card"
                                style="margin-top:10px;height:350px;overflow:scroll"
                            >
                                <el-alert
                                    :title="item.strategy_name"
                                    type="info"
                                    effect="dark"
                                    :closable="false"
                                    style="margin-bottom:10px"
                                    v-if="item.verify.type == 'string'"
                                ></el-alert>
                                <el-alert
                                    :title="item.strategy_name"
                                    type="warning"
                                    effect="dark"
                                    :closable="false"
                                    style="margin-bottom:10px"
                                    v-if="item.verify.type == 'int'"
                                ></el-alert>
                                <el-alert
                                    :title="item.strategy_name"
                                    type="success"
                                    effect="dark"
                                    :closable="false"
                                    style="margin-bottom:10px"
                                    v-if="item.verify.type == 'bool'"
                                ></el-alert>
                                <p class="p-break">
                                    <b>默认值：</b>
                                    {{item.strategy_defult_value}}
                                </p>
                                <p class="p-break">
                                    <b>数据类型：</b>
                                    {{item.verify.type}}
                                </p>
                                <p class="p-break" v-show="item.verify.type == 'int'">
                                    <b>最小取值：</b>
                                    {{item.verify.min}}
                                </p>
                                <p class="p-break" v-show="item.verify.type == 'int'">
                                    <b>最大取值：</b>
                                    {{item.verify.max}}
                                </p>
                                <p class="p-break" v-show="item.verify.type == 'string'">
                                    <b>最大长度：</b>
                                    {{item.verify.max_length}}
                                </p>
                                <p class="p-break">
                                    <b>描述：</b>
                                    <span
                                        v-html="item.strategy_value_comment.replace(/\n/g,'<br>')"
                                    ></span>
                                </p>
                            </el-card>
                        </el-col>
                    </el-row>
                    <!-- </div> -->
                </el-card>
                <el-card
                    class="box-card"
                    style="margin-top:10px"
                    v-if="strategySetInfo.strategy_set.product_code == 783"
                >
                    <div slot="header" class="clearfix">
                        <h4>域占用情况：</h4>
                    </div>
                    <SingelDomainLayer :strategySetId="strategySetId"></SingelDomainLayer>
                </el-card>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="20" :offset="2">
                <el-card class="box-card" style="margin-top:10px;">
                    <div slot="header" class="clearfix">
                        <h4>
                            <strong>实验列表</strong>
                        </h4>
                    </div>
                    <ExpTable :listData="layerList" />

                    <el-pagination
                        @current-change="getExpList"
                        :current-page.sync="page"
                        layout="total, prev, pager, next"
                        :total="total"
                        :page-size="20"
                        :hide-on-single-page="true"
                        align="center"
                        background
                        style="margin-top:20px;"
                    ></el-pagination>
                </el-card>
            </el-col>
        </el-row>
        <el-row class="row" v-show="!showAuditBtn" style="margin-top:30px">
            <el-col align="center">
                <el-button
                    type="primary"
                    @click="toEditStrategySet"
                    :disabled="strategySetInfo.operation.is_edit == false"
                >编辑</el-button>
                <el-button
                    type="primary"
                    @click="toApplyAction('apply_offline', '下线')"
                    v-if="strategySetInfo.operation.is_offline"
                >下线</el-button>
            </el-col>
        </el-row>

        <el-row v-show="showAuditBtn" style="margin-top:30px">
            <el-col align="center">
                <el-button
                    type="primary"
                    @click="toAuditAction('audit_reject', '拒绝')"
                    v-if="strategySetInfo.audit_operation.is_refuse"
                >驳回</el-button>
                <el-button
                    type="primary"
                    @click="toAuditAction('audit_pass', '上线')"
                    v-if="strategySetInfo.audit_operation.is_online"
                >上线</el-button>
                <el-button
                    type="primary"
                    @click="toAuditAction('audit_pass', '下线')"
                    v-if="strategySetInfo.audit_operation.is_offline"
                >下线</el-button>
                <!-- <button class="btn btn-danger btn-lg" @click="toPassAudit('删除')" v-if="strategySetInfo.audit_operation.is_del">删除</button> -->
            </el-col>
        </el-row>

        <el-drawer
            :title="'确认'+applyActionText+'该策略集？选择审核人'"
            :visible.sync="drawer"
            direction="ttb"
        >
            <span>
                <el-alert
                    title="警告提示的文案"
                    type="warning"
                    v-show="applyActionMsg != ''"
                >{{applyActionMsg}}</el-alert>
                <el-form>
                    <el-form-item>
                        <AddAuditor
                            :expZoneCode="strategySetInfo.strategy_set.exp_zone_code"
                            v-model="assignAuditor"
                            style="float:left"
                        />
                        <el-button
                            type="primary"
                            @click="doApplyAction()"
                            style="margin-left:50px;float:left"
                        >确认</el-button>
                    </el-form-item>
                </el-form>
            </span>
        </el-drawer>

        <el-drawer
            title="策略变更记录"
            :visible.sync="showRecord"
            :custom-class="'drawerscroll'"
            size="80%"
        >
            <el-row style="overflow:scroll" :style="{'height' : drawerHeight}">
                <StrategyUpdateRecord :strategySetId="strategySetId" />
            </el-row>
        </el-drawer>
    </el-row>
</template>

<script>
import SingelDomainLayer from "@/Faraday/Domain/SingelDomainLayer.vue";
import VolumeFullApplyList from "./VolumeFullApplyList.vue";
import FullVolumeConfirmList from "../Exp/FullVolumeConfirmList.vue";
import AddAuditor from "../Exp/AddAuditor";
import StrategyUpdateRecord from "./StrategyUpdateRecord.vue";
import ExpTable from "../Exp/ExpTable.vue";

import lodash from "lodash";
export default {
    data() {
        return {
            strategySetInfo: {
                operation: {
                    is_edit: false,
                    is_del: false,
                    is_offline: false,
                    is_online: false,
                    is_add_user: false
                },
                audit_operation: {
                    is_refuse: false,
                    is_del: false,
                    is_offline: false,
                    is_online: false
                },
                strategy_set: {
                    strategy_set_comment: "",
                    access_user: "",
                    assign_auditor: ""
                }
            },
            showAuditBtn: false,
            strategySetId: 0,
            applyActionText: "",
            applyAction: "",
            applyActionMsg: "",
            drawer: false,
            assignAuditor: [],
            showRecord: false,
            total: 0,
            page: 1,
            layerList: [],
            drawerHeight : '600px',
        };
    },
    components: {
        SingelDomainLayer,
        VolumeFullApplyList,
        AddAuditor,
        StrategyUpdateRecord,
        FullVolumeConfirmList,
        ExpTable
    },
    mounted() {
        if (this.$route.query.strategy_set_id > 0) {
            this.strategySetId = this.$route.query.strategy_set_id;
            this.getStrategyInfo();
            this.getExpList();
        } else {
            this.$router.push({ path: "/strategy/index" });
        }

        if (this.$route.name == "strategy_audit_detail") {
            this.showAuditBtn = true;
        }

        if (this.$route.query.action == "open_record") {
            this.showUpdateRecord();
        }
        this.drawerHeight = (document.documentElement.clientHeight - 150) + 'px'
    },
    methods: {
        async getStrategyInfo() {
            let res = await this.$api.strategy.getStrategyByGo(
                this.strategySetId
            );

            this.strategySetInfo = res.data;
            this.strategySetInfo.strategy_set.access_user = lodash.trim(
                this.strategySetInfo.strategy_set.access_user,
                ","
            );

            this.strategySetInfo.strategy_list = this.strategySetInfo.strategy_list.map(
                item => {
                    item.verify = JSON.parse(item.verification);
                    return item;
                }
            );
            setTimeout(() => {
                this.$refs["FullVolumeConfirmList"].getConfirmList();
            }, 100);
        },
        toEditStrategySet: function() {
            this.$router.push({
                path: "/strategy/edit",
                query: { strategy_set_id: this.$route.query.strategy_set_id }
            });
        },
        toApplyAction(action, text) {
            this.drawer = true;
            this.applyActionText = text;
            this.applyAction = action;
        },
        async doApplyAction() {
            let res = {};

            if (this.applyAction == "apply_offline") {
                res = await this.$api.strategy.applyOffline(
                    this.strategySetId,
                    this.assignAuditor.join(",")
                );
            }

            if (res.errcode != 0) {
                this.applyActionMsg = res.errmsg || "操作失败";
                return;
            }

            this.applyActionMsg = "";

            this.$message({
                message: `申请${this.applyActionText}成功，审核通过后生效`,
                duration: 500,
                onClose: () => {
                    this.$tool.reload();
                }
            });
        },
        async toAuditAction(action, text) {
            if (!confirm(`确认${text}该策略集？`)) {
                return;
            }
            let res = {};

            if (action == "audit_pass") {
                res = await this.$api.strategy.auditPass(this.strategySetId);
            } else if (action == "audit_reject") {
                res = await this.$api.strategy.auditReject(this.strategySetId);
            } else {
                return;
            }

            if (res.errcode != 0) {
                this.$message({
                    message: res.errmsg || "操作失败，请刷新页面重试",
                    type: "error"
                });
                return;
            }

            this.$message({
                message: `${text}成功`,
                type: "success"
            });

            this.$router.push({ path: "/strategy/audit_list" });
        },
        toAddUser() {
            this.$prompt("请输入用户名", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "关闭",
                inputPattern: /.*/,
                inputErrorMessage: "用户名不能为空",
                top: 10
            })
                .then(({ value }) => {
                    this.addUser(value);
                })
                .catch(() => {});
        },
        async addUser(user) {
            let res = await this.$api.strategy.addUser(
                this.strategySetId,
                user
            );
            if (res.errcode < 0) {
                this.$message(res.errmsg);
                return;
            }
            this.$message({
                message: "添加成功",
                type: "success",
                duration: 500,
                onClose: () => {
                    this.$tool.reload();
                }
            });
        },
        showUpdateRecord() {
            this.showRecord = true;
        },
        async getExpList() {
            let res = await this.$api.exp.getLayerListOfStrategy(
                this.strategySetId,
                this.page
            );
            this.$set(this, "layerList", res.data.list);
            this.total = res.data.total;
        }
    }
};
</script>

<style scoped lang="less">
.drawerscroll {
    overflow: scroll;
}
</style>
