<template>
    <div class="snap-shot">
        <el-row>
            <el-col :span="6" class="now-shot">
                <el-button type="success" @click="backOne">最新一条</el-button>
                <el-button type="defalut" @click="selectPrev" plain>
                    <i class="el-icon-back"></i>
                </el-button>
            </el-col>
            <el-col :span="14" class="block">
                <p class="demonstration">
                    <strong>倒数第{{skip+1}}条</strong>实验快照
                </p>
                <el-slider v-model="value1" :step="10" disabled :show-tooltip="false"></el-slider>
            </el-col>
            <el-col :span="4" class="next-shot">
                <el-button type="defalut" @click="selectLater" plain>
                    <i class="el-icon-right"></i>
                </el-button>
            </el-col>
        </el-row>

        <el-row>
            <el-tabs
                type="border-card"
                :span="18"
                :offset="3"
                v-model="activeName"
                style="margin-top:30px"
                @tab-click="switchTab"
            >
                <el-tab-pane label="全局快照" name="global"></el-tab-pane>
                <el-tab-pane label="流量侧实验快照" name="traffic_exp"></el-tab-pane>
                <el-tab-pane label="广告主侧实验快照" name="cust_exp"></el-tab-pane>
                <el-row>
                    <p>
                        <el-date-picker v-model="date" type="date" placeholder="选择日期" value-format="yyyy-MM-dd"></el-date-picker>
                        <el-button type="info" size="small" @click="locateDate">定位到该日期</el-button>
                    </p>
                </el-row>

                <p>
                    <strong>
                        倒数第
                        <strong>{{skip+1}}</strong>
                        条全局快照，快照生成时间:{{snapshotData[activeName].snapshot_time}}
                    </strong>
                </p>
                <ShowJson :showStr="snapshotContent"></ShowJson>
            </el-tabs>
        </el-row>
    </div>
</template>
<script>
import moment from "moment";
export default {
    data() {
        return {
            tableType: "global",
            snapshotData: {
                global: {},
                traffic_exp: {},
                traffic_exp_new: {},
                cust_exp: {}
            },
            activeName: "global",
            date: "",
            skip: 0,
            snapshotContent: "",
            value1: 100
        };
    },
    mounted() {
        this.getSnapshot();
    },
    methods: {
        switchTab: function(tab, event) {
            this.tableType = this.activeName;
            this.snapshotContent = this.snapshotData[
                this.activeName
            ].strategy_content;
        },
        async locateDate() {
            //let tmpDate = moment(this.date).subtract(1, 'days').format("YYYY-MM-DD")
            let res = await this.$api.user.locateSnapshotByGo(
                this.date,
                this.activeName
            );
            this.skip = res.data.offset - 1;
            this.getSnapshot();
        },
        selectPrev: function() {
            if (this.skip == 0) {
                this.$message.error("已经是最新的一条快照");
                return;
            }
            if (this.skip <= 10) {
                this.value1 = this.value1 + 10;
            }
            this.skip--;
            this.getSnapshot();
        },
        selectLater: function() {
            this.value1 = this.value1 - 10;
            this.skip++;
            this.getSnapshot();
        },
        async getSnapshot() {
            let res = await this.$api.user.getSnapshotByGo(this.skip);
            this.snapshotData = res.data;
            this.switchTab(this.tableType);
        },
        backOne() {
            this.skip = 0;
            this.value1 = 100;
        }
    }
};
</script>

