.demonstration {
    text-align: center;
    font-size: 16px;
}
.now-shot {
    padding-left: 100px;
    padding-top: 30px;
}
.next-shot {
    padding-top: 30px;
    padding-left: 50px;
}
.el-slider {
    transform: rotateY(180deg);
}
.red-font {
    color: red;
}
.green-font {
    color: green;
}
.grey-font {
    color: grey;
}
</style>

