<template>
    <div>
        <ul class="timeline">
            <li class="item" v-for="record in listData">
                <span class="date">
                    {{record.date}}
                    <br>
                    {{record.time}}
                </span>
                <span class="action" v-html="record.event_name">
                </span>
                <span class="operator">{{record.user_name}}</span>
                <!-- 插槽 -->
                <slot name="detail" :record="record"></slot>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: ["data"],
    data() {
        return {
            listData: []
        };
    },
    watch:{
        data(newVal,old){
            this.listData=newVal;
        }
    },
    mounted(){
        this.getExpRecord();
    },
    methods: {
        async getExpRecord() {
            this.listData=this.data;
        }
    },
};
</script>

