<template>
    <div>
        <el-row>
            <el-col :span="16" :offset="3">
                <el-form ref="form" :inline="true">
                    <el-form-item label="时间选择">
                        <el-date-picker
                            v-model="time"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            align="left"
                            size="small"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item>
                        <el-checkbox-group v-model="valType">
                            <el-checkbox label="kunkka">Kunkka</el-checkbox>
                            <el-checkbox label="faraday">法拉第</el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>

                    <el-form-item style="padding-left:80px">
                        <el-button type="primary" @click="getAllRecord">查询</el-button>
                    </el-form-item>
                </el-form>
                <el-form :inline="true">
                    <el-form-item label="产品线">
                        <el-select v-model="exp_zone" placeholder="产品线">
                            <el-option label="全部" :value="0"></el-option>
                            <el-option
                                v-for="(item, i) in expZoneList"
                                :key="i"
                                :label="item.product_chinese_name + '-' + item.exp_zone_chinese_name"
                                :value="item.exp_zone_code"
                                v-if="expZones.indexOf(item.exp_zone_code) > -1"
                            ></el-option>
                        </el-select> 只筛选<strong>法拉第</strong>事件
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="18" :offset="2">
                <loading :state="loadState" />
                <Timeline :data="dataFilt">
                    <template slot="detail" slot-scope="slotDetailExps">
                        <el-row
                            v-if="slotDetailExps.record.source =='faraday'"
                            style="padding:20px 0;"
                        >
                            <el-col :span="24">
                                <div
                                    class="common-card page-wrapper-header-item"
                                    v-for="item in slotDetailExps.record.faraday_series_event"
                                    style="margin-top:7px;overflow: hidden;"
                                >
                                    <div class="detail" style="padding:0 10px">
                                        <strong>{{item.action_text}}</strong>
                                        <div class="pull-right">
                                            <span
                                                class="label label-default p-break"
                                            >{{item.record.user_name}}</span>
                                            <span
                                                class="label label-success p-break"
                                            >{{item.record.create_time.substr(5,11)}}</span>
                                        </div>
                                    </div>
                                </div>
                            </el-col>
                        </el-row>
                        <el-row
                            v-else-if="slotDetailExps.record.source =='kunkka'"
                            style="padding:20px 0;"
                        >
                            <el-col :span="24">
                                <div class="common-card page-wrapper-header-item">
                                    <div class="detail p-break" style="padding:10px">
                                        <b>项目名：</b>
                                        {{slotDetailExps.record.kunkka_series_event[0].project}}
                                        <br />
                                        <br />
                                        <b>创建人：</b>
                                        {{slotDetailExps.record.kunkka_series_event[0].creator}}
                                        <br />
                                        <br />
                                        <b>目标主机（分组）：</b>
                                        {{slotDetailExps.record.kunkka_series_event[0].groups}}
                                        <br />
                                        <br />
                                        <b>上线版本：</b>
                                        {{slotDetailExps.record.kunkka_series_event[0].release}}
                                        <br />
                                        <br />
                                        <b>描述：</b>
                                        {{slotDetailExps.record.kunkka_series_event[0].describe}}
                                    </div>
                                </div>
                            </el-col>
                        </el-row>
                    </template>
                </Timeline>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import moment from "moment";
import Timeline from "./Timeline";
export default {
    data() {
        return {
            time: [],
            list: [],
            loadState: "",
            valType: ["kunkka", "faraday"],
            exp_zone: 0,
            expZones: [],
            expZoneList: []
        };
    },
    components: {
        Timeline
    },
    mounted() {
        this.time = [
            moment()
                .subtract(2, "days")
                .format("YYYY-MM-DD"),
            moment().format("YYYY-MM-DD")
        ];
        this.getExpZones();
        this.getAllRecord();
    },
    computed: {
        dataFilt() {
            return this.list.filter(k => {
                let eventType = this.valType;
                if (eventType.length < 1) {
                    eventType = ["faraday", "kunkka"];
                }

                if (this.exp_zone < 1) {
                    return eventType.indexOf(k.source) > -1;
                } else {
                    if (
                        eventType.indexOf("faraday") > -1 &&
                        k.source == "faraday"
                    ) {
                        return k.exp_zone_code == this.exp_zone;
                    }

                    if (eventType.indexOf("kunkka") > -1) {
                        return k.source == "kunkka";
                    }
                }
            });
        }
    },
    methods: {
        async getAllRecord() {
            this.list = [];
            this.loadState = "loading";
            let res = await this.$api.user.getAllRecordByGo(this.time);
            if (!res.data) {
                this.loadState = "暂无数据";
                return;
            }
            this.loadState = "";
            var dataBysort = res.data.sort(this.compare);
            this.$set(this, "list", dataBysort);
            let expZones = [];
            this.list.forEach(element => {
                expZones.push(element.exp_zone_code);
            });
            this.expZones = expZones;
        },
        compare(obj1, obj2) {
            let val1 = moment(obj1.create_time);
            let val2 = moment(obj2.create_time);
            if (val1.isBefore(val2)) {
                return 1;
            } else if (val1.isAfter(val2)) {
                return -1;
            } else {
                return 0;
            }
        },
        async getExpZones() {
            let res = await this.$api.user.getUserExpZoneList(4);

            this.expZoneList = res.data;
        }
    }
};
</script>
