<template>
    <el-row>
        <el-col :offset="2" :span="20">
            <el-form
                :label-position="'right'"
                label-width="180px"
                :model="layerInfo"
            >
                <el-form-item label="所属产品线(实验区)："
                    >{{ formate.product_chinese_name }} |
                    {{ formate.exp_zone_chinese_name }}</el-form-item
                >
                <el-form-item label="实验策略：">{{
                    strategySet.strategy_set_name
                }}</el-form-item>
                <el-form-item label="实验层名称：">{{
                    layerInfo.exp_layer_name
                }}</el-form-item>
                <el-form-item label="实验描述：">
                    <el-input
                        type="textarea"
                        v-model="layerInfo.exp_layer_comment"
                        rows="6"
                        placeholder="【请填写实验的预期目标，实验的设计方案，实验的放量计划，实验关心的核心指标等】"
                    ></el-input>
                </el-form-item>
                <el-form-item
                    label="实验对象类型："
                    v-if="layerInfo.is_same_type == 0"
                    >{{ formate.alloc_type_name }}</el-form-item
                >
                <template v-if="layerInfo.is_same_type > 0">
                    <el-form-item label="流量A/B分桶类型：">{{
                        formate.alloc_type_name
                    }}</el-form-item>
                    <el-form-item label="实验对象类型：">{{
                        layerInfo.exp_object_type_name
                    }}</el-form-item>
                </template>

                <el-form-item
                    label="实验对象："
                    v-if="layerInfo.is_same_type > 0"
                >
                    <el-input
                        type="textarea"
                        v-model="layerInfo.exp_object"
                        rows="6"
                        placeholder="【请填写实验的预期目标，实验的设计方案，实验的放量计划，实验关心的核心指标等】"
                        :disabled="formate.is_online_ever"
                    ></el-input>
                </el-form-item>
                <!-- </template> -->
            </el-form>
        </el-col>

        <el-col :offset="2" :span="20" v-if="showExpList">
            <SetExp ref="setexp" v-if="layerInfo.is_same_type > 0" />
            <SetExpTwo
                ref="setexp"
                v-if="layerInfo.is_same_type == 0"
                :allocType="layerInfo.alloc_type_code"
            />
        </el-col>

        <el-dialog title="选择审核人" :visible.sync="showDialogAuditor">
            <AddAuditor
                :expZoneCode="layerInfo.exp_zone_code"
                v-model="assignAuditor"
            />
            <span slot="footer" class="dialog-footer">
                <el-button @click="showDialogAuditor = false">取 消</el-button>
                <el-button type="primary" @click="hideFormAuditor"
                    >确 定</el-button
                >
            </span>
        </el-dialog>

        <el-col style="margin-top: 20px" align="center">
            <el-button
                type="primary"
                @click="toEditExp"
                v-show="layerInfo.exp_layer_status_code == 2"
                :disabled="isDisableSubmit"
                >{{
                    isDisableSubmit ? "正在提交中，请等待..." : "修改实验"
                }}</el-button
            >
            <el-button
                type="primary"
                @click="toEditExp"
                v-show="layerInfo.exp_layer_status_code == 3"
                :disabled="isDisableSubmit"
                >{{
                    isDisableSubmit ? "正在提交中，请等待..." : "放量实验"
                }}</el-button
            >
        </el-col>
    </el-row>
</template>

<script>
import FlowAlloc from "./FlowAlloc.vue";
import AddAuditor from "./AddAuditor";
import SetExp from "./SetExp";
import SetExpTwo from "./SetExp2";

export default {
    data() {
        return {
            formate: {
                alloc_type_name: ""
            },
            strategySet: {},
            expLayerId: 0,
            layerInfo: {
                exp_zone_code: "",
                exp_object: "",
                exp_object_type_name: "",
                is_same_type: 1
            },
            showExpList: false,
            isDisableSubmit: false,
            showDialogAuditor: false,
            assignAuditor: []
        };
    },
    components: {
        FlowAlloc,
        SetExp,
        SetExpTwo,
        AddAuditor
    },
    mounted() {
        if (this.$route.query.exp_layer_id > 0) {
            this.expLayerId = this.$route.query.exp_layer_id;
        } else {
            this.$router.push({
                path: "/"
            });
        }
        this.getlayerInfo();
    },
    methods: {
        async getlayerInfo() {
            let res = await this.$api.exp.getExpDetailByGo(this.expLayerId);
            //基本信息
            this.layerInfo = res.data.origin_exp_layer;
            this.formate = res.data.format;
            this.strategySet = res.data.strategy_set;
            if (
                this.layerInfo.exp_layer_status_code != 2 &&
                this.layerInfo.exp_layer_status_code != 3
            ) {
                this.$message({
                    message: "该实验暂时不能编辑",
                    type: "error"
                });
                this.$router.push({
                    path: "/lab/detail",
                    query: { exp_layer_id: this.expLayerId }
                });
                return;
            }
            this.setExpObject();
            this.$store.commit(
                "setStrategySetId",
                this.layerInfo.strategy_set_id
            );
            let list = res.data.exp_list.filter(item => {
                return item.exp_type_code > 0;
            });
            list.forEach((item, idx) => {
                list[idx].tab_name = idx + "";
            });
            this.$store.commit("setExpList", list);
            this.showExpList = true;
        },
        setExpObject() {
            let retData = this.$tool.formateCustExpObject(this.layerInfo);
            if (Object.keys(retData).length < 1) {
                return;
            }
            let expObject = retData[0];
            this.layerInfo.exp_object_type = expObject.name_code;
            this.$set(this.layerInfo, "exp_object", expObject.data);
            this.layerInfo.exp_object_type_name = expObject.name;
        },
        getUpdateParam: function() {
            let updateData = {
                strategy_set_id: this.layerInfo.strategy_set_id,
                exp_layer_comment: this.layerInfo.exp_layer_comment,
                data: this.$store.state.create_exp.exp_list,
                assign_auditor: this.assignAuditor
            };

            if (
                this.layerInfo.is_same_type > 0 &&
                !this.formate.is_online_ever
            ) {
                updateData.alloc_condition_value = {};
                updateData.alloc_condition_value[
                    this.layerInfo.exp_object_type
                ] = this.layerInfo.exp_object;
                updateData.alloc_condition_enable = 1;
            }

            if (this.$store.state.create_exp.del_exp_list.length > 0) {
                this.$store.state.create_exp.del_exp_list.forEach(item => {
                    updateData.data.push(item);
                });
            }

            return updateData;
        },
        hideFormAuditor() {
            if (this.assignAuditor.length < 1) {
                this.$message("请指定审核人");
            } else {
                this.showDialogAuditor = false;
                this.toEditExp();
            }
        },
        async toEditExp() {
            if (this.assignAuditor.length < 1) {
                this.showDialogAuditor = true;
                return;
            }

            var updataParam = this.getUpdateParam();
            updataParam.exp_layer_id = parseInt(this.$route.query.exp_layer_id);

            let successMsg =
                this.layerInfo.exp_layer_status_code == 3
                    ? "放量成功"
                    : "修改成功";

            if (this.isDisableSubmit) {
                return false;
            }

            //this.isDisableSubmit = true;
            try {
                let res = await this.$api.exp.updateByGo(updataParam);
                if (res.errcode == 0) {
                    this.$alert("修改成功，等待审核员审核", "创建成功", {
                        confirmButtonText: "确定",
                        callback: action => {
                            this.$router.push({
                                path: "/lab/detail",
                                query: {
                                    exp_layer_id: this.$route.query.exp_layer_id
                                }
                            });
                        }
                    });
                } else {
                    this.isDisableSubmit = false;
                    this.$message(res.errmsg);
                }
            } catch (err) {
                this.$message(err.message || "请刷新页面重试");
            }
        }
    }
};
</script>