<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <strong>版本列表</strong>
            <el-button
                style="float: right; padding: 3px 0"
                type="text"
                :icon="isFold ? 'el-icon-arrow-down' : 'el-icon-arrow-up'"
                @click="setFold"
            >{{ !isFold ? '折叠' : '更多版本'}}</el-button>
        </div>
        <template v-for="(item, idx) in showExpVersionList">
            <el-row style="margin-bottom:15px;" :key="idx">
                <el-col :span="2">
                    <el-tag
                        :type="item.version_code == expVersionCode ? 'danger' : ''"
                    >版本-{{item.version_code}}</el-tag>
                </el-col>
                <el-col :span="5">
                    <el-button
                        :type="item.version_code == expVersionCode ? 'danger' : ''"
                        size="small"
                        @click="queryByExpVersion(item)"
                    >
                        <span>
                            {{item.begin_time.substr(5)}} 至
                            <template
                                v-if="item.end_time != '现在'"
                            >{{item.end_time.substr(5)}}</template>
                            <template v-else-if="item.end_time == '现在'">现在</template>
                        </span>
                    </el-button>
                </el-col>
                <el-col :span="17">
                    <div class="progress" style="height: 35px;margin-bottom: 0px">
                        <div
                            class="progress-bar bg-warning"
                            :style="{'width': item.base_exp_info.prograss_bar_wd + '%', 'line-height': '35px',}"
                            @mouseover="showExpInfo(item.base_exp_info, $event)"
                        >
                            <span>
                                <b
                                    v-show="exp_layer.is_same_type == 1"
                                >{{item.base_exp_info.traffic_weight_percent + '%'}}</b>(基准实验)
                            </span>
                        </div>
                        <template v-for="(sonExp, index) in item.test_exp_info">
                            <div
                                :class="['progress-bar', (index %2 == 0) ? 'bg-primary' : 'bg-info']"
                                :style="{'width': sonExp.prograss_bar_wd + '%', 'line-height': '35px'}"
                                @mouseover="showExpInfo(sonExp, $event)"
                                :key="sonExp.id"
                            >
                                <span>
                                    <b
                                        v-show="exp_layer.is_same_type == 1"
                                    >{{sonExp.traffic_weight_percent + '%'}}</b>
                                    ({{sonExp.exp_title}})
                                </span>
                            </div>
                        </template>
                        <div
                            class="progress-bar progress-bar-color-grey"
                            :style="{'width': item.rest_exp_info.prograss_bar_wd + '%', 'line-height': '35px'}"
                            @mouseover="showExpInfo(item.rest_exp_info, $event)"
                        >
                            <span>
                                <b
                                    v-show="exp_layer.is_same_type == 1"
                                >{{item.rest_exp_info.traffic_weight_percent + '%'}}</b>(未使用流量）
                            </span>
                        </div>
                    </div>
                </el-col>
            </el-row>
        </template>
        <el-row>
            <el-col>
                <el-form ref="form" :label-position="'left'" label-width="100px" :inline="true">
                    <el-form-item label="自定义时间">
                        <el-date-picker
                            v-model="timeArea"
                            type="datetimerange"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            align="left"
                            size="small"
                            format="yyyy-MM-dd HH:mm"
                            @change="clickToQuery"
                            value-format="yyyy-MM-dd HH:mm"
                            :clearable="false"
                            :time-arrow-control="true"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="实验前时间" v-if="exp_layer.is_same_type == 0 ">
                        <el-date-picker
                            v-model="unExpTimeArea"
                            type="datetimerange"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            align="left"
                            size="small"
                            format="yyyy-MM-dd HH:mm"
                            value-format="yyyy-MM-dd HH:mm"
                            :time-arrow-control="true"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item>
                        <el-button
                            type="warning"
                            @click="clickToQuery"
                            icon="el-icon-search"
                            size="small"
                        >查询</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </el-card>
</template>
<script>
import moment from "moment";

import { mapState } from "vuex";
export default {
    props: ["expLayerId"],
    data() {
        return {
            expVersionList: [],
            expVersionCode: 1,
            versionInfo: {},
            isFold: true,
            pickerOptions: {
                shortcuts: [
                    {
                        text: "今日",
                        onClick(picker) {
                            picker.$emit("pick", [
                                moment()
                                    .startOf("day")
                                    .toDate(),
                                moment().toDate()
                            ]);
                        }
                    },
                    {
                        text: "昨日",
                        onClick(picker) {
                            picker.$emit("pick", [
                                moment()
                                    .subtract("days", 1)
                                    .startOf("day")
                                    .toDate(),
                                moment()
                                    .startOf("day")
                                    .toDate()
                            ]);
                        }
                    },
                    {
                        text: "最近3日",
                        onClick(picker) {
                            picker.$emit("pick", [
                                moment()
                                    .subtract("days", 3)
                                    .startOf("day")
                                    .toDate(),
                                moment()
                                    .startOf("day")
                                    .toDate()
                            ]);
                        }
                    },
                    {
                        text: "最近一周",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 7
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近一个月",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 30
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    }
                ]
            },
            timeArea: [],
            unExpTimeArea: []
        };
    },
    computed: {
        ...mapState({
            exp_layer: state => state.exp_effect.exp_layer
        }),
        showExpVersionList: function() {
            if (!this.isFold) {
                return this.expVersionList;
            }
            if (this.expVersionList.length > 3) {
                return this.expVersionList.slice(0, 3);
            }
            return this.expVersionList;
        }
    },
    watch: {
        expLayerId: function(layer, old) {
            this.getExpVerionList();
        }
    },
    methods: {
        setFold() {
            this.isFold = !this.isFold;
        },
        async getExpVerionList() {
            if (parseInt(this.expLayerId) < 1) {
                return;
            }
            let res = await this.$api.exp.getVersionList(this.expLayerId);
            if (res.data == null) {
                res.data = [];
            }
            this.expVersionList = res.data;
            if (this.expVersionList.length > 0) {
                this.queryByExpVersion(this.expVersionList[0]);
            } else {
                this.$message.error("该实验暂未上线运行");
            }
        },
        showExpInfo: function(expInfo, event) {
            $(event.currentTarget)
                .tooltip({
                    placement: "top",
                    title: expInfo.exp_title
                })
                .tooltip("show");
        },
        queryByExpVersion: function(data) {
            this.expVersionCode = data.version_code;
            this.versionInfo = data;
            let endTime = this.versionInfo.end_time;
            if (endTime == "现在") {
                endTime = moment().format("YYYY-MM-DD HH:mm");
            }
            this.$set(this, 'timeArea', [this.versionInfo.begin_time, endTime]);
            this.setUnExpTime();
            this.$emit("queryBySetTime", this.timeArea);
        },
        setUnExpTime() {
            if (this.exp_layer.is_same_type > 0) {
                return;
            }

            let tmp1 = moment(this.timeArea[0]).format("YYYY-MM-DD HH:00:00");
            let tmp2 = moment(this.timeArea[1]).format("YYYY-MM-DD HH:00:00");
            let unExpFromTimeUnix =
                moment(tmp1).unix() -
                (moment(tmp2).unix() - moment(tmp1).unix());
            let unExpFromTime = moment
                .unix(unExpFromTimeUnix)
                .format("YYYY-MM-DD HH:mm:00");

            this.unExpTimeArea = [
                unExpFromTime,
                moment(tmp1).format("YYYY-MM-DD HH:00:00")
            ];
            this.$store.commit("setUnExpTime", this.unExpTimeArea);
        },
        clickToQuery: function() {
            this.$emit("clickToQuery", this.timeArea, this.unExpTimeArea);
        }
    }
};
</script>
<style scoped>
.text {
    font-size: 14px;
}

.item {
    margin-bottom: 18px;
}
</style>


