<template>
    <div>
        <!-- <div style="padding-bottom:5px">
            <el-form ref="form" :label-position="'left'" label-width="100px">
                <el-form-item label="实验名称" style="margin-bottom:1px;">
                    <el-link type="primary" :underline="false">
                        <router-link
                            :to="{path : '/lab/detail', query : {
                            exp_layer_id : expLayerId
                        }}"
                        >{{layerInfo.exp_layer_name}}</router-link>
                    </el-link>
                </el-form-item>
            </el-form>
        </div> -->
        <div style="padding-bottom:5px">
            <FansFilter @queryByFilter="queryByFilter" v-if="layerInfo.product_code == 102"></FansFilter>
            <EffectFilter @queryByFilter="queryByFilter" v-else-if="layerInfo.product_code > 0"></EffectFilter>
        </div>

        <div style="padding-bottom:5px">
            <ExpLayerVersionList
                @queryBySetTime="queryBySetTime"
                :expLayerId="expLayerId"
                @clickToQuery="clickToQuery"
            />
        </div>

        <ExpEffectTable :statData="statData" />
    </div>
</template>
<script>
import ExpEffectTable from "./ExpEffectOverview.vue";
import moment from "moment";
import lodash from "lodash";
import ChartConfig from "@/config/ChartConfig";
import EffectCrumb from "./EffectCrumb.vue";
import ExpLayerVersionList from "./ExpLayerVersionList.vue";
import EffectFilter from "./EffectFilter.vue";
import FansFilter from "./FansFilter.vue";

import BudgetExpConsume from "./BudgetExpConsume.vue";
import { mapState, mapGetters } from "vuex";
export default {
    data() {
        return {
            expLayerId: 0,
            layerInfo: {
                product_code: 0
            },

            fromTime: "",
            endTime: "",

            statData: {}
        };
    },
    mounted() {
        this.$store.commit('setBaseExpId', '')
        this.$store.commit('selectGroupBy', 'psid')
        this.$store.commit('setLoadState', false)
        if (this.$route.query.exp_layer_id > 0) {
            this.expLayerId = this.$route.query.exp_layer_id;
            this.getLayerInfo();
        } else {
            this.$router.push({
                path: "/monitor/expEffect"
            });
        }
    },
    methods: {
        queryByFilter: function() {
            this.loadRateData();
        },
        buildQueryParams: function() {
            return {
                exp_layer_id: this.expLayerId,
                begin: this.fromTime,
                end: this.endTime,
                extra_query: this.$store.state.exp_effect.query,
                base_exp_id: this.$store.state.exp_effect.base_exp_id,
                group_by: this.$store.state.exp_effect.group_by
            };
        },
        async loadRateData() {

            if (this.$store.state.exp_effect.load_state ) {
                this.$message("请等待上次请求完成")
                return
            }
            this.$store.commit("setLoadState", true);
            this.statData.quota_list = [];

            var sendObj = this.buildQueryParams();
            try {
                let res = await this.$api.effect.getStat(sendObj);

                this.$store.commit("setLoadState", false);

                if (res.errcode != 0) {
                    this.$message("暂无数据");
                    this.statData = {
                        quota_list : [],
                        exp_ids : [],
                        select_exps : [],
                        base_exp_id : [],
                        show_quotas : [],
                    }
                    return;
                }
                this.statData = res.data;

                this.fixTableLeftSlide();
                this.$emit("fixedTableHead", "#tableRateId");
            } catch (err) {
                this.$message.error("后台出错，请刷新页面重试下");
                this.$store.commit("setLoadState", false);
            }
        },
        async getLayerInfo() {
            let res = await this.$api.exp.getExpLayer(this.expLayerId);
            this.layerInfo = res.data.origin_exp_layer;
            this.$store.commit("setExpLayer", this.layerInfo);
            this.$store.commit("selectGroupBy", "psid");
            this.$store.commit("setHeadBreadCrumb", this.layerInfo.exp_layer_name)
        },
        queryBySetTime: function(time) {
            this.fromTime = time[0];
            this.endTime = time[1];
            this.$store.commit("setTime", time);
            this.loadRateData();
        },
        clickToQuery: function(time) {
            this.queryBySetTime(time);
        },
        fixTableLeftSlide: function() {
            $(".tab-pane").scroll(function(e) {
                var el = e.target;
                var scrollLeft = el.scrollLeft;
                if (scrollLeft > 0) {
                    $("#tableRateId thead tr")
                        .find("td:eq(0),td:eq(1)")
                        .css({
                            transform: "translateX(" + scrollLeft + "px)",
                            background: "#d3edf8"
                        }); //固定表头前两列
                    $("#tableRateId tbody .first-tr")
                        .find("td:eq(1)")
                        .css({
                            transform: "translateX(" + scrollLeft + "px)",
                            background: "#d3edf8"
                        }); //请求量之外前两列
                    $("#tableRateId tbody tr")
                        .find("td:eq(0)")
                        .css({
                            transform: "translateX(" + scrollLeft + "px)",
                            background: "#d3edf8",
                            "z-index": "1000"
                        }); //请求量之外前两列
                } else {
                    $("#tableRateId thead tr")
                        .find("td:eq(0),td:eq(1)")
                        .css({ transform: "", background: "" }); //固定表头前两列
                    $("#tableRateId tbody .first-tr")
                        .find("td:eq(1)")
                        .css({ transform: "", background: "" }); //请求量之外前两列
                    $("#tableRateId tbody tr")
                        .find("td:eq(0)")
                        .css({ transform: "", background: "" }); //请求量之外前两列
                }
            });
        }
    },
    components: {
        ExpEffectTable,
        ExpLayerVersionList,
        EffectFilter,
        BudgetExpConsume,
        FansFilter
    }
};
</script>

