<template>
    <div>
        <el-col>
            <h4>
                子策略配置
                <el-dropdown @command="handleCommand">
                    <el-button>
                        添加策略
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="string">String</el-dropdown-item>
                        <el-dropdown-item command="int">Integer</el-dropdown-item>
                        <el-dropdown-item command="bool">Boolean</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </h4>
            <el-divider></el-divider>
        </el-col>

        <el-col>
            <template v-for="(item, index) in strategyList">
                <el-card class="box-card" style="margin-top:20px;" v-if="item.is_del ==undefined">
                    <div slot="header" class="clearfix">
                        <span>子策略设置【数据类型：{{item.type}}】</span>
                        <el-button type="text" @click="delStrategy(index)" style="float: right; padding: 3px 0">
                            <i class="el-icon-delete"></i>
                        </el-button>
                    </div>
                    <el-form :label-position="'right'" label-width="150px">
                        <el-form-item label="键值/key：">
                            <el-input
                                v-model="strategyList[index].strategy_name"
                                placeholder="请输入子策略key，由英文字符和下划线组成"
                            ></el-input>
                        </el-form-item>

                        <el-form-item label="策略描述：">
                            <el-input
                                type="textarea"
                                v-model="strategyList[index].strategy_value_comment"
                                rows="6"
                                placeholder="请输入策略描述信息"
                            ></el-input>
                        </el-form-item>

                        <template v-if="item.type == 'int'">
                            <el-form-item label="最小值：">
                                <el-input
                                    v-model="strategyList[index].min"
                                    placeholder="子策略最小值"
                                    type="number"
                                ></el-input>
                            </el-form-item>

                            <el-form-item label="最大值：" v-show="item.type == 'int'">
                                <el-input
                                    v-model="strategyList[index].max"
                                    placeholder="子策略最大值"
                                    type="number"
                                ></el-input>
                            </el-form-item>

                            <el-form-item label="默认值：">
                                <el-input
                                    v-model="strategyList[index].strategy_defult_value"
                                    placeholder="子策略默认值"
                                    type="number"
                                ></el-input>
                            </el-form-item>
                        </template>

                        <template v-if="item.type == 'string'">
                            <el-form-item label="最大长度：">
                                <el-input
                                    v-model="strategyList[index].max_length"
                                    placeholder="字符串最大长度小于等于1024"
                                    type="number"
                                ></el-input>
                            </el-form-item>

                            <el-form-item label="默认值：">
                                <el-input
                                    v-model="strategyList[index].strategy_defult_value"
                                    placeholder="子策略默认值"
                                    type="textarea"
                                    :rows="6"
                                ></el-input>
                            </el-form-item>
                        </template>

                        <template v-if="item.type == 'bool'">
                            <el-form-item label="默认值：">
                                <el-radio
                                    v-model="strategyList[index].strategy_defult_value"
                                    label="true"
                                >true</el-radio>
                                <el-radio
                                    v-model="strategyList[index].strategy_defult_value"
                                    label="false"
                                >false</el-radio>
                            </el-form-item>
                        </template>
                    </el-form>
                </el-card>
            </template>
        </el-col>

        <el-dialog title="删除提示" :visible.sync="showDelDialog" width="30%">
            <span>是否确认删除这条策略？？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="showDelDialog = false">取 消</el-button>
                <el-button type="primary" @click="comfirmDelStrategy()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    props : ['propStrategyList'],
    data() {
        return {
            strategyList: [],
            showDelDialog: false,
            delIndex : -1,
        };
    },
    watch : {
        propStrategyList : function() {
            this.strategyList = this.propStrategyList
        }
    },
    methods: {
        handleCommand(command) {
            switch (command) {
                case "string":
                    this.addString();
                    break;
                case "int":
                    this.addInt();
                    break;
                case "bool":
                    this.addBool();
                    break;
            }
            this.$emit("setStrategyList", this.strategyList);
        },
        addString: function() {
            this.strategyList.push({
                type: "string",
                strategy_name: "",
                strategy_value_comment:
                    "【子策略描述信息】\n\n...\n【子策略规格说明】\n\n...",
                max_length: "1024",
                strategy_defult_value: ""
            });
        },
        addInt: function() {
            this.strategyList.push({
                type: "int",
                strategy_name: "",
                strategy_value_comment:
                    "【子策略描述信息】\n\n...\n【子策略规格说明】\n\n...",
                max: "",
                min: "",
                strategy_defult_value: ""
            });
        },
        addBool: function() {
            this.strategyList.push({
                type: "bool",
                strategy_name: "",
                strategy_value_comment:
                    "【子策略描述信息】\n\n...\n【子策略规格说明】\n\n...",
                strategy_defult_value: "true"
            });
        },
        delStrategy: function(idx) {
           this.delIndex = idx
           this.showDelDialog = true
        },
        comfirmDelStrategy() {
            //this.strategyList[this.delIndex].is_del = true;
            this.$set(this.strategyList[this.delIndex], 'is_del', "true");
            this.showDelDialog = false
            this.$emit("setStrategyList", this.strategyList);
        }
    }
};
</script>
