<template>
    <div>
        <el-row>
            <el-form
                :inline="true"
                :model="formData"
                class="demo-form-inline"
                style="display:flex;justify-content:space-between;width:80%;padding-left:50px;"
            >
                <el-form-item label="用户名">
                    <el-input v-model="formData.user_name" placeholder="邮箱前缀"></el-input>
                </el-form-item>
                <el-form-item label="权限类型">
                    <el-select
                        v-model="formData.user_type"
                        @change="authoritySel"
                        placeholder="活动区域"
                    >
                        <el-option
                            v-for="(item, index) in  authorityTy"
                            :label="item.value"
                            :key="index"
                            :value="item.user_type"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item v-show="showLibArea" label="实验区">
                    <el-select v-model="formData.area" multiple filterable placeholder="请选择">
                        <el-option
                            v-for="item in exp_zone_list"
                            :key="item.exp_zone_code"
                            :label="item.product_chinese_name + '-' + item.exp_zone_chinese_name"
                            :value="item.exp_zone_code"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="confirmDredge">
                        <span class="glyphicon glyphicon-plus"></span>
                    </el-button>
                </el-form-item>
            </el-form>
            <el-col :span="24" style="padding: 0 40px">
                <el-table :data="currentLabData" height="500" stripe border style="width: 100%">
                    <el-table-column label="编号">
                        <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.$index+1 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="用户名">
                        <template slot-scope="scope">
                            <span
                                v-if="scope.row.type != undefined && scope.row.type == 10"
                            >{{ scope.row.user_name }}</span>
                            <span v-else>
                                <a
                                    href="javascript:;"
                                    @click="showAccessDetail(scope.row.user_name)"
                                >{{ scope.row.user_name }}</a>
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="user_type_name" label="权限类型"></el-table-column>
                    <el-table-column prop="address" label="操作">
                        <template slot-scope="scope">
                            <span @click="deleteUser(scope.row.user_name)">
                                <a href="javascript:void(0);">删除</a>
                            </span>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
            <el-dialog :title="selectUserName+'权限列表'" :visible.sync="dialogTableVisible">
                <el-table :data="userAccessList" border style="width: 100%" stripe>
                    <el-table-column type="index"></el-table-column>
                    <el-table-column prop="product_chinese_name" label="产品线" width="180"></el-table-column>
                    <el-table-column prop="user_type_name" label="权限类型"></el-table-column>
                    <el-table-column prop="creator" label="创建人"></el-table-column>
                    <el-table-column prop="create_time" label="创建时间"></el-table-column>
                    <el-table-column prop="update_time" label="更新时间"></el-table-column>
                </el-table>
            </el-dialog>
        </el-row>
    </div>
</template>
<script>
export default {
    data() {
        return {
            exp_zone_code: 0,
            exp_zone_list: [], //实验区列表
            formData: {
                user_name: "",
                user_type: "0",
                area: []
            },
            authorityTy: [
                {
                    value: "全部",
                    user_type: "0"
                },
                {
                    value: "超级管理员",
                    user_type: "10"
                },
                {
                    value: "审核员",
                    user_type: "4"
                },
                {
                    value: "实验员",
                    user_type: "2"
                },
                {
                    value: "普通用户",
                    user_type: "1"
                }
            ],
            currentLabValue: "选择实验区查看",
            currentLabData: [], //实验区列表数据
            currentAuthority: "全部",
            selLibArea: [], //选中的实验区对应的编号
            showLibArea: true,
            selectUserName: "",
            userAccessList: [],
            dialogTableVisible: false
            // area: []
        };
    },
    watch: {
        "formData.user_name": function(val, oldVal) {
            this.searchUser();
        },
        "formData.user_type": function(val, oldVal) {
            this.searchUser();
        }
    },
    mounted() {
        this.getMyExpZoneList();
        this.searchUser();
        var that = this;
    },
    methods: {
        // 展示特定权限数据列表
        authoritySel() {
            this.showLibArea = this.formData.user_type == 10 ? false : true;
            this.searchUser();
        },
        // 获取所有权限列表信息
        getUserAuthorityList: function() {
            this.searchUser();
        },
        async searchUser() {
            var exp_zone_codes = this.formData.area.join(",");
            let res = await this.$api.user.queryUserByGo(
                this.formData.user_name,
                this.formData.user_type,
                exp_zone_codes
            );
            this.currentLabData = res.data;
        },
        //获取实验区列表信息
        async getMyExpZoneList() {
            let res = await this.$api.user.getUserExpZoneList();
            this.exp_zone_list = res.data;
        },
        // 开通权限
        async confirmDredge() {
            if (this.formData.user_name == "") {
                this.$message.error("请输入用户邮箱前缀");
                return;
            }
            if (this.formData.user_type == 0) {
                this.$message.error("请选择用户类型");
                return;
            }
            if (
                this.formData.area.length == 0 &&
                this.formData.user_type != 10
            ) {
                this.$message.error("请选择实验区");
                return;
            }
            var exp_zone_codes = this.formData.area.join(",");
            this.$confirm("确认是否添加？", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(async() => {
                    let res = await this.$api.user.addUserAccess(
                        this.formData.user_name,
                        this.formData.user_type,
                        exp_zone_codes
                    );
                    if (res.errcode == 0) {
                        this.$message({
                            type: "success",
                            message: "添加成功!"
                        });
                        this.getUserAuthorityList();
                    }
                    else{
                        this.$message({
                            type: "error",
                            message: res.errmsg
                        });
                    }
                    
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消操作"
                    });
                });
        },
        // 删除用户权限
        async deleteUser(userMes) {
            this.$confirm("是否确定删除?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(async () => {
                    let res = await this.$api.user.deleteUser(userMes);
                    if (res.errcode == 0) {
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                        this.getUserAuthorityList();
                    } else {
                        this.$message({
                            type: "error",
                            message: "删除失败"
                        });
                    }
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消删除"
                    });
                });
        },
        showAccessDetail(userName) {
            this.selectUserName = userName;
            this.getUserAccessData();
            this.dialogTableVisible = true;
        },
        async getUserAccessData() {
            let res = await this.$api.user.getUserAccess(this.selectUserName);
            this.userAccessList = res.data;
        }
    }
};
</script>
