<template>
    <el-card class="box-card" v-if="confirmList.length > 0">
        <div slot="header" class="clearfix">
            <strong>实验全量申请，等待实验创建人确认</strong>
        </div>
        <template v-for="(item, index) in confirmList">
            <p :key="index">
                {{index + 1}}.
                <label>{{item.apply.assign_user}}</label>
                <span>(实验：<router-link :to="{path : '/lab/detail', query : {
                    exp_layer_id : item.layer.exp_layer_id
                }}">{{item.layer.exp_layer_name}}</router-link>)</span>
                <el-tag>{{item.confirm_text}}</el-tag>
                <el-tag
                    class="label label-danger"
                    v-if="item.apply.as_status == -1"
                >理由：{{item.apply.reply_content}}</el-tag>
                
                <span style="display:inline-block;float:right" v-show="item.show_confirm_btn">
                    <el-button size="mini" type="info" @click="reject(item.apply)">拒绝</el-button>
                    <el-button size="mini" type="danger" @click="pass(item.apply)">确认</el-button>
                </span>
            </p>
            
        </template>
    </el-card>
</template>
<script>
export default {
    props: ["strategySetId"],
    data() {
        return {
            confirmList: []
        };
    },
    methods: {
        reject(confirm) {
            this.$prompt("请填写拒绝原因", "原因").then(({ value }) => {
                if (value == "") {
                    this.$message("请填写拒绝原因");
                    return false;
                }

                if (value == null) {
                    return;
                }

                this.confirmMsg(confirm.id, "no", value);
            });
        },
        pass(confirm) {
            this.$confirm("确认通过本次放量申请么", "确认").then(
                ({ value }) => {
                    this.confirmMsg(confirm.id, "yes", "");
                }
            );
        },
        async confirmMsg(confirmId, yesNo, msg) {
            let res = await this.$api.exp.confirmMsgByGo(
                confirmId + "",
                yesNo,
                msg || ""
            );
            this.$message({
                message: res.errmsg,
                type: "success",
                duration: 1000,
                onClose: () => {
                    this.$tool.reload();
                }
            });
        },
        async getConfirmList() {
            let res = await this.$api.strategy.getFullVolumeConfirms(
                this.strategySetId
            );

            if (res.errcode == 0) {
                if (res.data != null) {
                    this.confirmList = res.data;
                }
            }
        }
    }
};
</script>
