<template>
    <el-row>
        <el-tabs type="border-card" v-model="tabType">
            <el-tab-pane label="权限管理" name="AccessQuery"></el-tab-pane>
            <el-tab-pane label="访问查询" name="PageViewList"></el-tab-pane>
            <el-tab-pane label="系统修改" name="SystemManage"></el-tab-pane>
            <el-tab-pane label="数据修改" name="DatabaseManage"></el-tab-pane>
            <component :is="tabType"></component>
        </el-tabs>
    </el-row>
</template>

<script>
import AccessQuery from "./AccessQuery";
import PageViewList from "./PageViewList";
import SystemManage from "./SystemManage";
import DatabaseManage from "./DatabaseManage";

export default {
    data() {
        return {
            tabType: "AccessQuery"
        };
    },
    mounted() {
        if (this.$route.query.tab == "access") {
            this.tabType = "AccessQuery";
        }

        if (this.$route.query.tab == "pv") {
            this.tabType = "PageViewList";
        }
    },
    components: {
        AccessQuery,
        PageViewList,
        SystemManage,
        DatabaseManage
    }
};
</script>

