<template>
    <el-row>
        <el-tabs v-model="listType" type="border-card">
            <el-tab-pane label="我的实验" name="mine"></el-tab-pane>
            <el-tab-pane label="放量实验" name="volume"></el-tab-pane>
            <el-tab-pane label="已结束实验" name="finish"></el-tab-pane>
            <el-tab-pane label="所有" name="all"></el-tab-pane>
            <!-- <el-tab-pane label="待审核" name="audit"></el-tab-pane>
                <el-tab-pane label="已驳回" name="refuse"></el-tab-pane>
            <el-tab-pane label="运行中" name="online"></el-tab-pane>-->

            <el-row>
                <!-- <el-radio-group
                    style="margin-bottom: 30px;"
                    v-model="listType"
                    @change="changeListType"
                >
                    <el-radio-button label="mine">我的实验</el-radio-button>
                    <el-radio-button label="volume">放量实验</el-radio-button>
                    <el-radio-button label="finish">已结束实验</el-radio-button>
                    <el-radio-button label="all">所有</el-radio-button>
                    <el-radio-button label="audit">待审核</el-radio-button>
                    <el-radio-button label="refuse">已驳回</el-radio-button>
                    <el-radio-button label="online">运行中</el-radio-button>
                </el-radio-group>-->
                <el-col :span="9">
                    <el-form ref="form">
                        <el-form-item>
                            <el-input
                                v-model="keyword"
                                placeholder="输入策略、描述或创建人，自动检索"
                                @input="searchLayerList"
                            ></el-input>
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-col :span="2" :offset="13">
                    <el-button
                        id="export"
                        type="warning"
                        icon="el-icon-upload"
                        @click="exportLayerList"
                    >导出</el-button>
                </el-col>
            </el-row>

            <ExpTable :listData="(listType == 'all' || listType == 'finish') ? listData : showDatas" />

            <el-pagination
                @current-change="getAllListData"
                :current-page.sync="page"
                layout="total, prev, pager, next"
                :total="total"
                :page-size="20"
                :hide-on-single-page="true"
                v-if="(listType == 'all' || listType == 'finish') && keyword == ''"
                align="center"
                background
                style="margin-top:20px;"
            ></el-pagination>
        </el-tabs>
    </el-row>
</template>

<script>
import ExpTable from "./ExpTable.vue";
export default {
    data() {
        return {
            listType: "mine",
            listData: [],
            page: 1,
            total: 0,
            keyword: "",
            timer: null
        };
    },
    components: {
        ExpTable
    },
    computed: {
        showDatas: function() {
            return this.listData.filter(data => {
                var reg = eval("/(?=" + this.keyword + ")/");

                return (
                    reg.test(data.origin_exp_layer.exp_layer_name) ||
                    reg.test(data.strategy_set_name) ||
                    reg.test(data.product_chinese_name) ||
                    reg.test(data.origin_exp_layer.creator) ||
                    reg.test(data.origin_exp_layer.exp_layer_id)
                );
            });
        }
    },
    watch: {
        listType() {
            this.changeListType();
        }
    },
    mounted() {
        let type = localStorage.getItem("expType");
        if (type) {
            this.listType = type;
        }
        this.getListData();
    },
    methods: {
        changeListType() {
            this.keyword = "";
            localStorage.setItem("expType", this.listType);
            this.getListData();
        },
        async getListData() {
            if (this.listType == "all" || this.listType == "finish") {
                return this.getAllListData();
            }
            let res = await this.$api.exp.getLayerListByGo(this.listType);
            this.$set(this, "listData", res.data);
        },
        async getAllListData() {
            let res = await this.$api.exp.getAllLayerList(this.page, this.listType);
            this.$set(this, "listData", res.data.list);
            this.total = res.data.total;
        },
        exportLayerList() {
            this.Export2Excel.export_table_to_excel(
                "layerList",
                this.listType + "列表"
            );
        },
        searchLayerList() {
            if (this.listType == "mine" || this.listType == "volume") {
                return false;
            }

            if (this.keyword.trim() == "") {
                this.getAllListData();
                return;
            }

            if (this.timer != null) {
                clearTimeout(this.timer);
            }

            this.timer = setTimeout(() => {
                this.doSearch();
            }, 1000);
        },
        async doSearch() {
            let res = await this.$api.exp.searchLayerListByGo(this.keyword);

            this.$set(this, "listData", res.data);
        }
    }
};
</script>
