<template>
    <el-row style="margin-top:20px;">
        <el-date-picker
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            align="left"
            size="small"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            :clearable="false"
            :unlink-panels="true"
            :time-arrow-control="true"
        ></el-date-picker>
    </el-row>
</template>
<script>
import moment from "moment";
export default {
    data() {
        return {
            timeArea: [],
            pickerOptions: {
                shortcuts: [
                    {
                        text: "今日",
                        onClick(picker) {
                            picker.$emit("pick", [
                                moment()
                                    .startOf("day")
                                    .toDate(),
                                moment().toDate()
                            ]);
                        }
                    },
                    {
                        text: "昨日",
                        onClick(picker) {
                            picker.$emit("pick", [
                                moment()
                                    .subtract("days", 1)
                                    .startOf("day")
                                    .toDate(),
                                moment()
                                    .startOf("day")
                                    .toDate()
                            ]);
                        }
                    },
                    {
                        text: "最近3日",
                        onClick(picker) {
                            picker.$emit("pick", [
                                moment()
                                    .subtract("days", 3)
                                    .startOf("day")
                                    .toDate(),
                                moment()
                                    .startOf("day")
                                    .toDate()
                            ]);
                        }
                    },
                    {
                        text: "最近一周",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 7
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近一个月",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 30
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    }
                ]
            }
        };
    },
    mounted() {
        this.$set(this, "timeArea", [
            "2019-10-16 10:30",
            moment().format("YYYY-MM-DD HH:mm")
        ]);
    },
    methods: {}
};
</script>
