<template>
    <el-card class="box-card">
        <el-form ref="form" :label-position="'left'" label-width="100px" v-model="queryConfig">
            <el-form-item label="场景">
                <el-checkbox-group v-model="queryConfig.psid" @change="toSetQueryConfig">
                    <template v-for="(item, index) in statConfig.service">
                        <el-checkbox :label="item.code + ''">{{item.name}}</el-checkbox>
                    </template>
                </el-checkbox-group>
            </el-form-item>
        </el-form>

        <el-form ref="form3" :label-position="'right'" label-width="100px">
            <el-form-item label="选择展现维度">
                <el-radio-group v-model="groupBy" @change="toSetQueryConfig">
                    <el-radio-button :label="'psid'">场景</el-radio-button>
                </el-radio-group>
            </el-form-item>
        </el-form>
    </el-card>
</template>
<style scoped>
.el-form-item {
    margin-bottom: 10px;
}
</style>

<script>
import lodash from "lodash";

export default {
    data() {
        return {
            statConfig: {
                platform: [],
                bid_type: [],
                promotion_objective: [],
                optimization_objective: [],
                creative_style_id: [],
                psid: [],
                service: []
            },
            queryConfig: {
                platform: [],
                bid_type: [],
                promotion_objective: [],
                creative_style_id: [],
                optimization_goal: [],
                psid: []
            },
            groupBy: "psid"
        };
    },
    mounted: function() {
        this.getStatConfig();
    },
    methods: {
        async getStatConfig() {
            let res = await this.$api.effect.getConfig();
            this.$set(this, "statConfig", res.data);
        },
        toSetQueryConfig() {
            this.$store.commit("selectGroupBy", this.groupBy);
            this.$store.commit("setQueryConfig", this.queryConfig);
        }
    }
};
</script>

