<template>
    <el-row>
        <el-form :inline="true" class="demo-form-inline">
            <el-form-item label="在线实验:" style="margin-left:20px">
                <el-select v-model="expLayerId" @change="loadData">
                    <template v-for="(item, index) in expLayerList">
                        <el-option
                            :label="item.exp_layer_name"
                            :value="item.exp_layer_id"
                            :key="index"
                        ></el-option>
                    </template>
                </el-select>
            </el-form-item>
            <el-form-item label="场景:" style="margin-left:60px">
                <el-select v-model="psid" @change="loadData">
                    <el-option :value="-1" label="整体"></el-option>
                    <template v-for="(item, index) in psidList">
                        <el-option :label="item.name" :value="item.code" :key="index"></el-option>
                    </template>
                </el-select>
            </el-form-item>
        </el-form>

        <el-card class="box-card">
            <div slot="header" class="clearfix" style="height:40px">
                <span style="float: right; padding: 3px 0">
                    <el-form ref="form" label-width="80px">
                        <el-form-item label="展示内容">
                            <el-radio-group v-model="dataType">
                                <el-radio value="offline" label="offline">离线数</el-radio>
                                <el-radio value="realtime" label="realtime">实时数</el-radio>
                                <el-radio value="dist" label="dist">误差数(实时数 - 离线数)</el-radio>
                                <el-radio value="percent" label="percent">误差率(误差数 / hive数)</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                </span>
            </div>

            <el-row style="width:100%; overflow:scroll;">
                <table
                    class="table table-condensed table-hover"
                    v-show="showTable"
                    style="overflow:scroll;"
                >
                    <thead>
                        <tr class="info">
                            <th style="text-align: center;vertical-align: middle;">时间</th>
                            <th style="text-align: center;vertical-align: middle">子实验</th>
                            <template v-for="(name, idx) in quotas">
                                <th
                                    style="text-align: center;vertical-align: middle;min-width: 80px;"
                                    :key="idx"
                                >{{name}}</th>
                            </template>
                        </tr>
                    </thead>
                    <tbody>
                        <template v-for="(hourData, time) in statData">
                            <template v-for="(data, idx) in hourData">
                                <tr>
                                    <template v-if="idx == 0">
                                        <td
                                            :rowspan="hourData.length"
                                            style="vertical-align: middle;text-align: center"
                                        >{{time}}</td>
                                    </template>
                                    <td>{{data.exp_title}}</td>
                                    <template v-for="(name, key) in quotas">
                                        <td
                                            @mouseover="showStatDetail(data, key, $event)"
                                            style="cursor:pointer"
                                            :class="data.cls[key]"
                                            :key="key"
                                        >
                                            {{data[dataType][key]}}
                                            <template
                                                v-if="dataType == 'percent'"
                                            >%</template>
                                        </td>
                                    </template>
                                </tr>
                            </template>
                        </template>
                    </tbody>
                </table>
            </el-row>
            <loading :state="loadState" style="padding-top:20px"></loading>
        </el-card>
        <el-alert style="margin-top:80px">说明：实时数据只保留三天数据，我们选择其中某一个实验层的昨天实时小时数据和昨天离线小时数据进行对比</el-alert>
    </el-row>
</template>

<script>
import lodash from "lodash";
import ScrollBtn from "@/components/Widget/ScrollBtn";

export default {
    components: { ScrollBtn },
    data: function() {
        return {
            tableType: "rate",
            statData: {
                exp_list: {}
            },
            quotas: {},
            loadState: "",
            noData: false,
            showTable: false,
            expLayerId: 0,
            psid: -1,
            expLayerList: [],
            psidList: [],
            day: "",
            dataType: "percent",
            errorLayer: [],
            templateId: "tableRealtimeId"
        };
    },
    mounted: function() {
        this.loadExpLayer();
        this.loadPsid();
    },
    methods: {
        async loadExpLayer() {
            let res = await this.$api.exp.getRecentExpsByGo();
            if (res.data.length == 0) {
                return;
            }
            this.expLayerList = res.data;
            let index = 0;
            if (this.$route.query.exp_layer_id > 0) {
                index = lodash.findIndex(this.expLayerList, [
                    "exp_layer_id",
                    parseInt(this.$route.query.exp_layer_id)
                ]);
                index = index >= 0 ? index : index;
                this.expLayerId = this.expLayerList[index].exp_layer_id;
            } else if (this.$route.query.error_layer_Ids) {
                this.findErrorLayer();
            } else {
                this.expLayerId = this.expLayerList[index].exp_layer_id;
            }
            this.loadData();
        },
        async loadPsid() {
            let res = await this.$api.effect.getConfig();
            this.psidList = res.data.psid;
        },
        async loadData() {
            this.statData = {};
            this.loadState = "loading";
            this.showTable = false;
            let res = await this.$api.effect.getYesterdayStatByGo(
                this.expLayerId,
                this.psid
            );
            this.loadState = "";
            if (res.errcode != 0) {
                this.loadState = "no-data";
                this.showTable = false;
                return;
            }
            this.statData = res.data.data;
            this.quotas = res.data.quotas;
            this.day = res.data.day;
            this.showTable = true;
        },
        fmtExpLayerList: function(expLayerList) {
            let errLayerIds = this.$route.query.error_layer_Ids || "";
            if (errLayerIds) {
                for (var i in expLayerList) {
                    if (
                        errLayerIds.indexOf(expLayerList[i].exp_layer_id) > -1
                    ) {
                        expLayerList[i].exp_layer_name =
                            expLayerList[i].exp_layer_name + "-(误差大)";
                    }
                }
            }
            return expLayerList;
        },
        findErrorLayer: function() {
            let errLayerIds = this.$route.query.error_layer_Ids || "";
            if (errLayerIds == "") {
                this.errorLayer = [];
                return;
            }
            let errLayerIdList = errLayerIds.split("|");
            let index = -1;
            for (var i in errLayerIdList) {
                index = lodash.findIndex(this.expLayerList, [
                    "exp_layer_id",
                    parseInt(errLayerIdList[i])
                ]);
                if (index >= 0) {
                    this.errorLayer.push(this.expLayerList[index]);
                }
            }

            if (this.errorLayer.length > 0) {
                this.expLayerId = this.errorLayer[0].exp_layer_id;
            } else {
                this.expLayerId = this.expLayerList[0].exp_layer_id;
            }
        },
        switchExp: function(layerId) {
            this.expLayerId = layerId;
            this.loadData();
        },
        showStatDetail: function(data, qouta, event) {
            $(event.currentTarget)
                .tooltip({
                    placement: "top",
                    html: true,
                    title:
                        "离线数：" +
                        data.offline[qouta] +
                        "<br>实时数：" +
                        data.realtime[qouta] +
                        "<br>误差数：" +
                        data.dist[qouta] +
                        "<br>误差率：" +
                        data.percent[qouta],
                    container: "body"
                })
                .tooltip("toggle");
        }
    }
};
</script>
<style scoped>
</style>
