<template>
    <div>
        <el-tabs
            type="border-card"
            v-loading="loading"
            element-loading-text="拼命加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
        >
            <el-tab-pane>
                <span slot="label" @click="switchTab('rate')">相对值</span>
            </el-tab-pane>
            <el-tab-pane label="配置管理">
                <span slot="label" @click="switchTab('detail')">绝对值</span>
            </el-tab-pane>
            <el-alert
                :title="'本次查询时间范围：' + statData.from + '至' + statData.to"
                type="success"
                style="margin-bottom:10px"
                :closable="false"
            ></el-alert>

            <template v-if="statData.is_over_30_days">
                <el-alert
                    title="选择时间范围超出30天"
                    type="warning"
                    description="暂时只支持实验选择范围30天的数据！！！"
                    show-icon
                    style="margin-bottom:10px"
                    :closable="false"
                ></el-alert>
            </template>
            <template v-if="statData.is_cross_version">
                <el-alert
                    title="跨实验版本提醒"
                    type="warning"
                    description="您选择的时间段已经超过当前版本的日期选择范围，所统计的数据可能已不具备参考价值，请谨慎使用，后果请自行承担"
                    show-icon
                    style="margin-bottom:10px"
                    :closable="false"
                ></el-alert>
            </template>

            <el-row style="margin-bottom:15px">
                <el-select v-model="baseExpId" placeholder="请选择基准子实验">
                    <el-option
                        v-for="(item, expId) in statData.exp_list"
                        :key="expId"
                        :label="item.exp_title"
                        :value="expId"
                    ></el-option>
                </el-select>
                <el-select
                    v-model="selectQuotas"
                    multiple
                    filterable
                    placeholder="选择展示的指标"
                    collapse-tags
                    style="width:30%"
                    @change="changeSelectQuota"
                >
                    <template v-for="(item, index) in statData.quota_list">
                        <el-option :label="item.quota_text" :value="item.quota_name" :key="index"></el-option>
                    </template>
                </el-select>
                <el-select
                    v-model="selectExps"
                    multiple
                    filterable
                    placeholder="选择展示的实验"
                    collapse-tags
                    style="width:30%"
                >
                    <template v-for="(item, index) in statData.exp_list">
                        <el-option :label="item.exp_title" :value="index" :disabled="tableType== 'rate' && index == baseExpId" :key="index"></el-option>
                    </template>
                </el-select>

                <el-dropdown style="margin-left:10px;" @command="handleCommand">
                    <span class="el-dropdown-link">
                        工具
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="downloadHourData">下载小时数据</el-dropdown-item>
                        <el-dropdown-item command="downloadDayData">下载天数据</el-dropdown-item>
                        <el-dropdown-item command="exportExcel">报表导出</el-dropdown-item>
                        <el-dropdown-item command="openUserStat">三方会审评测</el-dropdown-item>
                        <el-dropdown-item
                            command="showConsume"
                            v-if="exp_layer.is_same_type == 2"
                        >预算分桶实验消耗</el-dropdown-item>
                        <el-dropdown-item command="getDebugParam">debug参数</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
               
                <router-link :to="{path : '/lab/detail', query : {
                    exp_layer_id : $route.query.exp_layer_id
                }}" target="_blank">实验详情</router-link>
                <router-link :to="{path : '/quota/list', query : {
                    quota_type : 'quotaBook'
                }}" target="_blank">实验指标字典</router-link>
            </el-row>
            <el-row v-show="showSaveAlert">
                <p>保存所选指标，下次进来直接展示？<el-button  type="primary"
                    @click="saveSelectQuota" size="mini">保存</el-button></p>
            </el-row>

            <div class="tab-pane" style="overflow:scroll">
                <table class="table table-hover" id="tableRateId">
                    <thead>
                        <tr class="info">
                            <td style="text-align: center;">指标</td>
                            <td style="text-align: right;">实验</td>
                            <template v-for="(item, idx) in statData.header">
                                <td style="text-align:right;" :key="idx">{{item.name}}</td>
                            </template>
                        </tr>
                    </thead>
                    <tbody>
                        <template v-for="(item, index) in statData.quota_list">
                            <template v-for="(expId, idx) in showExpIds">
                                <tr
                                    :class="{'trBorderBottom' : idx === showExpIds.length-1, 'first-tr' : idx == 0}"
                                    v-if="selectQuotas.length == 0 || selectQuotas.indexOf(item.quota_name) > -1"
                                >
                                    <template v-if="idx == 0">
                                        <td :rowspan="showExpIds.length" class="firstTd">
                                            <el-popover
                                                placement="top-start"
                                                width="200"
                                                trigger="hover"
                                                :content="item.quota_comment"
                                            >
                                                <span
                                                    slot="reference"
                                                    @click="showQuotaHourData(item.quota_name, item.quota_text, '-1')"
                                                    :class="item.cls"
                                                    class="label"
                                                >{{item.quota_en_text}}</span>
                                            </el-popover>
                                            <br />
                                            <h6>{{item.quota_text}}</h6>
                                        </td>
                                    </template>

                                    <td style="text-align: right">
                                        <span
                                            v-popover:exp
                                            @mouseover="showExpTitle(statData.exp_list[expId], $event)"
                                            style="cursor:pointer"
                                            @mouseleave="hideExpTitle(statData.exp_list[expId].exp_title, $event)"
                                        >{{statData.exp_list[expId].exp_title.length > 50 ? (statData.exp_list[expId].exp_title.substring(0, 48) + '..') : statData.exp_list[expId].exp_title}}</span>
                                        <span
                                            style="color:#113285;padding-right: 2px;"
                                            v-show="exp_layer.exp_layer_type==0"
                                        >[{{statData.exp_list[expId].traffic_weight / 10}}%]</span>
                                    </td>

                                    <template v-for="dim in statData.header">
                                        <td>
                                            <p
                                                @mouseover="showTip($event, item.quota_name, expId, dim.code)"
                                                @mouseleave="hideTip($event)"
                                                style="cursor: pointer;"
                                                :class="statData[tableType][item.quota_name][expId][dim.code] !== undefined ? statData[tableType][item.quota_name][expId][dim.code].cls : ''"
                                            >{{statData[tableType][item.quota_name][expId][dim.code] !== undefined ? statData[tableType][item.quota_name][expId][dim.code].val : '-'}}</p>
                                        </td>
                                    </template>
                                </tr>
                            </template>
                        </template>
                    </tbody>
                </table>
            </div>
        </el-tabs>

        <el-dialog :visible.sync="showDialog" width="70%">
            <div slot="title" class="clearfix">
                <strong>
                    {{dialogTitle}}
                    <i class="el-icon-loading" v-if="hloading"></i>
                </strong>
                <el-select v-model="dimType" placeholder="请选择" @change="changeDimType">
                    <el-option
                        v-for="(item, index) in statData.header"
                        :label="item.name"
                        :value="item.code"
                        :key="index"
                    ></el-option>
                </el-select>
            </div>

            <drawchart :chartData="quotaHourData" chartId="hourDataChartId" />
        </el-dialog>

        <!--debug参数-->
        <el-dialog :visible.sync="showDebugDialog" width="70%">
            <div slot="title" class="clearfix">
                <strong>debug参数</strong>
            </div>
            <div style="margin-bottom:20px;" v-for="(item, index) in debugData">
                <p>
                    <strong>Key：</strong>
                    {{index}}
                </p>
                <p>
                    <strong>Param：</strong>
                    {{item.param}}
                </p>
                <p>
                    <strong>Token：</strong>
                    {{item.token}}
                </p>
                <p>
                    <strong>AdType：</strong>
                    {{item.ad_type}}
                </p>
                <el-divider></el-divider>
            </div>
        </el-dialog>

        <!--预算分桶弹框-->
        <el-dialog :visible.sync="showConsumeDialog" width="70%">
            <div slot="title" class="clearfix">
                <strong>实验对象的预算消耗曲线</strong>
            </div>

            <BudgetExpConsume :expLayerId="exp_layer.exp_layer_id" ref="budget" />
        </el-dialog>

        <div class="row text-right" style="padding-right: 20px;">
            <span style="color: grey;font-size: 12px">由D+提供算力支持(Powered by D+)</span>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import BudgetExpConsume from "./BudgetExpConsume";
import lodash from "lodash";
export default {
    props: ["statData"],
    data: function() {
        return {
            tableType: "rate",
            selectQuotas: [],
            selectExps: [],
            quotaHourData: {
                data: {},
                title: "",
                xAxis: []
            },
            hloading: false,
            showDialog: false,
            dialogTitle: "",
            dimType: 0,
            quota: "",
            baseExpId : "",

            showDebugDialog: false,
            debugData: {},

            showConsumeDialog: false,

            firstAccess: true,
            showSaveAlert: false,
        };
    },
    watch: {
        loading: function() {
            if (this.firstAccess && this.loading == false) {
                this.selectQuotas = this.statData.show_quotas;
                this.firstAccess = false;
                this.baseExpId = this.statData.base_exp_id;
            }
        },
        baseExpId : function() {
            this.$store.commit('setBaseExpId', this.baseExpId)
        }
    },
    components: {
        BudgetExpConsume
    },
    computed: {
        ...mapState({
            exp_layer: state => state.exp_effect.exp_layer,
            loading: state => state.exp_effect.load_state
        }),
        showExpIds: function() {

            if (this.tableType == "rate") {
                lodash.pull(this.selectExps, this.statData.base_exp_id);
            }
            if (this.selectExps.length == 0) {
                if (this.tableType == "rate") {
                    return this.statData.exp_ids;
                } else {
                    return lodash
                        .union(
                            [this.statData.base_exp_id],
                            this.statData.exp_ids
                        )
                        .sort();
                }
            }
            return this.selectExps;
        }
    },
    methods: {
        changeSelectQuota() {
            if (this.selectQuotas.length > 0) {
                this.showSaveAlert = true;
            }
        },
        showExpTitle: function(data, event) {
            let strategy = JSON.parse(data.exp_strategy_content);
            let allKeys = Object.keys(strategy);
            let content = "";
            for (var key in allKeys) {
                let val = strategy[allKeys[key]];
                if (val.length > 100) {
                    val = val.substring(0, 100) + "...";
                }
                content +=
                    '<p class="p-break-full"><strong>' +
                    allKeys[key] +
                    "：</strong>" +
                    val +
                    "</p>";
            }
            $(event.currentTarget)
                .popover({
                    placement: "top",
                    html: true,
                    delay: 1,
                    content: content,
                    html: true,
                    container: "body",
                    title: data.exp_title,
                    animation: true,
                    template:
                        '<div class="popover" role="tooltip"><div class="arrow"></div><h3 class="popover-title"></h3><div class="popover-content"></div></div>'
                })
                .popover("toggle");
        },
        hideExpTitle: function(data, event) {
            $(event.currentTarget).popover("destroy");
        },
        getQuotaVal(quota, exp, aggre) {
            let val =
                lodash.get(
                    this.statData.detail,
                    `${quota}.${exp}[${aggre}].val`,
                    0
                ) + "";
            return val.replace(/,/g, "");
        },
        showTip: function(event, quota, exp, aggre) {
            let index = lodash.findIndex(this.statData.quota_list, [
                "quota_name",
                quota
            ]);

            if (index < 0) {
                return;
            }
            let quotaType = lodash.get(
                this.statData.quota_list,
                `${index}.quota_type`,
                1
            );

            if (quotaType == 1) {
                return;
            }

            let totalVal = this.getQuotaVal(quota, exp, -1);
            if (totalVal == "0") {
                return;
            }

            let val = this.getQuotaVal(quota, exp, aggre);

            let percent = ((val / totalVal) * 100).toFixed(3);
            let tip = `占整体${percent}%`;

            $(event.currentTarget)
                .tooltip({
                    placement: "top",
                    html: true,
                    delay: 0,
                    title: tip,
                    container: "body"
                })
                .tooltip("show");
        },
        hideTip: function(event) {
            $(event.currentTarget).tooltip("hide");
        },
        switchTab(t) {
            this.tableType = t;
        },
        async getQuotaHourData(quotaName, dimType) {
            //指标的变化值
            var queryParams = {
                exp_layer_id: this.$store.state.exp_effect.exp_layer
                    .exp_layer_id,
                begin: this.$store.state.exp_effect.time_area[0],
                end: this.$store.state.exp_effect.time_area[1],
                target: quotaName,
                stat_type_code: dimType,
                extra_query: this.$store.state.exp_effect.query,
                group_by: this.$store.state.exp_effect.group_by
            };
            this.quotaHourData = {
                data: {},
                title: "",
                xAxis: []
            };
            this.hloading = true;
            let res = await this.$api.effect.getQuotaChartData(queryParams);
            this.hloading = false;
            if (res.errcode == 0 && res.data.xAxis.length > 0) {
                this.quotaHourData = res.data;
            } else {
                this.quotaHourData = {
                    data: {},
                    title: "",
                    xAxis: []
                };
            }
        },
        showQuotaHourData(quotaName, quotaText, dimType) {
            this.showDialog = true;
            this.dialogTitle = quotaText;
            this.dimType = dimType;
            this.quota = quotaName;
            this.getQuotaHourData(quotaName, dimType);
        },
        changeDimType() {
            this.getQuotaHourData(this.quota, this.dimType);
        },
        downloadData(aggre) {
            let extraQuery = JSON.stringify(this.$store.state.exp_effect.query);
            window.open(
                "//faraday.biz.weibo.com/go/stat_d/download_hour_data?exp_layer_id=" +
                    this.$store.state.exp_effect.exp_layer.exp_layer_id +
                    "&begin=" +
                    this.$store.state.exp_effect.time_area[0] +
                    "&end=" +
                    this.$store.state.exp_effect.time_area[1] +
                    "&extra_query=" +
                    extraQuery +
                    "&aggre=" +
                    aggre
            );
        },
        handleCommand(command) {
            switch (command) {
                case "downloadHourData":
                    this.downloadData("hour");
                    break;
                case "downloadDayData":
                    this.downloadData("day");
                    break;
                case "exportExcel":
                    this.exportExcel();
                    break;
                case "getDebugParam":
                    this.getDebugParam();
                    break;
                case "openUserStat":
                    this.openUserStat();
                    break;
                case "showConsume":
                    this.showConsume();
                    break;
            }
        },
        exportExcel: function() {
            let excelName =
                this.tableType == "rate"
                    ? this.exp_layer.exp_layer_name + "相对值"
                    : this.exp_layer.exp_layer_name + "绝对值";
            this.Export2Excel.export_table_to_excel("tableRateId", excelName);
        },
        async getDebugParam() {
            var sendObj = {
                exp_layer_id: this.$store.state.exp_effect.exp_layer
                    .exp_layer_id,
                begin: this.$store.state.exp_effect.time_area[0],
                end: this.$store.state.exp_effect.time_area[1],
                extra_query: this.$store.state.exp_effect.query,
                group_by: this.$store.state.exp_effect.group_by
            };
            let res = await this.$api.effect.getDebugParamByGo(sendObj);
            this.debugData = res.data;
            this.showDebugDialog = true;
        },
        async openUserStat() {
            let res = await this.$api.exp.getLinkTask(
                this.exp_layer.exp_layer_id,
                0
            );

            if (res.errcode == 0) {
                window.open(
                    "http://st.tongji.weibo.com/custom/detail_show?task_id=" +
                        res.data.task_id
                );
            } else {
                this.$message("暂无该实验的三方会审页面");
                // window.open(
                //     "http://st.tongji.weibo.com/index.php/custom/index"
                // );
            }
        },
        showConsume() {
            this.showConsumeDialog = true;
            this.$refs.budget.getBudgetExpLayer();
        },
        async saveSelectQuota() {
            let params = {
                exp_layer_id: this.exp_layer.exp_layer_id + '',
                base_exp_id: this.statData.base_exp_id,
                select_exp: this.selectExps.join(','),
                show_quota: this.selectQuotas.join(',')
            };
            try {
                let res = await this.$api.effect.saveCustomization(params);
                if (res.errcode == 0) {
                    this.$message("保存成功");
                } else {
                    this.$message("保存失败，请重试");
                }
                this.showSaveAlert = false;
            } catch (err) {
                console.log(err);
                this.$message("保存失败，请稍后重试");
            }
        }
    }
};
</script>
<style scoped>
.trBorderBottom {
    border-bottom: 1px solid gray;
}
.first-tr {
    border-spacing: 100px;
}
tbody > tr > td,
thead > tr > td {
    border-top: 0px;
    padding: 2px !important;
    text-align: right;
    vertical-align: middle;
    font-family: sans-serif;
    word-break: keep-all;
    white-space: nowrap;
}
.firstTd {
    border-right: 1px solid rgb(217, 237, 247);
    border-left: 1px solid rgb(217, 237, 247);
    border-bottom: 1px solid gray;
    text-align: center;
    vertical-align: middle;
}
</style>
