<template>
    <el-row>
        <el-alert type="warning" :closable="false" style="margin-bottom:10px;">
            <strong style="font-size:18px;">非同质实验对象只能在一个子实验里，不能被其他子实验共用</strong>
        </el-alert>
        <el-col>
            <el-tabs v-model="editableTabsValue" type="card" editable @edit="handleTabsEdit">
                <el-tab-pane
                    :key="item.tab_name"
                    v-for="(item, index) in expList"
                    :label="item.exp_title"
                    :closable="false"
                    :name="item.tab_name"
                >
                    <el-row :gutter="20">
                        <el-col :span="20">
                            <el-form :label-position="'top'">
                                <el-form-item label="子实验名称：">
                                    <el-input
                                        placeholder="请输入实验名称（不超过20个字）"
                                        v-model="item.exp_title"
                                        :disabled="item.exp_id > 0"
                                    ></el-input>
                                </el-form-item>

                                <el-form-item label="描述：">
                                    <el-input
                                        type="textarea"
                                        rows="3"
                                        placeholder="实验描述"
                                        v-model="item.exp_comment"
                                    ></el-input>
                                </el-form-item>
                                <el-form-item label="实验对象名单:">
                                    <el-input
                                        type="textarea"
                                        rows="3"
                                        :placeholder="tip"
                                        v-model="item.white_list_uid"
                                        :disabled="item.exp_id > 0"
                                    ></el-input>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </el-row>

                    <el-form :label-position="'top'">
                        <template v-for="(val, key) in strategyTypeMap">
                            <el-form-item :label="key + '、' +val">
                                <el-input
                                    type="textarea"
                                    rows="3"
                                    v-model="item['s@'+key]"
                                    :disabled="item.exp_id > 0"
                                ></el-input>
                            </el-form-item>
                        </template>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
        </el-col>
    </el-row>
</template>
<script>
import lodash from "lodash";
import allocConf from "@/config/AllocConditionConf.js";
export default {
    props: ["allocType"],
    data() {
        return {
            editableTabsValue: "0",
            tabIndex: 0,
            expList: [],
            strategyList: [],
            strategyMap: {},
            defaultStrategyMap: {},
            strategyTypeMap: {},
            tip: ""
        };
    },
    watch: {
        expList: {
            handler: function() {
                this.$store.commit("setExpList", this.getCommitExpList());
            },
            deep: true
        }
    },
    mounted() {
        if (this.$store.state.create_exp.strategy_set_id < 1) {
            this.$emit("preStep");
            return;
        }
        if (this.allocType != undefined) {
            this.tip =
                "请输入" +
                allocConf.CustObjectTypeMap[this.allocType + ""] +
                "ID";
        } else if (
            this.$store.state.create_exp.exp_base_info.alloc_type_code !=
            undefined
        ) {
            this.tip =
                "请输入" +
                allocConf.CustObjectTypeMap[
                    this.$store.state.create_exp.exp_base_info.alloc_type_code +
                        ""
                ] +
                "ID";
        }

        this.getStrategys();
    },
    methods: {
        async getStrategys() {
            let res = await this.$api.strategy.getStrategyByGo(
                this.$store.state.create_exp.strategy_set_id
            );
            let strategys = [];
            res.data.strategy_list.forEach(item => {
                strategys.push(
                    this.$lodash.merge(item, JSON.parse(item.verification))
                );
            });
            this.strategyList = strategys;
            this.genStrategyMap();
            this.getOriginExpList();
        },
        getOriginExpList() {
            if (this.$store.state.create_exp.exp_list.length > 0) {
                this.expList = this.getUnCommitExpList(
                    this.$store.state.create_exp.exp_list
                );
                this.tabIndex = this.expList.length - 1;
                this.editableTabsValue = this.expList[0].tab_name;
            } else {
                this.addExp();
            }
        },
        getUnCommitExpList(list) {
            let retData = [];
            list.forEach((exp, index) => {
                let tmpExp = lodash.pick(exp, [
                    "exp_title",
                    "exp_type_code",
                    "traffic_weight",
                    "exp_comment",
                    "white_list_uid",
                    "exp_id",
                    "exp_layer_id",
                    "tab_name"
                ]);

                Object.keys(exp.exp_strategy_content).forEach(val => {
                    tmpExp["s@" + val] = exp.exp_strategy_content[val];
                });
                retData.push(tmpExp);
            });
            return retData;
        },
        genStrategyMap() {
            for (var i in this.strategyList) {
                this.defaultStrategyMap[
                    "s@" + this.strategyList[i].strategy_name
                ] = this.strategyList[i].strategy_defult_value;

                if (this.strategyList[i].type == "int") {
                    this.strategyTypeMap[
                        this.strategyList[i].strategy_name
                    ] = `类型:int，范围:[${this.strategyList[i].min}, ${this.strategyList[i].max}]`;
                } else if (this.strategyList[i].type == "string") {
                    this.strategyTypeMap[
                        this.strategyList[i].strategy_name
                    ] = `类型:string，最大长度:${this.strategyList[i].max_length}`;
                } else if (this.strategyList[i].type == "bool") {
                    this.strategyTypeMap[
                        this.strategyList[i].strategy_name
                    ] = `类型:bool`;
                }
            }
        },

        handleTabsEdit(targetName, action) {
            if (action === "add") {
                this.addExp();
            }
            if (action === "remove") {
                this.removeExp(targetName);
            }
        },
        addExp() {
            let newTabName = ++this.tabIndex + "";
            this.expList.push(
                lodash.merge(
                    {
                        exp_title: "实验" + newTabName,
                        exp_type_code: 1,
                        traffic_weight: 0,
                        exp_comment: "",
                        white_list_uid: "",
                        tab_name: newTabName
                        //exp_id : 0,
                    },
                    this.defaultStrategyMap
                )
            );
            this.editableTabsValue = newTabName;
        },
        removeExp(targetName) {
            let tabs = this.expList;
            let activeName = this.editableTabsValue;

            if (this.expList.length == 1) {
                this.$message({
                    type: "warning",
                    message: "请至少保留一个子实验"
                });
                return false;
            }

            if (activeName === targetName) {
                tabs.forEach((tab, index) => {
                    if (tab.tab_name === targetName) {
                        let nextTab = tabs[index + 1] || tabs[index - 1];
                        if (nextTab) {
                            activeName = nextTab.tab_name;
                        }
                    }
                });
            }

            this.editableTabsValue = activeName;
            this.expList = tabs.filter(tab => tab.tab_name !== targetName);
        },
        checkInputData() {
            let checkRes = [];
            this.expList.forEach((exp, index) => {
                let res = this.checkExpBaseInfo(exp, index);
                if (res != "") {
                    checkRes.push(res);
                }

                this.strategyList.forEach((strategy, index) => {
                    let tmpMsg = this.checkStrategy(
                        strategy,
                        exp["s@" + strategy.strategy_name]
                    );
                    if (tmpMsg != "") {
                        checkRes.push(
                            `子实验<strong>${exp.exp_title}</strong>` + tmpMsg
                        );
                    }
                });
            });
            //this.$store.commit("setExpList", this.getCommitExpList());
            return checkRes;
        },
        getCommitExpList() {
            let retData = [];
            this.expList.forEach((exp, index) => {
                let tmpExp = lodash.pick(exp, [
                    "exp_id",
                    "exp_layer_id",
                    "exp_title",
                    "exp_type_code",
                    "traffic_weight",
                    "exp_comment",
                    "white_list_uid",
                    "tab_name"
                ]);
                tmpExp.exp_strategy_content = {};
                this.strategyList.forEach((strategy, index) => {
                    tmpExp.exp_strategy_content[strategy.strategy_name] =
                        exp["s@" + strategy.strategy_name];
                });
                retData.push(tmpExp);
            });
            return retData;
        },
        checkExpBaseInfo(exp, idx) {
            if (exp.exp_title == "") {
                return "第" + (idx + 1) + "个子实验名称不能为空";
            }
            let title = exp.exp_title;
            if (exp.exp_title.length > 20) {
                return `<strong>${title}</strong>名称长度不能超过20`;
            }
            if (exp.exp_comment == "") {
                return `<strong>${title}</strong>描述不能为空`;
            }

            if (exp.white_list_uid == "") {
                return `请填写<strong>${title}</strong>实验对象`;
            }

            return "";
        },
        checkStrategy(strategy, inputVal) {
            if (
                strategy.type == "int" &&
                !this.checkInt(inputVal, strategy.min, strategy.max)
            ) {
                return `策略${strategy.strategy_name}类型为int，范围为${strategy.min} ~ ${strategy.max}`;
            }

            if (
                strategy.type == "string" &&
                !this.checkString(inputVal, strategy.max_length)
            ) {
                return `策略${strategy.strategy_name}类型为string，最大长度为${strategy.max_length}`;
            }

            if (strategy.type == "bool" && !this.checkBool(inputVal)) {
                return `策略${strategy.strategy_name}类型为bool，只能为true或者false`;
            }

            return "";
        },
        checkString(str, maxLength) {
            return str.length <= maxLength;
        },
        checkBool(boolStr) {
            return boolStr == "true" || boolStr == "false";
        },
        checkInt(str, min, max) {
            let strInt = parseInt(str);
            let maxInt = parseInt(max);
            let minInt = parseInt(min);

            return strInt >= minInt && strInt <= maxInt;
        }
    }
};
</script>
