<template>
    <el-row>
        <el-row style="margin-bottom:10px;">
            <el-col :span="10">
                <el-input placeholder="输入策略、描述或创建人，自动检索" v-model="keyword">
                    <el-button
                        slot="append"
                        @click="keyword = ''"
                        icon="el-icon-close"
                        v-if="keyword != ''"
                    ></el-button>
                </el-input>
            </el-col>
            <el-col :offset="12" :span="2">
                <el-button id="export" type="warning" icon="el-icon-download" @click="doit">导出</el-button>
            </el-col>
        </el-row>
        <StrategyListTable :strategySetList="showDatas" strategySetTabId="myStrategy"></StrategyListTable>
    </el-row>
</template>

<script>
import StrategyListTable from "./StrategyListTable.vue";

export default {
    name: "StrategyMyList",
    data() {
        return {
            datas: [],
            strategySetTabId: "StrategySetMyList",
            keyword: ""
        };
    },
    computed: {
        showDatas: function() {
            var result = this.datas.filter(function(data) {
                var reg = eval("/(?=" + this.keyword + ")/");

                let retData =
                    reg.test(data.origin_strategy_set.strategy_set_name) ||
                    reg.test(data.origin_strategy_set.strategy_set_comment) ||
                    reg.test(data.origin_strategy_set.creator) ||
                    reg.test(data.origin_strategy_set.strategy_name_list) ||
                    reg.test(data.origin_strategy_set.strategy_set_id);

                return retData;
            }, this);

            return result;
        }
    },
    components: { StrategyListTable },
    mounted() {
        this.getList();
    },
    methods: {
        async getList() {
            let res = {};
            res = await this.$api.strategy.getMyListByGo();

            if (res.errcode != 0) {
                this.$set(this, "datas", []);
                return;
            }
            if (res.data != null) {
                this.$set(this, "datas", res.data);
            }
        },
        doit() {
            this.Export2Excel.export_table_to_excel(
                "myStrategy",
                "我的策略"
            );
        }
    }
};
</script>

