<template>
    <ul class="pagination" v-if="total > 1">
        <template v-if="page == 1">
            <li class="disabled"><span>&laquo;</span></li>
        </template>
        <template v-else>
            <li><a href="javascript:;" rel="prev" @click="switchPage(page - 1)">&laquo;</a></li>
        </template>

        
       <template v-for="item in elementList">

            <template v-if="item == '···'">
                <li class="disabled"><span>···</span></li>
            </template>
            <template v-else-if="item == page ">
                <li class="active"><span>{{ page }}</span></li>
            </template>
            <template v-else>
                <li><a href="javascript:;" @click="switchPage(item)">{{ item }}</a></li>
            </template>
        </template>

        <template v-if="page < total">
            <li><a href="javascript:;" rel="next" @click="switchPage(page + 1)">&raquo;</a></li>
        </template>
        <template v-else>
            <li class="disabled"><span>&raquo;</span></li>
        </template>
    </ul>

</template>

<script>


export default {
	props : ['page', 'total'],
    data() {
        return {
            elementList : []
        }
    },
    watch : {
        page : function() {
            this.genElementList();
        },
        total : function() {
            this.genElementList();
        }
    },
    mounted(){
        this.genElementList();
    },
    methods : {
        switchPage : function(page) {
            this.$emit('swicthPage', page);
        },
        genElementList : function() {

            this.elementList = [];

            /**
             *CASE 1
             **/
            if(this.total <= 10) {
                for (var i = 1; i <= this.total; i++) {
                    this.elementList.push(i);
                }
                return;
            }

            /**
             *CASE 2 页面偏向左边
             **/

            if(this.page -1 < 6 ) {
                for (var i = 1; i <= 8; i++) {
                    this.elementList.push(i);
                }

                this.elementList.push('···');
                this.elementList.push(this.total-1);
                this.elementList.push(this.total);
                return;
            }

            /**
             *CASE 3 页面偏向右边
             **/

            if(this.total -this.page < 6) {
                for (var i = 0; i <= 7; i++) {
                    this.elementList.unshift(this.total - i);
                }

                this.elementList.unshift('···');
                this.elementList.unshift(2);
                this.elementList.unshift(1);
                return;
            }


            /**
             *CASE 4 页面中间
             **/

            if(this.page -1 >= 6 && this.total -this.page >= 6) {
                
                this.elementList.push(1);
                this.elementList.push(2);
                this.elementList.push('···');

                for(var i = -3; i <= 3; i++) {
                    this.elementList.push(this.page + i);
                }
                
                this.elementList.push('···');
                this.elementList.push(this.total-1);
                this.elementList.push(this.total);
                return;
            }


        }
    }
}
</script>
