<template>
    <el-row>
       
       <el-col :offset="1" :span="22">
            <Timeline :data="data">
                <template slot="detail" slot-scope="detailData">
                    <ul
                        class="detail"
                        v-show="detailData.record.record.action == 'create' || (detailData.record.record.action == 'edit' && detailData.record.edit_log == undefined) || (detailData.record.record.action == 'volume_all' && detailData.record.edit_log == undefined)"
                    >
                        <li>
                            <span class="key">名 称</span>
                            <span class="val">{{detailData.record.strategy_set.strategy_set_name}}</span>
                        </li>
                        <li>
                            <span class="key">描 述</span>
                            <span
                                class="val"
                            >{{detailData.record.strategy_set.strategy_set_comment}}</span>
                        </li>
                        <li>
                            <span class="key">创建人</span>
                            <span class="val">{{detailData.record.strategy_set.creator}}</span>
                        </li>
                        <li>
                            <span class="key">策略配置</span>
                            <span class="val"></span>
                        </li>
                        <el-table
                            size="mini"
                            :data="Object.keys(detailData.record.strategy_list).map(k =>detailData.record.strategy_list[k])"
                            style="width: 100%"
                        >
                            <el-table-column prop="strategy_name" label="策略名" width="180"></el-table-column>
                            <el-table-column prop="strategy_defult_value" label="策略值"></el-table-column>
                        </el-table>
                    </ul>
                    <ul
                        class="detail"
                        v-if="(detailData.record.record.action == 'edit' || detailData.record.record.action == 'volume_all') && detailData.record.edit_log != undefined"
                    >
                        <el-table
                            size="mini"
                            :data="Object.keys(detailData.record.edit_log.update).map(k =>detailData.record.edit_log.update[k])"
                            style="width: 100%"
                        >
                            <el-table-column label="策略名" width="180">
                                <template slot-scope="scope">{{scope.row.strategy_name}}(修改)</template>
                            </el-table-column>
                            <el-table-column label="编辑前策略值">
                                <template slot-scope="scope">
                                    <span
                                        :class="{ 'red-font' : detailData.record.old_strategy_list[scope.row.strategy_id].strategy_defult_value != scope.row.strategy_defult_value }"
                                    >
                                        <popover
                                            :showStr="detailData.record.old_strategy_list[scope.row.strategy_id].strategy_defult_value"
                                        ></popover>
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column label="编辑后策略值">
                                <template slot-scope="scope">
                                    <span
                                        :class="{ 'red-font' : detailData.record.old_strategy_list[scope.row.strategy_id].strategy_defult_value != scope.row.strategy_defult_value }"
                                    >
                                        <popover
                                            :showStr="scope.row.strategy_defult_value"
                                        ></popover>
                                    </span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-table
                            size="mini"
                            v-if="Object.keys(detailData.record.edit_log.add).length>0"
                            :show-header="false"
                            :data="Object.keys(detailData.record.edit_log.add).map(k =>detailData.record.edit_log.add[k])"
                            style="width: 100%"
                        >
                            <el-table-column width="180">
                                <template slot-scope="scope">
                                    {{scope.row.strategy_name}}
                                    <b class="green-font">(新增)</b>
                                </template>
                            </el-table-column>
                            <el-table-column></el-table-column>
                            <el-table-column>
                                <template slot-scope="scope">
                                    <span class="red-font">
                                        <popover
                                            :showStr="scope.row.strategy_defult_value"
                                            :length="20"
                                        ></popover>
                                    </span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-table
                            size="mini"
                            v-if="Object.keys(detailData.record.edit_log.delete).length>0"
                            :show-header="false"
                            :data="Object.keys(detailData.record.edit_log.delete).map(k =>detailData.record.edit_log.delete[k])"
                            style="width: 100%"
                        >
                            <el-table-column width="180">
                                <template slot-scope="scope">
                                    <s>
                                        {{scope.row.strategy_name}}
                                        <b class="red-font">(删除)</b>
                                    </s>
                                </template>
                            </el-table-column>
                            <el-table-column>
                                <template slot-scope="scope">
                                    <s>
                                        <popover
                                            :showStr="scope.row.strategy_defult_value"
                                            :length="20"
                                        ></popover>
                                    </s>
                                </template>
                            </el-table-column>
                            <el-table-column></el-table-column>
                        </el-table>
                    </ul>
                </template>
            </Timeline>
        </el-col>
    </el-row>
</template>

<script>
import moment from "moment";
import Timeline from "../Tool/Timeline";
import lodash from "lodash";

export default {
    props : ['strategySetId'],
    data() {
        return {
            data: [],
        };
    },
    components: {
        Timeline: Timeline
    },
     watch : {
        strategySetId() {
            this.getStrategyRecord()
        }
    },
    mounted() {
        this.getStrategyRecord()
    },
    methods: {
        async getStrategyRecord() {
            let res = await this.$api.strategy.getOperateListByGo(
                this.strategySetId
            );
            if (res.data == null) {
                return;
            }
            res.data.sort((obj1, obj2) => {
                return obj1.record.id > obj2.record.id ? -1 : 1;
            });

            if (res.errcode != 0) {
                return;
            }
            this.data = res.data;
        }
    }
};
</script>

