<template>
    <el-row>
        <el-col :offset="2" :span="20">
            <el-form :label-position="'right'" label-width="180px" :model="layerInfo">
                <el-form-item
                    label="所属产品线(实验区)："
                >{{formate.product_chinese_name}} | {{formate.exp_zone_chinese_name}}</el-form-item>
                <el-form-item label="实验策略：">{{strategySet.strategy_set_name}}</el-form-item>
                <el-form-item label="划分实验域流量占比：" v-if="showDomainInput">
                    <el-slider
                        v-model="formate.traffic_weight"
                        :min="0"
                        :max="restTrafficWeight"
                        :step="10"
                    ></el-slider>
                    <p>
                        本实验使用流量：
                        <el-tag>{{formate.traffic_weight}}</el-tag>，其他域已用流量：
                        <el-tag type="warning">{{1000 - restTrafficWeight}}</el-tag>，剩余：
                        <el-tag type="success">{{restTrafficWeight - formate.traffic_weight}}</el-tag>
                    </p>
                </el-form-item>
                <el-form-item label="实验层名称：">{{layerInfo.exp_layer_name}}</el-form-item>
                <el-form-item label="实验描述：">
                    <el-input
                        type="textarea"
                        v-model="layerInfo.exp_layer_comment"
                        rows="6"
                        placeholder="【请填写实验的预期目标，实验的设计方案，实验的放量计划，实验关心的核心指标等】"
                    ></el-input>
                </el-form-item>
                <el-form-item label="流量A/B分桶类型：">{{formate.alloc_type_name}}</el-form-item>
                <el-form-item label="流量团圈定：">
                    <template
                        v-if="formate.is_online_ever && layerInfo.alloc_condition_enable == 0"
                    >不启用</template>
                    <template v-if="!formate.is_online_ever">
                        <el-radio v-model="layerInfo.alloc_condition_enable" :label="0">不启用</el-radio>
                        <el-radio v-model="layerInfo.alloc_condition_enable" :label="1">启用</el-radio>
                    </template>
                </el-form-item>
            </el-form>

            <template v-if="!formate.is_online_ever && layerInfo.alloc_condition_enable == 1">
                <el-card class="box-card">
                    <FlowAlloc />
                </el-card>
            </template>

            <template v-if="formate.is_online_ever && layerInfo.alloc_condition_enable == 1">
                <el-card class="box-card">
                    <template v-for="item in layerInfo.alloc_condition_display">
                        <p>
                            <strong>{{item.name}} :</strong>
                            {{item.data}}
                        </p>
                    </template>
                </el-card>
            </template>
        </el-col>

        <el-col :offset="2" :span="20" v-if="showExpList">
            <SetExp ref="setexp" />
        </el-col>

        <el-dialog title="选择审核人" :visible.sync="showDialogAuditor">
            <AddAuditor :expZoneCode="layerInfo.exp_zone_code" v-model="assignAuditor" />
            <span slot="footer" class="dialog-footer">
                <el-button @click="showDialogAuditor = false">取 消</el-button>
                <el-button type="primary" @click="hideFormAuditor">确 定</el-button>
            </span>
        </el-dialog>

        <el-col style="margin-top: 20px" align="center">
            <el-button
                type="primary"
                @click="toEditExp"
                v-show="layerInfo.exp_layer_status_code == 2"
                :disabled="isDisableSubmit"
            >{{isDisableSubmit ? '正在提交中，请等待...' : '修改实验'}}</el-button>
            <el-button
                type="primary"
                @click="toEditExp"
                v-show="layerInfo.exp_layer_status_code == 3"
                :disabled="isDisableSubmit"
            >{{isDisableSubmit ? '正在提交中，请等待...' : '放量实验'}}</el-button>
        </el-col>

        <el-dialog
            :title="'[实验放量超20%审核]' + layerInfo.exp_layer_name"
            :visible.sync="dialogFormVisible"
        >
            <p class="red-font h4">重要提醒：效果实验放量超20%需要leader邮件组一致审核通过！！！请确认你已经发送了邮件</p>
            <el-form label-position="top" label-width="150px">
                <el-form-item label="发送时间">
                    <el-date-picker
                        v-model="mailInfo.time"
                        type="datetime"
                        placeholder="选择日期"
                        format="yyyy-MM-dd HH:mm"
                        value-format="yyyy-MM-dd HH:mm"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="邮件内容">
                    <el-input type="textarea" v-model="mailInfo.content" :rows="6"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="comfirmFillMail">确 定</el-button>
            </div>
        </el-dialog>
    </el-row>
</template>

<script>
import FlowAlloc from "./FlowAlloc.vue";
import lodash from "lodash";
import VolumeAuditFlow from "./VolumeAuditFlow.vue";
import AddAuditor from "./AddAuditor";
import SetExp from "./SetExp";

export default {
    data() {
        return {
            expLayerId: 0,
            layerInfo: {
                exp_zone_code: ""
            },
            formate: {},
            strategySet: {},
            restTrafficWeight: 1000,
            showExpList: false,
            showCondition: false,
            isDisableSubmit: false,

            mailInfo: {},
            mailSubject: "",

            showDialogAuditor: false,
            assignAuditor: [],

            dialogFormVisible: false
        };
    },
    computed: {
        showDomainInput: function() {
            return this.layerInfo.exp_zone_code == 78301;
        }
    },
    components: {
        FlowAlloc,
        SetExp,
        VolumeAuditFlow,
        AddAuditor
    },
    mounted() {
        if (this.$route.query.exp_layer_id > 0) {
            this.expLayerId = this.$route.query.exp_layer_id;
        } else {
            this.$router.push({
                path: "/"
            });
        }
        this.getLayerInfo();
         this.$store.dispatch("clearExpInfo");
        this.$store.dispatch("clearDelExps");
        //$('#auditFlow').modal('show');
    },
    methods: {
        async getLayerInfo() {
            let res = await this.$api.exp.getExpDetailByGo(this.expLayerId);
            //基本信息
            this.layerInfo = res.data.origin_exp_layer;
            this.formate = res.data.format;
            this.strategySet = res.data.strategy_set;
            if (
                this.layerInfo.exp_layer_status_code != 2 &&
                this.layerInfo.exp_layer_status_code != 3
            ) {
                this.$message({
                    message: "该实验暂时不能编辑",
                    type: "error"
                });
                this.$router.push({
                    path: "/lab/detail",
                    query: { exp_layer_id: this.expLayerId }
                });
                return;
            }
            this.$store.commit(
                "setStrategySetId",
                this.layerInfo.strategy_set_id
            );
            let list = res.data.exp_list.filter(item => {
                return item.exp_type_code > 0;
            });
            list.forEach((item, idx) => {
                list[idx].tab_name = idx + "";
            });
            this.$store.commit("setExpList", list);
            this.showExpList = true;
            this.setAlloCondtion();
            this.showCondition = true;

            this.getRestDomain();
        },
        async getRestDomain() {
            if (!this.showDomainInput) {
                return;
            }
            let res = await this.$api.strategy.getStrategyExpStat(
                this.layerInfo.strategy_set_id
            );
            this.restTrafficWeight = 1000 - (res.data.use_traffic - this.formate.traffic_weight);
        },
        setAlloCondtion() {
            let allocVal = JSON.parse(this.layerInfo.alloc_condition_value);
            let retData = this.$tool.unZipAlloCondtion(allocVal);
            if (Object.keys(retData).length < 1) {
                return;
            }
            retData = this.$lodash.merge(
                this.$store.state.create_exp.flow_alloc_conf,
                retData
            );
            this.layerInfo.alloc_condition_display = this.$tool.getAllocConfDisplayData(this.layerInfo.alloc_condition_value)
            this.$store.commit("setFlowAlloConf", retData);
        },
        getUpdateParam: function() {
            let updateData = {
                exp_layer_comment: this.layerInfo.exp_layer_comment,
                traffic_weight: this.formate.traffic_weight,
                data: this.$store.state.create_exp.exp_list,
                assign_auditor: this.assignAuditor,
                flow_type: 0
            };

            if (this.$store.state.create_exp.del_exp_list.length > 0) {
                this.$store.state.create_exp.del_exp_list.forEach(item => {
                    updateData.data.push(item);
                });
            }

            if (this.layerInfo.is_online_ever) {
                return updateData;
            }

            //未上线
            updateData.alloc_condition_value = {};
            updateData.alloc_condition_code = [];
            updateData.alloc_condition_enable = this.layerInfo.alloc_condition_enable;
            if (updateData.alloc_condition_enable == 1) {
                let conf = this.$tool.zipAllocCondition(
                    this.$store.state.create_exp.flow_alloc_conf
                );
                updateData.alloc_condition_value = conf;
                updateData.alloc_condition_code = Object.keys(conf);
            }

            return updateData;
        },
        hideFormAuditor() {
            if (this.assignAuditor.length < 1) {
                this.$message("请指定审核人");
            } else {
                this.showDialogAuditor = false;
                this.toEditExp();
            }
        },
        async toEditExp() {
            if (this.assignAuditor.length < 1) {
                this.showDialogAuditor = true;
                return;
            }

            var updataParam = this.getUpdateParam();
            updataParam.exp_layer_id = parseInt(this.$route.query.exp_layer_id);
            updataParam.mail_info = this.mailInfo;
            // console.log(updataParam);
            // return;
            let successMsg =
                this.layerInfo.exp_layer_status_code == 3
                    ? "放量成功"
                    : "修改成功";

            if (this.isDisableSubmit) {
                return false;
            }

            this.isDisableSubmit = true;

            try {
                let res = await this.$api.exp.updateByGo(updataParam);
                if (res.errcode == 0) {
                    this.$message({
                        message: res.errmsg,
                        duration: 1000,
                        onClose: () => {
                            this.$router.push({
                                path: "/lab/detail",
                                query: {
                                    exp_layer_id: this.$route.query.exp_layer_id
                                }
                            });
                        }
                    });
                } else if (res.errcode == 3) {
                    this.isDisableSubmit = false;
                    this.showMailAlert();
                } else {
                    this.isDisableSubmit = false;
                    this.$message(res.errmsg);
                    this.mailInfo = {};
                }
            } catch (err) {
                this.$message(err.message || "请刷新页面重试");
            }
        },
        showMailAlert: function() {
            this.dialogFormVisible = true;
        },
        comfirmFillMail: function(data) {
            this.dialogFormVisible = false;
            this.toEditExp();
        }
    }
};
</script>
