<template>
<div>
    <div v-if="domainLayer.length > 0">
        <SingleDomainLayerTab :domainLayer="domainLayer" @domainClick="domainClick" :expLayerId="expLayerId"/>
    </div>
    <div v-else>
        <p  style="color: grey">该策略上暂无实验</p>
    </div>

</div>
</template>

<script>

import lodash from 'lodash';
import SingleDomainLayerTab from '@/Faraday/Domain/SingleDomainLayerTab'
export default {
    props : ['strategySetId', 'expLayerId'],
    data(){
        return {
            domainLayer : [],
        }
    },
    watch : {
        strategySetId : function() {
            this.getDomainLayer();
        }
    },
    mounted() {
        this.getDomainLayer();
    },
    components : {
        SingleDomainLayerTab
    },
    methods:{
        async getDomainLayer() {

            if(parseInt(this.strategySetId) == 0) {
                return false;
            }

            this.domainLayer = [];
            let res = await this.$api.strategy.getStrategyDomainList(this.strategySetId);

            let sum = 0
            res.data.forEach(item => {
                sum += item.domain.traffic_weight
            });

            let restDomain = (1000 - sum) / 10
            res.data.push({
                domain : {
                    domain_type : 0,
                    traffic_weight : 1000 - sum,
                },
                exp_layer : {
                    exp_layer_id : 0,
                    creator : "system"
                },
                bg_class : "bg-info",
                progress_bar_wd : restDomain + "%",
                progress : restDomain,
            })
            this.domainLayer = res.data;

        },
        domainClick : function(domain) {
            if(domain.exp_layer.exp_layer_id == this.expLayerId) {
                return false;
            }
            if(this.$route.path == '/monitor/exp_effect') {
                this.$emit('domainClick', domain);
            } else {
                this.$router.push({
                    path : '/lab/detail',
                    query : {
                        exp_layer_id : domain.exp_layer.exp_layer_id
                    }
                });
                this.$emit('domainClick', domain);
            }

        }

    }
}
</script>
