<template>
    <el-col>
        <el-form :label-position="'top'" label-width="150px" v-model="layerInfo">
            <el-form-item label="实验名称：">
                <el-input v-model="layerInfo.exp_layer_name" placeholder="请输入实验名称（不超过20个字）" @change="commitLayerInfo"></el-input>
            </el-form-item>

            <el-form-item label="实验描述：">
                <el-input
                    type="textarea"
                    v-model="layerInfo.exp_layer_comment"
                    rows="6"
                    placeholder="【请填写实验的预期目标，实验的设计方案，实验的放量计划，实验关心的核心指标等】"
                    @change="commitLayerInfo"
                ></el-input>
            </el-form-item>

            <el-form-item label="实验对象类型：">
                <el-radio v-model="layerInfo.alloc_type_code" :label="4" @change="commitLayerInfo">广告主</el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="5" @change="commitLayerInfo">广告计划</el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="6" @change="commitLayerInfo">博文</el-radio>
                <el-radio v-model="layerInfo.alloc_type_code" :label="7" @change="commitLayerInfo">广告行业</el-radio>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
export default {
    data() {
        return {
            layerInfo: {
                exp_layer_type : 1,
                alloc_type_code: 7,
                exp_layer_comment:
                    "【实验目的】\n\n【实验方案】\n\n【实验风险】",
                exp_layer_name: ""
            }
        };
    },
    mounted() {
        if (this.$store.state.create_exp.strategy_set_id < 1) {
            this.$emit("preStep");
            return;
        }
        if (
            Object.keys(this.$store.state.create_exp.exp_base_info).length > 0
        ) {
            console.log(this.$store.state.create_exp.exp_base_info);
            this.layerInfo = this.$store.state.create_exp.exp_base_info;
        }
    },
    methods: {
        commitLayerInfo() {
            this.$store.commit("setExpBaseInfo", this.layerInfo);
            return;
        },
        checkInputData() {
            if (this.layerInfo.exp_layer_name == "") {
                return ["请输入实验名称"];
            }

            if (this.layerInfo.exp_layer_name.length > 60) {
                return ["实验名称长度在 1 到 60个字符"];
            }

            if (this.layerInfo.exp_layer_comment == "") {
                return ["请输入实验描述"];
            }

            return [];
        }
    }
};
</script>
