<script>
export default {
    props: ["expLayerId", "expVersionCode"],
    methods: {
        async openUserStat() {
            let res = this.$api.exp.getLinkTask(
                this.expLayerId,
                this.expVersionCode
            );

            if (res.errcode == 0) {
                window.open(
                    "http://st.tongji.weibo.com/custom/detail_show?task_id=" +
                        res.data.task_id
                );
            } else {
                if (confirm("没有关联任务，是否跳往列表页面？？")) {
                    window.open(
                        "http://st.tongji.weibo.com/index.php/custom/index"
                    );
                }
            }
        },
        clickFunc: function(type) {
            this.$emit("clickFunc", type);
        }
    }
};
</script>

