<template>
    <el-row :gutter="20">
        <el-col :span="8" style="margin-bottom:10px;height:210px">
            <el-card class="box-card" style="height:100%">
                <div slot="header" class="clearfix">
                    <span>重新生成实验快照</span>
                </div>
                <div class="text item">
                    <div style="margin-bottom: 10px;">
                        <el-button-group>
                            <el-button @click="confirmGen('traffic', '流量侧实验快照')">流量侧</el-button>
                            <el-button @click="confirmGen('customer', '广告主侧实验快照')">广告主侧</el-button>
                            <el-button @click="confirmGen('global', '全局实验快照')">全局快照</el-button>
                        </el-button-group>
                    </div>
                    <!-- <div class="well">主动生成线上快照</div> -->
                    <el-tag type="danger">
                        <i class="el-icon-warning" style="padding-right:10px"></i>主动生成线上快照
                    </el-tag>
                </div>
            </el-card>
        </el-col>

        <el-col :span="8" style="margin-bottom:10px;height:210px">
            <el-card class="box-card" style="height:100%">
                <div slot="header" class="clearfix">
                    <span>
                        删除最后一条实验快照
                        <router-link :to="{path : '/snapshot/list'}">快照查看</router-link>
                    </span>
                </div>
                <div class="text item">
                    <div style="margin-bottom: 10px;">
                        <el-button-group>
                            <el-button @click="confirmDel('traffic', '流量侧实验快照')">流量侧</el-button>
                            <el-button @click="confirmDel('customer', '广告主侧实验快照')">广告主侧</el-button>
                            <el-button @click="confirmDel('global', '全局实验快照')">全局快照</el-button>
                        </el-button-group>
                    </div>
                    <!-- <div class="well">线上快照数据有问题时使用</div> -->
                    <el-tag type="danger">
                        <i class="el-icon-warning" style="padding-right:10px"></i>线上快照数据有问题时使用
                    </el-tag>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
export default {
    methods: {
        confirmDel(type, text) {
            this.$confirm("确认删除最后一条" + text, "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    this.delSnapshot(type);
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消操作"
                    });
                });
        },
        async delSnapshot(type) {
            let res = await this.$api.user.deleteLastSnapshotByGo(type);
            if (res.errcode == 0) {
                // this.$bootbox.alert("删除成功");
                this.$message({
                    showClose: true,
                    message: "删除成功",
                    type: "success"
                });
            } else {
                // this.$bootbox.alert(res.errmsg);
                this.$message({
                    showClose: true,
                    message: res.errmsg,
                    type: "error"
                });
            }
        },
        confirmGen: function(type, text) {
            this.$confirm("确认新生成一条" + text, "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    this.genSnapshot(type);
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "取消操作"
                    });
                });
        },
        async genSnapshot(type) {
            let res = await this.$api.user.genSnapshotByGo(type);
            if (res.errcode == 0) {
                this.$message({
                    showClose: true,
                    message: "生成成功",
                    type: "success"
                });
            } else {
                // this.$bootbox.alert(res.errmsg);
                this.$message({
                    showClose: true,
                    message: res.msg,
                    type: "error"
                });
            }
        }
    }
};
</script>

