<template>
    <div style="margin:0 20px;">
        <!-- <ShowJson :showStr="showJsonStr" /> -->
        <el-card class="box-card">
            <p>
                <strong>实验类型：</strong>
                {{layerTypeName}}
            </p>
        </el-card>

        <el-card class="box-card" style="margin-top:20px;">
            <div slot="header" class="clearfix">
                <strong>实验基本信息</strong>
            </div>
            <p>
                <strong>实验名称：</strong>
                {{previewData.exp_layer_name}}
            </p>
            <p>
                <strong>实验描述：</strong>
                {{previewData.exp_layer_comment}}
            </p>
            <p v-if="previewData.is_same_type == 1">
                <strong>流量A/B分桶类型：</strong>
                {{allocTypeName}}
            </p>
            <template v-if="previewData.exp_layer_type == 0">
                <p>
                    <strong>流量团圈定：</strong>
                    {{previewData.alloc_condition_enable == 0 ? '不启用': ''}}
                </p>
                <el-card class="box-card" v-if="previewData.alloc_condition_enable == 1">
                    <template v-for="item in allocCondition">
                        <p>
                            <strong>{{item.name}} :</strong>
                            {{item.data}}
                        </p>
                    </template>
                </el-card>
            </template>
            <template v-if="previewData.exp_layer_type == 1 && previewData.is_same_type == 1">
                <p>
                    <strong>预算分桶实验：</strong>
                    {{previewData.is_divide_budget ? '是' : '否'}}
                </p>
                <p>
                    <strong>实验对象类型：</strong>
                    {{custObjTypeName}}
                </p>
                <p>
                    <strong>实验对象：</strong>
                    {{custExpObj}}
                </p>
            </template>
            <template v-if="previewData.exp_layer_type == 1 && previewData.is_same_type == 0">
                <p>
                    <strong>实验对象类型：</strong>
                    {{custObjTypeName}}
                </p>
            </template>
        </el-card>

        <el-card class="box-card" style="margin-top:20px;">
            <div slot="header" class="clearfix">
                <strong>子实验分桶设置</strong>
            </div>
            <template v-for="(item, idx) in expList">
                <el-card class="box-card" :key="idx">
                    <p>
                        <strong>子实验 :</strong>
                        {{item.exp_title}}
                    </p>
                    <p>
                        <strong>子实验描述 :</strong>
                        {{item.exp_comment}}
                    </p>
                    <p v-if="previewData.is_same_type == 1">
                        <strong>流量权重 :</strong>
                        {{item.traffic_weight}}
                    </p>
                    <p v-if="previewData.is_same_type == 1">
                        <strong>用户白名单 :</strong>
                        {{item.white_list_uid}}
                    </p>
                    <p v-if="previewData.is_same_type == 0">
                        <strong>实验对象 :</strong>
                        {{item.white_list_uid}}
                    </p>
                    <p>
                        <strong>策略 :</strong>
                    </p>
                    <ul>
                        <li v-for="(item, key) in item.exp_strategy_content">
                            <span class="green-font">{{key}}:</span>
                            {{item}}
                        </li>
                    </ul>
                </el-card>
            </template>
        </el-card>
    </div>
</template>

<script>
import allocConf from "@/config/AllocConditionConf";

export default {
    props: ["previewData"],
    data() {
        return {
            allocCondition: {},
            expList: []
        };
    },
    computed: {
        custObjTypeName: function() {
            if (
                this.previewData.exp_layer_type == 1 &&
                this.previewData.is_same_type == 1
            ) {
                return allocConf.CustObjectTypeMap[
                    this.previewData.exp_object_type
                ];
            }

            if (
                this.previewData.exp_layer_type == 1 &&
                this.previewData.is_same_type == 0
            ) {
                return allocConf.CustObjectTypeMap[
                    this.previewData.alloc_type_code + ''
                ];
            }

            return "";
        },
        custExpObj : function() {
            if (
                this.previewData.exp_layer_type == 1 &&
                this.previewData.is_same_type == 1
            ) {
                let key = Object.keys(this.previewData.alloc_condition_value)[0]
                return this.previewData.alloc_condition_value[key]
            }
            return "";
        },
        allocTypeName: function() {
            return allocConf.AllocTypeMap[this.previewData.alloc_type_code];
        },
        // allocCondition: function() {
        //     return;
        // },
        layerTypeName: function() {
            if (this.previewData.exp_layer_type == 0) {
                return "流量侧";
            }
            if (
                this.previewData.exp_layer_type == 1 &&
                this.previewData.is_same_type == 1
            ) {
                return "广告主侧 - 同质";
            }
            if (
                this.previewData.exp_layer_type == 1 &&
                this.previewData.is_same_type == 0
            ) {
                return "广告主侧 - 非同质";
            }

            return "流量侧";
        },
        showJsonStr: function() {
            return JSON.stringify(this.previewData);
        }
    },
    methods: {
        reload() {
            this.expList = this.previewData.data;

            if (this.previewData.exp_layer_type != 0) {
                return;
            }
            this.allocCondition = this.$tool.foramatAllocCondition(
                this.previewData.alloc_condition_value
            );
        }
    }
};
</script>
