<template>
    <el-row>
        <el-col :offset="1" :span="22">
        <Timeline :data="records">
            <template slot="detail" slot-scope="slotDetailExps">
                <ul
                    class="detail"
                    v-if="slotDetailExps.record.record.action == 'create' || slotDetailExps.record.record.action == 'edit' || slotDetailExps.record.record.action == 'volume'"
                >
                    <li>
                        <span class="key">名 称</span>
                        <span class="val">{{slotDetailExps.record.exp_layer.exp_layer_name}}</span>
                    </li>
                    <li>
                        <span class="key">描 述</span>
                        <span class="val">{{slotDetailExps.record.exp_layer.exp_layer_comment}}</span>
                    </li>
                    <li>
                        <span class="key">创建人</span>
                        <span class="val">{{slotDetailExps.record.exp_layer.creator}}</span>
                    </li>
                    <li>
                        <span class="key">策略集</span>
                        <span class="val">{{strategySetName}}</span>
                    </li>
                    <li>
                        <span class="key">实验配置</span>
                        <span class="val"></span>
                    </li>
                    <section style="overflow:scroll">
                        <table class="exps table table-striped table-bordered">
                            <thead>
                                <tr>
                                    <th>子实验id</th>
                                    <th>标题</th>
                                    <th>流量权重</th>
                                    <th>用户白名单</th>
                                    <template v-for="item in strategys">
                                        <th>策略:{{item}}</th>
                                    </template>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="exp in slotDetailExps.record.exp_list">
                                    <td>layer-{{exp.exp_layer_id}}#exp-{{exp.exp_id}}</td>
                                    <td>{{exp.exp_title}}</td>
                                    <td>{{exp.traffic_weight}}</td>
                                    <td>{{exp.white_list_uid}}</td>

                                    <template v-for="item in strategys">
                                        <td>
                                            <p
                                                class="p-break"
                                                :class="JSON.parse(exp.exp_strategy_content)[item]==base[item]?'':diffFont"
                                            >{{JSON.parse(exp.exp_strategy_content)[item]}}</p>
                                        </td>
                                    </template>
                                </tr>
                            </tbody>
                        </table>
                    </section>
                </ul>

                <ul class="detail" v-if="slotDetailExps.record.record.action == 'volume'">
                    <li>
                        <span class="key">流量变化：</span>
                    </li>

                    <el-table
                        v-if="Object.keys(slotDetailExps.record.edit_log.update).length>0"
                        :data="Object.keys(slotDetailExps.record.edit_log.update).map(k =>slotDetailExps.record.edit_log.update[k])"
                        style="width: 100%"
                        size="mini"
                        border
                        stripe
                    >
                        <el-table-column label="子实验id">
                            <template
                                slot-scope="scope"
                            >layer-{{scope.row.exp_layer_id}}#exp-{{scope.row.exp_id}}</template>
                        </el-table-column>
                        <el-table-column prop="exp_title" label="标题"></el-table-column>
                        <el-table-column label="放量前流量权重">
                            <template slot-scope="scope">
                                <span
                                    :class="slotDetailExps.record.old_exp_list[scope.row.exp_id].traffic_weight != scope.row.traffic_weight ? 'red-font' : ''"
                                >{{slotDetailExps.record.old_exp_list[scope.row.exp_id].traffic_weight}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="放量后流量权重">
                            <template slot-scope="scope">
                                <span
                                    :class="scope.row.traffic_weight_old != scope.row.traffic_weight ? 'red-font': ''"
                                >{{scope.row.traffic_weight}}</span>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-table
                        v-if="Object.keys(slotDetailExps.record.edit_log.add).length>0"
                        :data="Object.keys(slotDetailExps.record.edit_log.add).map(k =>slotDetailExps.record.edit_log.add[k])"
                        style="width: 100%"
                        :show-header="false"
                        size="mini"
                        border
                        stripe
                    >
                        <el-table-column>
                            <template slot-scope="scope">
                                <span>
                                    layer-{{scope.row.exp_layer_id}}#exp-{{scope.row.exp_id}}
                                    <b
                                        class="green-font"
                                    >(新增)</b>
                                </span>
                            </template>
                        </el-table-column>
                        <el-table-column prop="exp_title"></el-table-column>
                        <el-table-column>
                            <template slot-scope="scope">
                                <span class="red-font">{{'0'}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column class="red-font" prop="traffic_weight"></el-table-column>
                    </el-table>
                    <el-table
                        v-if="Object.keys(slotDetailExps.record.edit_log.delete).length>0"
                        :data="Object.keys(slotDetailExps.record.edit_log.delete).map(k =>slotDetailExps.record.edit_log.delete[k])"
                        style="width: 100%"
                        :show-header="false"
                        size="mini"
                        border
                        stripe
                    >
                        <el-table-column>
                            <template slot-scope="scope">
                                <s>
                                    layer-{{scope.row.exp_layer_id}}#exp-{{scope.row.exp_id}}
                                    <b
                                        class="red-font"
                                    >(删除)</b>
                                </s>
                            </template>
                        </el-table-column>
                        <el-table-column>
                            <template slot-scope="scope">
                                <s>{{scope.row.exp_title}}</s>
                            </template>
                        </el-table-column>
                        <el-table-column class="red-font" prop="traffic_weight"></el-table-column>
                        <el-table-column>
                            <template slot-scope="scope">
                                <span class="red-font">{{'0'}}</span>
                            </template>
                        </el-table-column>
                    </el-table>
                </ul>
            </template>
        </Timeline>
        </el-col>
    </el-row>
</template>
<script>
import Timeline from "../Tool/Timeline";

export default {
    props : ['layerId','strategySetName', 'strategys'],
    data() {
        return {
            records: [],
            base: {},
            diffFont: "diff-font"
        };
    },
    watch : {
        layerId() {
            this.getUpdateRecord()
        }
    },
    mounted() {
        this.getUpdateRecord();
    },
    components: {
        Timeline
    },
    methods: {
        async getUpdateRecord() {
            let res = await this.$api.exp.getExpsOperateListByGo(
                this.layerId
            );
            if (res.errcode != 0) {
                return;
            }
            if (res.data == null) {
                return;
            }
            res.data.sort((obj1, obj2) => {
                return obj1.record.id > obj2.record.id ? -1 : 1
            });
            this.records = res.data;
            let list = Object.keys(this.records[0].exp_list);
            this.base = JSON.parse(
                this.records[0].exp_list[list[0]].exp_strategy_content
            );
        }
    }
};
</script>

<style scoped>
.diff-font {
    color: #ed1250;
}
</style>