<template>
	<div>
	    <mytable  :listData="listData"></mytable>
	</div>
</template>
<script>
import table from './ExpTable.vue';
export default {
	components:{
		'mytable':table
	},
	data(){
		return {
			listData : []
		}
	},
	mounted(){
        this.getListData();
    },
	methods : {
        async getListData() {
          let res = {};
          res = await this.$api.exp.getLayerListByGo("my_audit");
          this.$set(this, 'listData', res.data);
        },
    }
}
</script>
