<template>
    <div>
        <el-card class="box-card">
            <el-form ref="form" :label-position="'left'" label-width="100px" v-model="queryConfig">
                <el-form-item label="场景">
                    <el-checkbox-group v-model="queryConfig.psid" @change="toSetQueryConfig">
                        <template v-for="(item, index) in statConfig.psid">
                            <el-checkbox :label="item.code + ''">{{item.name}}</el-checkbox>
                        </template>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="竞价类型">
                    <el-checkbox-group v-model="queryConfig.bid_type" @change="toSetQueryConfig">
                        <template v-for="(item, index) in statConfig.bid_type">
                            <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                        </template>
                    </el-checkbox-group>
                </el-form-item>
            </el-form>

            <el-collapse style="margin-bottom:20px;">
                <el-collapse-item title="更多筛选条件..." name="1">
                    <el-form
                        ref="form2"
                        :label-position="'left'"
                        label-width="100px"
                        v-model="queryConfig"
                    >
                        <el-form-item label="平台">
                            <el-checkbox-group
                                v-model="queryConfig.platform"
                                @change="toSetQueryConfig"
                            >
                                <template v-for="(item, index) in statConfig.platform">
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="营销目标">
                            <el-checkbox-group
                                v-model="queryConfig.promotion_objective"
                                @change="toSetQueryConfig"
                            >
                                <template v-for="(item, index) in statConfig.promotion_objective">
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="优化目标">
                            <el-checkbox-group
                                v-model="queryConfig.optimization_goal"
                                @change="toSetQueryConfig"
                            >
                                <template v-for="(item, index) in statConfig.optimization_objective">
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="创意类型">
                            <el-checkbox-group
                                v-model="queryConfig.creative_style_id"
                                @change="toSetQueryConfig"
                            >
                                <template v-for="(item, index) in statConfig.creative_style_id">
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                    </el-form>
                </el-collapse-item>
            </el-collapse>
            <el-form ref="form" :label-position="'left'" label-width="100px">
                <el-form-item label="子实验">
                    <el-select v-model="expId" @change="toSetQueryConfig">
                        <template v-for="(item, index) in expList">
                            <el-option :value="'layer-' + item.exp_layer_id + '#exp-' + item.exp_id" :label="item.exp_title"></el-option>
                        </template>
                    </el-select>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>
<style scoped>
.el-form-item {
    margin-bottom: 1px;
}
</style>

<script>
import lodash from "lodash";
import { mapState } from "vuex";

export default {
    data() {
        return {
            activeNames: [],
            statConfig: {
                platform: [],
                bid_type: [],
                promotion_objective: [],
                optimization_objective: [],
                creative_style_id: [],
                psid: [],
                service: []
            },
            queryConfig: {
                platform: [],
                bid_type: [],
                promotion_objective: [],
                creative_style_id: [],
                optimization_goal: [],
                psid: []
            },
            groupBy: "psid",
            expId : "",
        };
    },
    mounted: function() {
        this.$store.commit("setQueryConfig", this.queryConfig);
        this.getStatConfig();
    },
    watch : {
        expList : function() {
            this.expId = "layer-" + this.expList[0].exp_layer_id + "#exp-" + this.expList[0].exp_id;
            this.toSetQueryConfig()
        }
    },
    computed: {
        ...mapState({
            expList: state => state.exp_effect.exp_list,
        }),
        showQuotaTip: function() {
            return (
                [
                    "bid_type",
                    "promotion_objective",
                    "creative_style_id"
                ].indexOf(this.groupBy) > -1 ||
                this.queryConfig.bid_type.length > 0 ||
                this.queryConfig.promotion_objective.length > 0 ||
                this.queryConfig.creative_style_id.length > 0
            );
        }
    },
    methods: {
        async getStatConfig() {
            let res = await this.$api.effect.getConfig();
            this.$set(this, "statConfig", res.data);
        },
        toSetQueryConfig() {
            this.$store.commit("selectGroupBy", this.groupBy);
            this.$store.commit("setExpId", this.expId);
        }
    }
};
</script>

