<template>
    <div class="panel panel-info" v-if="applyList.length > 0">
        <table class="table table-bordered table-hover">
            <thead>
                <tr>
                    <td>ID</td>
                    <td>类型</td>
                    <td>状态</td>
                    <td>申请人</td>
                    <td>创建时间</td>
                    <td>更新时间</td>
                    <td>操作人</td>
                    <td>备注</td>
                </tr>
            </thead>
            <tbody>
                <template v-for="(item, idx) in applyList">
                    <tr
                        :key="idx"
                        v-if="expLayerId == undefined || expLayerId == item.layer.exp_layer_id"
                    >
                        <td>
                            <a
                                href="javascript:;"
                                v-if="item.apply.apply_type == 10"
                            >{{item.apply.id}}</a>
                            <template v-if="item.apply.apply_type == 2">{{item.apply.id}}</template>
                        </td>
                        <td>{{item.apply_type_text}}</td>
                        <td>{{item.confirm_text}}</td>
                        <td>{{item.apply.username}}</td>
                        <td>{{item.apply.create_time}}</td>
                        <td>{{item.apply.update_time}}</td>
                        <td>{{item.apply.auditor}}</td>
                        <td>{{item.apply.audit_reason}}</td>
                    </tr>
                </template>
            </tbody>
        </table>

        <modal modalId="confirmDetail">
            <template slot="header">
                <h4 class="modal-title">确认详情</h4>
            </template>
            <ul class="list-group">
                <template v-for="(item, index) in confirmList.leader">
                    <li class="list-group-item">
                        {{index + 1}}.
                        <label>{{item.assign_user}}</label>
                        <span class="label" :class="item.as_status_cls">{{item.as_status_text}}</span>
                        <span
                            class="label label-primary"
                            v-if="item.show_expire"
                        >将于{{item.expire_time}}过期</span>
                        <span
                            class="label label-danger"
                            v-if="item.as_status == -1"
                        >理由：{{item.reply_content}}</span>
                    </li>
                </template>
            </ul>
        </modal>
    </div>
    <div v-else>暂无</div>
</template>
<script>
export default {
    props: ["expLayerId", "strategySetId"],
    data() {
        return {
            applyList: [],
            confirmList: {
                leader: [],
                exper: []
            }
        };
    },
    methods: {
        async getApplyList() {
            let res = await this.$api.strategy.getVolumeApplys(
                this.strategySetId
            );
            if (res.data != null) {
                this.applyList = res.data;
            } else {
                this.applyList = [];
            }
        },
        async getConfirmList(applyId) {
            let res = await this.$api.exp.getConfirmListByApplyId(applyId);
            $("#confirmDetail").modal("show");
            this.confirmList = res.data;
        }
    }
};
</script>
