
import Vue from 'vue'
import App from './Index.vue'
import router from './router.js'
import lodash from 'lodash'
import Export2Excel from '@/assets/js/Export2Excel.js';
import tool from '@/utils/tool.js';
import api from '@/api';
import VueClipboard from 'vue-clipboard2'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
require('vue-tour/dist/vue-tour.css')

VueClipboard.config.autoSetContainer = true
Vue.use(VueClipboard)


import Loading from '@/components/Widget/Loading';
import ShowJson from '@/components/Widget/ShowJson';
import Popover from '@/components/Widget/Popover';
import Paginate from '@/components/Widget/Paginate';
import DrawChart from '@/components/Widget/DrawChart';

Vue.component('loading', Loading);
Vue.component('ShowJson', ShowJson);
Vue.component('popover', Popover);
Vue.component('paginate', Paginate);
Vue.component('drawchart', DrawChart);
import store from '@/store'

Vue.config.productionTip = false;
Vue.prototype.$lodash = lodash;
Vue.prototype.Export2Excel = Export2Excel;
Vue.prototype.$tool = tool;
Vue.prototype.$api = api;

Vue.prototype.$errorMsg = '暂时无法获取数据，请刷新页面重试，如果还无法解决，请联系liuhu3@staff.weibo.com，q767856178';

//系统错误捕获
const errorHandler = (error, vm)=>{
    vm.$api.user.saveJsErrorTrack({
      message : error.message,
      name : error.name,
      path : vm.$route.fullPath
    });
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  store
});
