<template>
    <div>
        
        <el-row>
            <el-col :offset="4">
            <el-form :inline="true">
                <el-form-item label="实验：">
                    <el-select v-model="expId" placeholder="选择实验">
                        <template v-for="(item, index) in expList">
                            <el-option :label="item.exp_title" :value="item.uniqe_exp_id"></el-option>
                        </template>
                    </el-select>
                </el-form-item>
                <el-form-item label="实验：">
                    <el-select v-model="expObject" placeholder="广告计划：">
                        <template v-for="item in expObjects">
                            <el-option :label="item" :value="item"></el-option>
                        </template>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <i class="el-icon-loading" v-if="loadState"></i>
                </el-form-item>
            </el-form>
            </el-col>
        </el-row>

        <el-row v-if="loadState == false">
            <drawchart
                :chartData="chartData"
                chartId="budgetExpLayerConsumeChart"
                v-if="loadState != 'loading'"
            />
        </el-row>
    </div>
</template>
<script>
export default {
    props: ["expLayerId"],
    data() {
        return {
            chartData: {
                data: {},
                title: "",
                xAxis: []
            },
            expId: "",
            expObject: "",
            expList: [],
            expObjects: [],
            loadState: ""
        };
    },
    watch: {
        expId() {
            this.getChart();
        },
        expObject() {
            this.getChart();
        }
    },
    methods: {
        async getBudgetExpLayer() {
            let res = await this.$api.exp.getBudgetExpLayer(this.expLayerId);
            this.$set(this, "expList", res.data.exp_list);
            this.$set(this, "expObjects", res.data.exp_object);
            this.$set(this, "expId", res.data.exp_list[0].uniqe_exp_id);
            this.$set(this, "expObject", res.data.exp_object[0]);
            this.getChart();
        },
        async getChart() {
            this.loadState = true;
            this.chartData = {
                data: {},
                title: "",
                xAxis: []
            };
            let res = await this.$api.exp.getExpObjectConsume(
                this.expId,
                this.expObject
            );
            this.loadState = false;
            this.$set(this, "chartData", res.data);
        }
    }
};
</script>

