<template>
    <div>
        <el-card class="box-card">
            <el-form ref="form" :label-position="'left'" label-width="100px" v-model="queryConfig">
                <el-form-item label="场景">
                    <el-checkbox-group v-model="queryConfig.psid" @change="toSetQueryConfig">
                        <template v-for="(item, index) in statConfig.psid">
                            <el-checkbox :label="item.code + ''">{{item.name}}</el-checkbox>
                        </template>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="竞价类型">
                    <el-checkbox-group v-model="queryConfig.bid_type" @change="toSetQueryConfig">
                        <template v-for="(item, index) in statConfig.bid_type">
                            <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                        </template>
                    </el-checkbox-group>
                </el-form-item>
            </el-form>
             <template v-if="showQuotaTip">
                <el-alert
                    title="请求类指标提醒"
                    type="warning"
                    description="展现维度为营销目标、竞价类型、创意类型 或 筛选条件包含营销目标、竞价类型、创意类型时，请求类的指标和请求类的相关率值不会返回，
                    请求类的指标包含：库存、请求量、出广告的请求量"
                    show-icon
                    :closable="false"
                ></el-alert>
            </template>
            <el-collapse style="margin-bottom:20px;">
                <el-collapse-item title="更多筛选条件..." name="1">
                    <el-form ref="form2" :label-position="'left'" label-width="100px" v-model="queryConfig">
                        <el-form-item label="平台">
                            <el-checkbox-group v-model="queryConfig.platform" @change="toSetQueryConfig">
                                <template v-for="(item, index) in statConfig.platform">
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="营销目标">
                            <el-checkbox-group v-model="queryConfig.promotion_objective" @change="toSetQueryConfig">
                                <template v-for="(item, index) in statConfig.promotion_objective">
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="优化目标">
                            <el-checkbox-group v-model="queryConfig.optimization_goal" @change="toSetQueryConfig">
                                <template
                                    v-for="(item, index) in statConfig.optimization_objective"
                                >
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="创意类型">
                            <el-checkbox-group v-model="queryConfig.creative_style_id" @change="toSetQueryConfig">
                                <template v-for="(item, index) in statConfig.creative_style_id">
                                    <el-checkbox :label="item.code">{{item.name}}</el-checkbox>
                                </template>
                            </el-checkbox-group>
                        </el-form-item>
                    </el-form>
                </el-collapse-item>
            </el-collapse>

            <el-form ref="form3" :label-position="'right'" label-width="100px">
                <el-form-item label="选择展现维度">
                    <el-radio-group v-model="groupBy" @change="toSetQueryConfig">
                        <el-radio-button :label="'psid'">场景</el-radio-button>
                        <el-radio-button :label="'bid_type'">竞价类型</el-radio-button>
                        <el-radio-button :label="'platform'">平台</el-radio-button>
                        <el-radio-button :label="'promotion_objective'">营销目标</el-radio-button>
                        <el-radio-button :label="'creative_style_id'">创意类型</el-radio-button>
                    </el-radio-group>
                </el-form-item>

                <!-- <el-form-item>
                    <el-button type="warning" @click="toQuery" icon="el-icon-search">查询</el-button>
                </el-form-item> -->
            </el-form>
        </el-card>
    </div>
</template>
<style scoped>
.el-form-item {
    margin-bottom: 1px;
}
</style>

<script>
import lodash from "lodash";

export default {
    data() {
        return {
            activeNames: [],
            statConfig: {
                platform: [],
                bid_type: [],
                promotion_objective: [],
                optimization_objective : [],
                creative_style_id: [],
                psid: [],
                service: []
            },
            queryConfig: {
                platform: [],
                bid_type: [],
                promotion_objective: [],
                creative_style_id: [],
                optimization_goal : [],
                psid: []
            },
            groupBy: "psid",
        };
    },
    mounted: function() {
        this.$store.commit("setQueryConfig", this.queryConfig);
        this.getStatConfig();
    },
    computed : {
        showQuotaTip : function() {
            return [
                    "bid_type",
                    "promotion_objective",
                    "creative_style_id"
                ].indexOf(this.groupBy) > -1 ||
                this.queryConfig.bid_type.length > 0 ||
                this.queryConfig.promotion_objective.length >
                    0 ||
                this.queryConfig.creative_style_id.length > 0;
        }
    },
    methods: {
        async getStatConfig() {
            let res = await this.$api.effect.getConfig();
            this.$set(this, "statConfig", res.data);
        },
        toSetQueryConfig() {
            this.$store.commit("selectGroupBy", this.groupBy);
            this.$store.commit("setQueryConfig", this.queryConfig);
        }
    }
};
</script>

