<template>
    <el-row>
        <el-col>
            <el-form ref="form" :label-position="'top'" label-width="150px">
                <el-form-item label="产品线">
                    <el-radio-group v-model="expZoneCode" @change="changeExpZoneCode">
                        <template v-for="(item, code) in expZoneList">
                            <el-radio-button
                                :label="item.exp_zone_code"
                            >{{ item.product_chinese_name + ' | ' + item.exp_zone_chinese_name}}</el-radio-button>
                        </template>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="策略/参数">
                    <el-col>
                        <el-select
                            placeholder="请选择策略"
                            style="display:block;"
                            v-model="strategySetId"
                            filterable
                            @change="changeStrategySetId"
                        >
                            <template v-for="item in strategySetList">
                                <el-option
                                    :label="item.strategy_set_name_new"
                                    :value="item.strategy_set_id"
                                ></el-option>
                            </template>
                        </el-select>
                    </el-col>
                </el-form-item>
            </el-form>
        </el-col>
    </el-row>
</template>

<script>
export default {
    data() {
        return {
            expZoneList: [],
            strategySetList: [],
            expZoneCode: 78301,
            strategySetId: 0
        };
    },
    mounted() {
        if (this.$store.state.create_exp.exp_layer_type < 0) {
            this.$tool.reload();
            return;
        }
        this.getMyAccessList(
            this.$store.state.create_exp.exp_zone_code,
            this.$store.state.create_exp.strategy_set_id
        );
        this.$store.commit("initAssignUser", []);
    },
    methods: {
        async getMyAccessList(zoneCode, strategySetId) {
            let res = await this.$api.user.getUserExpZoneList(2);

            if (res.data.length < 1) {
                this.$alert(
                    "你暂时没有创建实验权限，请您联系tieniu@staff.weibo.com开通权限",
                    "无权限提示",
                    {
                        confirmButtonText: "确定",
                        callback: action => {
                            this.$tool.reload();
                        }
                    }
                );
                return;
            }
            this.expZoneList = res.data;

            if (zoneCode > 0) {
                this.expZoneCode = zoneCode;
            } else {
                this.expZoneCode = this.expZoneList[0].exp_zone_code;
                this.$store.commit("setExpZoneCode", this.expZoneCode);
            }

            res = await this.$api.strategy.getCreateExpStrategyList(
                this.expZoneCode,
                this.$store.state.create_exp.exp_layer_type
            );

            if (res.data.length < 1) {
                this.$message("您在实验区下暂无可用实验的策略");
                return;
            }

            this.strategySetList = this.$tool.fmtStrategySetList(res.data);
            if (strategySetId > 0) {
                this.strategySetId = strategySetId;
            } else {
                this.strategySetId = this.strategySetList[0].strategy_set_id;
                this.changeStrategySetId();
            }
        },
        async changeExpZoneCode() {
            this.$store.commit("setExpZoneCode", this.expZoneCode);
            let res = await this.$api.strategy.getCreateExpStrategyList(
                this.expZoneCode,
                this.$store.state.create_exp.exp_layer_type
            );

            if (Object.keys(res.data).length < 1) {
                this.strategySetList = [];
                this.$message("您在实验区下暂无可用实验的策略");
                this.strategySetId = 0;
                this.changeStrategySetId();
                return;
            }
            this.strategySetList = this.$tool.fmtStrategySetList(res.data);
            this.strategySetId = this.strategySetList[0].strategy_set_id;
            this.changeStrategySetId();
        },
        changeStrategySetId() {
            this.$store.commit("setStrategySetId", this.strategySetId);
        },
        async checkInputData() {
            let res = {};
            if (this.strategySetId < 1) {
                return ["请选择策略"];
            }

            res = await this.$api.strategy.getStrategyExpStat(
                this.strategySetId
            );

            if (res.data.strategy_set.strategy_set_id < 1) {
                return ["策略有误"];
            }

            if (res.data.volume_full_apply.id > 1) {
                return ["有正在申请全量的实验，暂时无法创建实验"];
            }

            if (
                this.$store.state.create_exp.exp_layer_type == 0 &&
                this.expZoneCode == 78301
            ) {
                if (res.data.use_traffic >= 1000) {
                    return ["剩余域流量为0，无法继续创建实验"];
                }
            } else {
                if (res.data.exp_layer.exp_layer_id > 0) {
                    return [
                        "实验" +
                            res.data.exp_layer.exp_layer_name +
                            "正在占用该策略，暂时无法创建实验"
                    ];
                }
            }

            if (res.data.strategy_set.strategy_set_type == 2) {
                if (
                    res.data.exp_layer.exp_layer_type != 0 
                ) {
                    return [
                        "实验" +
                            res.data.exp_layer.exp_layer_name +
                            "正在占用该策略，暂时无法创建实验"
                    ];
                }
            }
            return [];
        }
    }
};
</script>
