<template>
    <div>
        <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="离线vs实时" name="realtime_check">
                <RealtimeDataCheck></RealtimeDataCheck>
            </el-tab-pane>
            <el-tab-pane label="原表vs离线" name="offline_check">
                <OfflineDataCheck></OfflineDataCheck>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import lodash from "lodash";
import RealtimeDataCheck from "./RealtimeDataCheck.vue";
import OfflineDataCheck from "./OfflineDataCheck.vue";

export default {
    data: function() {
        return {
            tableType: "realtime_check",
            activeName: "realtime_check"
        };
    },
    mounted: function() {
        this.$emit("fixedTableHead", "#tableRealtimeId");
        if (this.$route.query.error_dates) {
            this.tableType = "offline_check";
        }
    },
    components: {
        RealtimeDataCheck: RealtimeDataCheck,
        OfflineDataCheck: OfflineDataCheck
    },
    methods: {
        handleClick(tab, event) {
            console.log(tab, event);
            this.activeName = tab.name;
            if (this.activeName == "realtime_check") {
                this.$emit("fixedTableHead", "#tableRealtimeId");
            } else {
                this.$emit("fixedTableHead", "#tableOfflineId");
            }
        },
        switchTab: function(tabType) {
            this.tableType = tabType;
            if (this.tableType == "realtime_check") {
                this.$emit("fixedTableHead", "#tableRealtimeId");
            } else {
                this.$emit("fixedTableHead", "#tableOfflineId");
            }
        }
    }
};
</script>
