<template>
    <el-row>
        <el-col :span="20" :offset="2">
            <el-card>
                <el-form ref="form" label-width="80px">
                    <el-form-item label="权限类型:">
                        <el-radio-group v-model="user_type">
                            <el-radio label="普通用户"></el-radio>
                            <el-radio label="审核员"></el-radio>
                            <el-radio label="实验员"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="实验区:">
                        <el-button
                            type="default"
                            v-for="(item, idx) in areaList"
                            :key="idx"
                            style="margin:5px;cursor:pointer"
                            @click="selectArea(item)"
                            :type="selectedList.indexOf(item) > -1 ? 'primary' : ''"
                        >{{item}}</el-button>
                    </el-form-item>
                    <el-form-item label="抄送:">
                        <el-input
                            v-model="userTo"
                            placeholder="输入直属learder邮箱前缀"
                            style="width:400px"
                        ></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="confirmDredge" type="warning">申请</el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </el-col>
    </el-row>
</template>
<script>
export default {
    data() {
        return {
            user_name: "",
            userTo: "",
            user_type: "普通用户", //用户权限类型
            // authorityTy: ["审核员", "实验员", "普通用户"],
            areaList: [],
            selectedList: [],
            allSelect: false
        };
    },
    mounted() {
        this.user_name = localStorage.getItem("userName");
        this.getArea();
    },
    watch: {
        allSelect(newval, oldval) {
            if (newval == true) {
                this.selectedList = this.areaList;
            } else {
                this.selectedList = [];
            }
        }
    },
    methods: {
        async getArea() {
            let res = await this.$api.user.getAllExpZones();

            for (var val in res.data) {
                let temp =
                    res.data[val].product_chinese_name +
                    "-" +
                    res.data[val].exp_zone_chinese_name;
                this.areaList.push(temp);
                // console.log(product_chinese_name+'-'+res.data[val].exp_zone_chinese_name);
            }
            // this.areaList = res.data.map(val => {
            //     return (
            //         val.product_chinese_name + "-" + val.exp_zone_chinese_name
            //     );
            // });
        },
        selectArea(item) {
            if (this.selectedList.indexOf(item) == -1) {
                this.selectedList.push(item);
            } else {
                let index = this.selectedList.indexOf(item);
                this.selectedList.splice(index, 1);
            }
        },
        // 申请权限
        async confirmDredge() {
            if (this.selectedList.length == 0) {
                this.$message("请选择实验区");
                return;
            }

            if (this.userTo == "") {
                this.$message("请选择抄送leader");
                return;
            }
            var exp_zone = this.selectedList.join("、");

            let cor = confirm("确认是否申请？");
            if (!cor) {
                return;
            }
            let res = await this.$api.user.applyUserAccessByGo(
                this.user_name,
                this.user_type,
                exp_zone,
                this.userTo
            );
            if (res.errcode == 0) {
                this.$message("申请成功");
                return;
            }
            this.$message(res.errmsg);
        }
    }
};
</script>

<style scoped>
.form-item {
    width: 60%;
    margin-top: 10px;
}
</style>
