<template>
    <el-row>
        <el-tabs v-model="tabType" type="border-card">
            <el-tab-pane label="全部策略" name="all">
                <StrategyAllList></StrategyAllList>
            </el-tab-pane>
            <el-tab-pane label="我的策略" name="mine">
                <StrategyMyList></StrategyMyList>
            </el-tab-pane>
        </el-tabs>
    </el-row>
</template>

<script>
import StrategyAllList from "./StrategyAllList";
import StrategyMyList from "./StrategyMyList";

export default {
    data() {
        return {
            tabType: "all"
        };
    },
    watch : {
        tabType() {
            localStorage.setItem("strategyType", this.tabType);
        }
    },
    components: {
        StrategyAllList,
        StrategyMyList
    },
    mounted() {
        let type = localStorage.getItem("strategyType");
        if (type) {
            this.tabType = type;
        }
    }
};
</script>
