<template>
    <div>
        <div class="alert alert-danger" role="alert" v-show="errDates.length > 0">
            以下日期的统计数据误差较大：
            <div class="btn-group" role="group" aria-label="...">
                <template v-for="item in errDates">
                    <button
                        type="button"
                        class="btn btn-default"
                        :class="date == item ? 'btn-danger' : ''"
                        @click="switchDate(item)"
                    >{{item}}</button>
                </template>
            </div>
        </div>
        <p v-if="layerInfo.exp_layer_id > 0">
            <strong>子实验：</strong>
            {{layerInfo.exp_layer_name}} - {{expInfo.exp_title}}
        </p>
        <el-card class="box-card">
            <div slot="header" class="clearfix" style="height:40px">
                <span>
                    <span class="glyphicon glyphicon-calendar" aria-hidden="true"></span>
                    <el-select v-model="date" placeholder="请选择" @change="loadData">
                        <el-option
                            v-for="(item, index) in dateList"
                            :key="index"
                            :label="item"
                            :value="item"
                        ></el-option>
                    </el-select>
                </span>
                <span style="float: right; padding: 3px 0">
                    <el-form ref="form" label-width="80px">
                        <el-form-item label="特殊资源">
                            <el-radio-group v-model="dataType">
                                <el-radio value="hive" label="hive">hive数</el-radio>
                                <el-radio value="offline" label="offline">离线数</el-radio>
                                <el-radio value="dist" label="dist">误差数(离线数 - hive数)</el-radio>
                                <el-radio value="percent" label="percent">误差率(误差数 / hive数)</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                </span>
            </div>

            <div>
                <el-table
                    stripe
                    style="width: 100%"
                    :data="typeList"
                    height="500"
                    v-loading="loading"
                    element-loading-text="拼命加载中"
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                >
                    <el-table-column label="日期" prop="time"></el-table-column>
                    <template v-for="(item,key) in quotas">
                        <el-table-column :label="item">
                            <template slot-scope="scope">
                                <span
                                    style="cursor:pointer"
                                    :class="scope.row.color[key]"
                                    @mouseover="showStatDetail(statData[scope.row.time], key, $event)"
                                >{{scope.row[key]}}
                                </span>
                            </template>
                        </el-table-column>
                    </template>
                </el-table>
            </div>
            <el-alert
                title="说明：原表数据是D+端每天随机选取一个固定的子实验的统计全部场景小时数据，写入mysql供我们读取，再和该子实验离线小时数据对比"
                type="warning"
                style="margin-top:30px"
                show-icon>
            </el-alert>
        </el-card>
    </div>
</template>

<script>
import moment from "moment";
import ScrollBtn from "@/components/Widget/ScrollBtn";

export default {
    components: { ScrollBtn },
    data: function() {
        return {
            statData: {},
            dateList: [],
            date: "",
            quotas: {},
            noData: false,
            showTable: false,
            expInfo: {
                exp_layer: {
                    exp_layer_name: ""
                },
                exp_title: ""
            },
            layerInfo : {
                exp_layer_id :0
            },
            dataType: "percent",
            errDates: [],
            templateId: "tableOfflineId",
            // typeList: [],
            loading: true,
            // loadState:true
        };
    },
    computed: {
        typeList() {
            let arr = [];
            Object.keys(this.statData).forEach(key => {
                let obj = {
                    time: key,
                    activate_count: this.statData[key][this.dataType]
                        .activate_count,
                    consume: this.statData[key][this.dataType].consume,
                    download_count: this.statData[key][this.dataType]
                        .download_count,
                    color: this.statData[key].cls,
                    inventory_count: this.statData[key][this.dataType]
                        .inventory_count,
                    monitor_pv_count: this.statData[key][this.dataType]
                        .monitor_pv_count,
                    pv_count: this.statData[key][this.dataType].pv_count,
                    req_count: this.statData[key][this.dataType].req_count,
                    total_bhv_count: this.statData[key][this.dataType]
                        .total_bhv_count,
                    unlike_count: this.statData[key][this.dataType].unlike_count
                };

                arr.push(obj);
            });
            return arr;
        }
    },
    mounted: function() {
        for (var i = 1; i <= 10; i++) {
            this.dateList.push(
                moment()
                    .subtract(i, "day")
                    .format("YYYY-MM-DD")
            );
        }
        if (this.$route.query.error_dates) {
            this.findErrorDate();
        } else {
            this.date = this.dateList[0];
        }
        this.loadData();
    },
    methods: {
        async loadData() {
            this.loading = true;
            this.statData = {};
            this.showTable = false;
            let res = await this.$api.effect.getOfflineStatCheckByGo(this.date);
            // this.loadState = "";
            if (res.errcode != 0) {
                // this.loadState = "no-data";
                this.showTable = false;
                return;
            }
            this.statData = res.data.data;
            this.quotas = res.data.quotas;
            this.expInfo = res.data.exp;
            this.layerInfo = res.data.layer;
            this.loading = false;

        },
        findErrorDate: function() {
            let errDates = this.$route.query.error_dates || "";
            if (errDates == "") {
                this.errDates = [];
                return;
            }
            this.errDates = errDates.split("|");

            if (this.errDates.length > 0) {
                this.date = this.errDates[0];
            }
        },
        switchDate: function(d) {
            this.date = d;
            this.loadData();
        },
        showStatDetail: function(data, quota, event) {
            $(event.currentTarget)
                .tooltip({
                    placement: "top",
                    html: true,
                    title:
                        "hive数：" +
                        data.hive[quota] +
                        "<br>离线数：" +
                        data.offline[quota] +
                        "<br>误差数：" +
                        data.dist[quota] +
                        "<br>误差率：" +
                        data.percent[quota],
                    container: "body"
                })
                .tooltip("toggle");
        }
    }
};
</script>
<style scoped>
