<template>
    <el-row>
        <el-form :inline="true" class="demo-form-inline">
            <el-form-item label="选择时间">
                <el-date-picker
                    style="width:100%"
                    v-model="time"
                    type="daterange"
                    align="right"
                    unlink-panels
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    :picker-options="pickerOptions"
                ></el-date-picker>
            </el-form-item>
            <el-form-item label="用户名">
                <el-input v-model="user"></el-input>
            </el-form-item>
            <el-form-item label="系统选择">
                <el-select v-model="webType" style="width:140px">
                    <el-option label="全部" value="all"></el-option>
                    <el-option label="实验平台" value="faraday"></el-option>
                    <el-option label="精益洞察" value="insights"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button id="export" type="primary" @click="clickToSearch">查询</el-button>
            </el-form-item>
        </el-form>
        <el-row>
            <el-col :span="24">
                <el-table :data="list" border style="width: 100%" stripe>
                    <el-table-column prop="username" label="用户名"></el-table-column>
                    <el-table-column prop="page_name" label="页面"></el-table-column>
                    <el-table-column prop="page_params" label="参数"></el-table-column>
                    <el-table-column prop="create_time" label="访问时间"></el-table-column>
                </el-table>

            </el-col>

            <loading :state="loadState" />
        </el-row>
        <el-pagination
            style="text-align:center;margin-top:20px"
            @current-change="handleCurrentChange"
            :current-page.sync="pageIndex"
            layout="total, prev, pager, next"
            :page-count="pageTotal"
        ></el-pagination>
    </el-row>
</template>

<script>
import moment from "moment";
import Paginate from "@/components/Widget/Paginate";

export default {
    data() {
        return {
            loadState: "",
            list: [],
            statData: {},
            fromTime: "",
            toTime: "",
            user: "",
            webType: "all",
            time: [
                moment(new Date()).add(-3, "days"),
                moment().format("YYYY-MM-DD")
            ],
            pageIndex: 1,
            pageTotal: 0,
            pickerOptions: {
                shortcuts: [
                    {
                        text: "最近一周",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 7
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近一个月",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 30
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近三个月",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 90
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    }
                ]
            }
        };
    },

    mounted() {
        this.getRecordList();
    },
    components: { Paginate },
    methods: {
        handleCurrentChange(val) {
            this.getRecordList();
        },
        swicthPage() {
            this.getRecordList();
        },
        clickToSearch: function() {
            this.pageIndex = 1;
            this.getRecordList();
        },
        async getRecordList() {
            this.loadState = "loading";

            this.list = [];

            let query = this.buildQuery();
            let res = await this.$api.user.getPageViewListByGo(query);
            this.pageTotal = res.data.total_page;
            this.loadState = "";

            var data = res.body;

            this.list = res.data.list;
        },
        buildQuery: function() {
            let query = {
                from: moment(this.time[0]).format("YYYY-MM-DD 00:00:00"),
                to: moment(this.time[1]).format("YYYY-MM-DD 23:59:59"),
                username: this.user,
                web_type: this.webType,
                page: this.pageIndex
            };

            return query;
        },
        async getStat() {
            let res = await this.$api.user.getPageViewStat();
            this.statData = res.data;
        }
    }
};
</script>

<style lang="less" scoped>
.demo-form-inline {
    margin-bottom: 0px;
}
</style>
