<template>
    <el-row>
        <el-col :offset="2" :span="16">
            <el-form :label-position="'right'" label-width="180px" :model="strategySetInfo">
                <el-form-item label="所属产品线(实验区)：">
                    <el-select placeholder="请选择策略" v-model="strategySetInfo.exp_zone_code">
                        <template v-for="item in useExpZones">
                            <el-option
                                :label="item.product_chinese_name + '||' + item.exp_zone_chinese_name"
                                :value="item.exp_zone_code"
                            ></el-option>
                        </template>
                    </el-select>
                </el-form-item>
                <el-form-item label="策略：">
                    <el-input
                        v-model="strategySetInfo.strategy_set_name"
                        placeholder="请输入策略名称（全英文小写字母或下划线，共不超过50个字符）"
                        @change="changeStrategySetName()"
                    ></el-input>
                </el-form-item>
                <el-form-item label="策略类型：">
                    <el-radio
                        v-model="strategySetType"
                        :label="0"
                        @change="changeStrategySetType"
                    >流量侧</el-radio>
                    <el-radio
                        v-model="strategySetType"
                        :label="1"
                        @change="changeStrategySetType"
                    >广告主侧</el-radio>
                    <el-radio
                        v-model="strategySetType"
                        :label="2"
                        @change="changeStrategySetType"
                    >流量侧 + 广告主侧</el-radio>
                </el-form-item>
                <el-form-item label="策略功能：">
                    <el-radio v-model="strategySetInfo.func" label="exp">效果实验</el-radio>
                    <el-radio v-model="strategySetInfo.func" label="control">功能控制</el-radio>
                </el-form-item>
                <el-form-item label="策略描述：">
                    <el-input
                        type="textarea"
                        v-model="strategySetInfo.strategy_set_comment"
                        rows="6"
                        placeholder="请输入策略描述信息"
                    ></el-input>
                </el-form-item>
            </el-form>
            <AddAuditor
                :expZoneCode="strategySetInfo.exp_zone_code"
                v-model="strategySetInfo.assign_auditor"
            />
        </el-col>

        <el-col :offset="4" :span="16" style="margin-top:30px">
            <SetStrategy @setStrategyList="setStrategyList" />
        </el-col>

        <el-col :offset="11" :span="2" style="margin-top:20px">
            <el-button
                type="primary"
                :disabled="isDisableSubmit"
                @click="toCreateStrategySet"
            >{{isDisableSubmit ? '正在提交中，请等待...' : '提交策略'}}</el-button>
        </el-col>
    </el-row>
</template>

<script>
import lodash from "lodash";
import AddAuditor from "../Exp/AddAuditor";
import SetStrategy from "./SetStrategy";

export default {
    data() {
        return {
            strategyList: [],
            expZoneList: [],
            strategySetInfo: {
                exp_zone_code: 0,
                strategy_set_comment:
                    "【策略名称】\n(在此输入策略的中文名称)\n\n【策略功效】\n(在此输入此策略的功效)\n\n【策略描述】\n(在此填写策略的描述备注信息)\n\n【策略构成】\n(在此输入各个子策略含义和规格)\n",
                func: "exp",
                strategy_set_name: "xxxxx_xxxx_strategy",
                assign_auditor: []
            },
            strategySetType: 0,
            strategy_set_comment_default_length: 91,
            strategySetName: "xxxxx_xxxx_strategy",
            strategyRegexpMsg: false,
            isDisableSubmit: false
        };
    },
    components: {
        AddAuditor,
        SetStrategy
    },
    mounted() {
        this.getMyAccessList();
        this.$store.commit("initAssignUser", []);
    },
    computed: {
        useExpZones: function() {
            return this.expZoneList.filter(item => {
                if (this.strategySetType != 0) {
                    return item.product_code == 783;
                }
                return true;
            });
        }
    },
    methods: {
        changeStrategySetType() {
            if (
                this.strategySetType != 0 &&
                this.strategySetInfo.exp_zone_code != 78301
            ) {
                this.strategySetInfo.exp_zone_code = 78301;
            }
            if (this.strategySetType != 0) {
                this.$message("广告主侧策略目前只支持超级粉丝通实验区");
            }
        },
        changeStrategySetName() {
            let matchData = this.strategySetInfo.strategy_set_name.match(
                /^[a-z_]{1,50}$/
            );
            if (matchData === null) {
                this.$message("请输入全英文小写字母或下划线，共不超过50个字符");
                return false;
            }
            return true;
        },
        setStrategyList(list) {
            this.strategyList = list;
        },
        async getMyAccessList() {
            let res = await this.$api.user.getUserExpZoneList(2);

            if (res.errcode != 0) {
                this.$message(res.errmsg);
                this.$router.push({ path: "/strategy/index" });
                return;
            }

            this.expZoneList = res.data;
            let selectIndex = lodash.findIndex(this.expZoneList, [
                "exp_zone_code",
                78301
            ]);
            selectIndex = selectIndex > -1 ? selectIndex : 0;
            this.strategySetInfo.exp_zone_code = this.expZoneList[
                selectIndex
            ].exp_zone_code;
        },
        getPostData: function() {
            var requestData = {
                strategy_set_name: this.strategySetInfo.strategy_set_name,
                strategy_set_comment: this.strategySetInfo.strategy_set_comment,
                exp_zone_code: this.strategySetInfo.exp_zone_code,
                strategy_set_type: this.strategySetType,
                func: this.strategySetInfo.func,
                assign_auditor: this.strategySetInfo.assign_auditor
            };

            requestData.strategy_list = this.strategyList.filter(
                item => item.is_del == undefined
            );
            return requestData;
        },
        getStrategyCount: function() {
            return this.strategyList.filter(item => item.is_del == undefined)
                .length;
        },
        pureCommit: function(str) {
            let pureConfig = [
                "【策略名称】",
                "【策略功效】",
                "【策略描述】",
                "【策略构成】",
                "(在此输入策略的中文名称)",
                "(在此输入此策略的功效)",
                "(在此填写策略的描述备注信息)",
                "(在此输入各个子策略含义和规格)"
            ];

            for (var idx in pureConfig) {
                str = str.replace(pureConfig[idx], "");
            }
            return str.replace(/[\r\n]/g, "");
        },
        getDelTpl: function(str) {
            let tplConfig = [
                "【策略名称】",
                "【策略功效】",
                "【策略描述】",
                "【策略构成】"
            ];

            let delTpl = [];

            for (var idx in tplConfig) {
                if (str.indexOf(tplConfig[idx]) < 0) {
                    delTpl.push(tplConfig[idx]);
                }
            }
            return delTpl;
        },
        async toCreateStrategySet() {
            if (this.changeStrategySetName() === false) {
                return;
            }
            if (this.strategySetInfo.assign_auditor.length < 1) {
                this.$message("请指定审核人");
                return;
            }

            var requestData = this.getPostData();
            let delTpl = this.getDelTpl(requestData.strategy_set_comment);

            if (delTpl.length > 0) {
                this.$message(
                    "请不要删除“策略描述”的模版：" + delTpl.join("、")
                );

                return;
            }

            let afterPureCommit = this.pureCommit(
                requestData.strategy_set_comment
            );

            if (afterPureCommit.length < 10) {
                this.$message("“策略描述”信息除模板外，不得少于10个字符");
                return;
            }

            if (this.getStrategyCount() < 1) {
                this.$message("请输入至少一个策略");
                return;
            }

            if (this.isDisableSubmit) {
                return false;
            }
            this.isDisableSubmit = true;

            let res = await this.$api.strategy.create(requestData);

            if (res.errcode != 0) {
                this.isDisableSubmit = false;
                this.$message(res.errmsg || "创建策略集失败");
                return;
            }

            this.$message({
                message: "创建策略集成功,该策略集需要管理员审核上线方能生效",
                duration: 1000,
                type: "success",
                onClose: () => {
                    this.$router.push({
                        path: "/strategy/detail",
                        query: { strategy_set_id: res.data.strategy_set_id }
                    });
                }
            });
        }
    }
};
</script>
