<template>
    <div>
        <!-- <div style="padding-bottom:5px">
            <el-form ref="form" :label-position="'left'" label-width="100px">
                <el-form-item label="实验名称" style="margin-bottom:1px;">
                    <el-link type="primary" :underline="false">
                        <router-link
                            :to="{path : '/lab/detail', query : {
                            exp_layer_id : expLayerId
                        }}"
                        >{{layerInfo.exp_layer_name}}</router-link>
                    </el-link>
                </el-form-item>
            </el-form>
        </div> -->
        <div style="padding-bottom:5px">
            <CustExpFilter></CustExpFilter>
        </div>

        <div style="padding-bottom:5px">
            <ExpLayerVersionList
                @queryBySetTime="queryBySetTime"
                :expLayerId="expLayerId"
                @clickToQuery="clickToQuery"
            />
        </div>

        <ExpEffectTable :custExpStatData="statData" />
    </div>
</template>
<script>
import ExpEffectTable from "./CustomerExpEffectDetail.vue";
import ExpLayerVersionList from "./ExpLayerVersionList.vue";
import CustExpFilter from "./CustExpFilter.vue";

export default {
    data() {
        return {
            expLayerId: 0,
            layerInfo: {},
            statData: {
                data: {},
                time_info: {}
            }
        };
    },
    mounted() {
        if (this.$route.query.exp_layer_id > 0) {
            this.expLayerId = this.$route.query.exp_layer_id;
            this.getLayerInfo();
        } else {
            this.$router.push({
                path: "/monitor/expEffect"
            });
        }
    },
    methods: {
        async loadCustExpData(expTime, unExpTime) {
            var p = {
                exp_layer_id: this.expLayerId,
                begin: expTime[0],
                end: expTime[1],
                before_begin: unExpTime[0],
                before_end: unExpTime[1],
                stat_type_code: 0,
                extra_query: this.$store.state.exp_effect.query,
                exp_id: this.$store.state.exp_effect.exp_id
            };
            this.$store.commit("setLoadState", true);
            this.statData = {
                data: {},
                time_info: {}
            };
            let res = await this.$api.effect.getCustomerStat(p);
            this.$store.commit("setLoadState", false);
            if (res.errcode != 0) {
                this.$message(res.errmsg)
                return
            }
            this.statData = res.data;
            this.$store.commit("setLoadState", false);
        },
        async getLayerInfo() {
            let res = await this.$api.exp.getExpDetailByGo(this.expLayerId);
            this.layerInfo = res.data.origin_exp_layer;
            this.$store.commit("setExpLayer", this.layerInfo);
            this.$store.commit("setExpList", res.data.exp_list);
            this.$store.commit(
                "setHeadBreadCrumb",
                this.layerInfo.exp_layer_name
            );
        },
        queryBySetTime: function(time) {
            this.$store.commit("setTime", time);
        },
        clickToQuery(expTime, unExpTime) {
            this.loadCustExpData(expTime, unExpTime);
        }
    },
    components: {
        ExpEffectTable,
        ExpLayerVersionList,
        CustExpFilter
    }
};
</script>

