<template>
  <div>
    <div class="btn-group">
      <template v-for="item in quotaList">
        <button type="button" class="btn btn-default" :class="selectQuotaList[item.quota_id] === true ? 'btn-info' : ''" @click="selectQuota(item)" @mouseover="showQuotaDetail(item, $event)">
        {{item.quota_text}}</button>
      </template>
    </div>
  </div>
</template>
<style scoped>
.btn-group>.btn {
  margin-bottom: 8px;
  margin-right: 8px;
}

</style>
<script>
import lodash from 'lodash';

export default {
  props: { 'expLayerType': String, 'selectQuotaIds': Array, 'expZoneCode': Number },
  data() {
    return {
      quotaList: [],
      selectQuotaList: {},
      isSelect: false,
    }
  },
  watch: {
    selectQuotaIds: {
      handler: function(newVal, oldVal) {
        if (!this.isSelect) {
          this.initSelectQuota(newVal);
          this.isSelect = true;
        }
      }
    },
    expZoneCode: {
      handler: function() {
        this.initQuotaList();
      }
    }
  },
  updated() {
    let that = this;
    let selectQuotaId = [];
    Object.keys(this.selectQuotaList).forEach(function(val, index) {
      if (that.selectQuotaList[val] == true) {
        selectQuotaId.push(val);
      }
    });
    this.$emit('changeExpQuota', selectQuotaId);
  },
  mounted() {
    this.initQuotaList();

    if (this.selectQuotaIds != undefined && this.selectQuotaIds.length > 0) {
      this.initSelectQuota(this.selectQuotaIds);
    }
  },
  methods: {
    initSelectQuota: function(selectQuotaIds) {
      let that = this;
      selectQuotaIds.forEach(function(val, index) {
        that.$set(that.selectQuotaList, val, true);
      });
    },
    initQuotaList: function() {
      if (this.expLayerType == 'traffic') {
        this.getTrafficQuotaList();
      } else if (this.expLayerType == 'customer') {
        this.getCustomerQuotaList();
      }
    },

    selectQuota: function(quotaInfo) {
      if (this.selectQuotaList[quotaInfo.quota_id] != undefined) {
        this.$set(this.selectQuotaList, quotaInfo.quota_id, !this.selectQuotaList[quotaInfo.quota_id]);
      } else {
        this.$set(this.selectQuotaList, quotaInfo.quota_id, true);
      }
    },
    async getTrafficQuotaList() {
      let res = await this.$api.quota.getTrafficQuotas(this.expZoneCode);
      this.quotaList = res.data;
    },
    async getCustomerQuotaList() {
      let res = await this.$api.quota.getCustomerQuotas(this.expZoneCode);
      this.quotaList = res.data;
    },
    showQuotaDetail: function(data, event) {
      return;
      $(event.currentTarget).tooltip({
        placement: 'top',
        html: true,
        title: '英文别名：' + data.quota_name + '<br>描述：' + data.quota_comment + '<br>计算公式：' + data.expression_text,
        container: 'body'
      }).tooltip('toggle');
    },

  }
}

</script>
